@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
/* 불릿 아이템 */
[class^='bul_item_'] {
	position: relative;
	@include mixins.font_size_xs;
	color: variables.$textColorGray;
	font-weight: variables.$fontWeightRegular;
	text-align: left;

	& + [class^='bul_item_'] {
		margin-top: 10px;
	}

	.bul_list {
		margin-top: 10px;

		&.type_sub_sm {
			margin-top: 6px;
		}
	}

	.bul_item_dash {
		margin-top: 6px;
	}

	.btn_text_link {
		display: inline;
		color: inherit;
		font-size: inherit;
		line-height: inherit;
		text-decoration: underline;

		.text {
			display: inline;
		}
	}

	// btn_more_view 공통 여백
	.btn_more_view {
		&:not(:first-child) {
			margin-top: 6px;
		}
	}

	& + .btn_more_view,
	& + .btn_more_list {
		margin-top: 6px;
	}

	.btn_xxs {
		margin: -2px 0 0 6px;
	}
	& + .btn_more_list { 	}

	.btn_xxs {
		margin: -2px 0 0 10px;
	}

	&.font_size_lg {
		padding-left: 12px;

		@include mixins.font_size_lg;
		letter-spacing: -.025em;

		&:before {
			width: 4px;
			height: 4px;
			top: 14px;
		}
	}

	// font-size: 13px
	&.font_size_xxs {
		@include mixins.font_size_xxs;

		& + .font_size_xxs {
			margin-top: 6px;
		}

		.bul_list {
			margin-top: 6px;
		}
	}

	// font-size: 12px
	&.font_size_min {
		padding-left: 6px;
		@include mixins.font_size_min;

		&:before {
			width: 2px;
			height: 2px;
		}

		& + .font_size_min {
			margin-top: 4px;
		}

		.bul_list {
			margin-top: 4px;
		}
	}
}

// 불릿 dot 타입
.bul_item_dot {
	padding-left: 9px;

	&:before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 8px;
		width: 3px;
		height: 3px;
		border-radius: 50%;
		background-color: variables.$colorGray01;
	}

	// font-size: 12px
	&.font_size_min {
		&:before {
			top: 7px;
		}
	}

	.bul_item_asterisk {
		display: block;
		margin-top: 6px;
	}

	.bul_item_dash {
		margin-top: 6px;
	}
}

// 불릿 asterisk 타입
.bul_item_asterisk {
	color: variables.$textColorLightGray;
	padding-left: 8px;

	&:before {
		content: '*';
		color: variables.$textColorPurple;
		position: absolute;
		left: 0;
		top: 0;
	}

	// font-size: 12px
	&.font_size_min {
		&:before {
			top: 1px;
		}
	}

	&.fc_spot {
		&:before {
			color: variables.$textColorPurple;
		}
	}
	&.fc_red {
		&:before {
			color: variables.$textColorRed;
		}
	}
}

// 불릿 branch 타입
.bul_item_branch {
	padding-left: 12px;

	&:before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 4px;
		width: 6px;
		height: 6px;
		@include mixins.ratio_images('../images/bul_branch', 'png', 6px, 6px);
	}

	// font-size: 12px
	&.font_size_min {
		padding-left: 12px;

		&:before {
			top: 4px;
			width: 6px;
			height: 6px;
		}
	}
}

// 불릿 dash 타입
.bul_item_dash {
	padding-left: 8px;

	&:before {
		content: '-';
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		background: none;
	}

	// font-size: 13px
	&.font_size_xxs {
		padding-left: 6px;
	}

	// font-size: 12px
	&.font_size_min {
		&:before {
			width: auto;
			height: auto;
		}
	}
}

/* decimal 리스트 */
.decimal_item {
	// font-size: 14px;
	color: variables.$textColorGray;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightRegular;

	& ~ .decimal_item {
		margin-top: 8px;
	}

	// font-size: 13px
	&.font_size_xxs {
		@include mixins.font_size_xxs;
	}

	// font-size: 12px
	&.font_size_min {
		@include mixins.font_size_min;
	}

	.btn_text_link {
		display: inline;
		color: inherit;
		font-size: inherit;
		line-height: inherit;
		text-decoration: underline;

		.text {
			display: inline;
		}
	}
}
.decimal_list {
	padding-left: 15px;
	list-style: decimal;
	box-sizing: border-box;
}

/* number text 리스트 */
.number_text_list {
	.number_text_item {
		@include mixins.font_size_xs;
		color: variables.$textColorBlack;

		& + .number_text_item {
			margin-top: 16px;
		}

		.number_text {
			display: block;
			font-weight: variables.$fontWeightMedium;
			vertical-align: top;

			& + .tbl_row_wrap {
				margin-top: 12px;
			}
		}
	}

	& + .info_text_box {
		margin-top: 30px;
	}
}

/* 불릿 리스트 아래 타이틀이 있을 때 간격 */
.bul_list {
	& + .title_wrap {
		&.title_size_sm {
			margin-top: 20px;
		}

		&.title_size_xs {
			margin-top: 18px;
		}

		&.title_size_min {
			margin-top: 16px;
		}
	}

	& + .info_text_box {
		margin-top: 30px;
	}

	& + .btn_xxs {
		margin-top: 10px;
	}

	// btn_more_view 공통 여백
	& + .btn_more_view {
		margin-top: 6px;
	}
	& + .btn_more_list {
		margin-top: 10px;
	}
}

/* 단순 정보성 텍스트 */
.info_text {
	@include mixins.font_size_xs;
	color: variables.$textColorGray;

	&.font_size_xxs {
		@include mixins.font_size_xxs;

		+ .info_text {
			&.font_size_xxs {
				// TODO : 가이드에 13PX + 13PX 사이 여백 추가될 때 수정 필요
				margin-top: 16px;
			}
		}
	}

	&.font_size_min {
		@include mixins.font_size_min;

		+ .info_text {
			&.font_size_min {
				// TODO : 가이드에 12PX + 12PX 사이 여백 추가될 때 수정 필요
				margin-top: 16px;
			}
		}
	}

	&.font_size_sm {
		@include mixins.font_size_sm;
		color: variables.$textColorBlack;
		font-weight: variables.$fontWeightMedium;
		margin-bottom: 13px;
	}

	+ .info_text {
		margin-top: 16px;
	}

	.btn_text_link {
		display: inline;
		color: inherit;
		font-size: inherit;
		line-height: inherit;
		text-decoration: underline;

		.text {
			display: inline;
		}
	}

	& + .bul_list {
		// TODO 추후 디자인 여백 가이드 나오면 정리
		margin-top: 12px;
	}

	.btn_info_popup {
		margin-top: 2px;
		margin-left: 3px;
	}

	// btn_more_view 공통 여백
	.btn_more_view {
		&:not(:first-child) {
			margin-top: 6px;
		}
	}
	& + .btn_more_view {
		margin-top: 6px;
	}
	& + .btn_more_list {
		margin-top: 10px;
	}

	& + .tbl_row_wrap,
	& + .tbl_col_wrap {
		margin-top: 20px;
	}

	& + .file_list {
		margin-top: 10px;
	}
}

/* 텍스트 underline */
.underline {
	text-decoration: underline;
	text-underline-position: under;
}

/* 단순 텍스트 리스트 */
.info_text_list {
	.info_text,
	.info_text.font_size_xxs,
	.info_text.font_size_min {
		& + .info_text {
			margin-top: 0;
		}
	}

	.info_text.font_size_sm {
		&:last-child {
			margin-bottom: 0;
		}
	}

	// btn_more_view 공통 여백
	& + .btn_more_view {
		margin-top: 6px;
	}
	& + .btn_more_list {
		margin-top: 10px;
	}
}

/* 텍스트 gray 박스 */
.info_text_box {
	display: block;
	padding: 18px;
	border-radius: 10px;
	background-color: variables.$colorGray06;
	box-sizing: border-box;

	.info_text_inner {
		+ .info_text_inner {
			margin-top: 18px;
			padding-top: 18px;
			border-top: 1px solid variables.$colorGray04;
		}
	}

	&.size_sm {
		padding: 14px;

		[class^=bul_item_].font_size_min {

			& + [class^=bul_item_].font_size_min {
				margin-top: 4px;
			}
		}
	}

	&.size_lg {
		padding: 24px;
	}

	.info_text {
		+ .bul_list {
			margin-top: 6px;
		}
	}

	.bul_list {
		& + .info_text {
			margin-top: 10px;
		}
	}
	& + .bul_list {
		margin-top: 20px;
	}

	+ .btn_more_view, + .btn_more_list {
		margin-top: 8px;
	}

	& + .title_wrap {
		margin-top: 20px;
	}

	& + .payment_info_wrap {
		margin-top: 30px;
	}

	& + .tbl_prod_wrap.type_myroom {
		margin-top: 20px;
	}

		// 단순 텍스트 리스트 공통 예외 여백
	.info_text_list {
		.info_text {
			& + .info_text {
				margin-top: 6px;
			}
		}
	}
}

/* 컨텐츠 상단 텍스트 영역 */
.cont_summary_box {
	@include mixins.justify_width(center);
	margin-bottom: 13px;

	.info_text.font_size_sm {
		margin-bottom: 0;

		& ~ .bul_item_asterisk {
			margin-left: 6px;
		}
	}
}

/* STEP 리스트 */
/* STEP - 상품상세 팝업 > 바로드림 구매 순서 */
.ord_progress_list {
	position: relative;
	font-size: 0;

	.ord_progress_item {
		@include mixins.justify_width(center);
		position: relative;
		z-index: 1;

		[class^=ico_] {
			flex: 0 auto;
			width: 58px;
		}

		.ord_progress_step {
			display: inline-block;
			flex: 1;
			padding-left: 14px;
			vertical-align: top;

			.ord_step_num {
				display: inline-block;
				margin-right: 15px;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightBold;
				color: variables.$textColorPurple;
				vertical-align: top;
			}

			.ord_step_text {
				display: inline-block;
				@include mixins.font_size_xs;
				vertical-align: top;
			}
		}

		& + .ord_progress_item {
			padding-top: 12px;
		}

		.btn_text_link {
			display: inline;
			color: inherit;
			font-size: inherit;
			line-height: inherit;
			text-decoration: underline;

			.text {
				display: inline;
			}
		}
	}

	&:before {
		content: '';
		position: absolute;
		top: 29px;
		left: 29px;
		width: 1px;
		height: calc(100% - 58px);
		@include mixins.ratio_images('../images/line_dot_vertical', 'png', 1px, 3px);
		background: {
			repeat: repeat-y;
		};
	}
}

/* STEP - highlight 타입 */
.step_highlight_list {
	display: block;

	.step_item {
		display: block;
		text-align: left;
		box-sizing: border-box;

		~ .step_item {
			margin-top: 6px;
		}

		.step_text_box {
			display: block;
			font-size: 0;

			.step_num {
				display: inline-block;
				width: 60px;
				margin-right: 5px;
				color: variables.$textColorPurple;
				@include mixins.font_size_min;
				font-weight: variables.$fontWeightMedium;
				vertical-align: top;

				.highlight {
					display: inline-block;
					position: relative;

					&:before {
						content: '';
						display: inline-block;
						position: absolute;
						left: 0;
						bottom: 2px;
						width: 100%;
						height: 6px;
						background-color: variables.$colorGray04;
					}

					.num {
						display: inline-block;
						position: relative;
						z-index: 1;
					}
				}
			}

			.step_text {
				display: inline-block;
				width: calc(100% - 65px);
				color: variables.$textColorBlack;
				@include mixins.font_size_xs;
				vertical-align: top;

				// btn_more_view 공통 여백
				.btn_more_view {
					&:not(:first-child) {
						margin-top: 6px;
					}
				}
			}
		}
	}
}

/* STEP - pill 타입 */
.step_pill_list {
	display: block;

	.step_item {
		display: block;
		position: relative;
		padding: 0 16px 24px;
		text-align: left;
		box-sizing: border-box;
		font-size: 0;

		&:before {
			content: '';
			display: inline-block;
			position: absolute;
			left: 0;
			top: 10px;
			width: 6px;
			height: 6px;
			border-radius: 50%;
			background-color: variables.$kyoboPurple;
		}

		&:after {
			content: '';
			display: inline-block;
			position: absolute;
			left: 2px;
			top: 26px;
			bottom: 0;
			width: 1px;
			border-left: 1px dashed variables.$colorGray02;
		}

		&:last-child {
			padding-bottom: 0;

			&:after {
				display: none;
			}
		}

		.step_text {
			display: inline-block;
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
			font-weight: variables.$fontWeightMedium;
			vertical-align: top;
			text-align: left;

			// btn_more_view 공통 여백
			.btn_more_view {
				&:not(:first-child) {
					margin-top: 6px;
				}
			}
		}

		.step_text_box {
			display: block;
			font-size: 0;

			.step_num {
				display: inline-block;
				width: 69px;
				height: 25px;
				padding: 2px 5px 3px;
				margin-right: 10px;
				color: variables.$textColorPurple;
				@include mixins.font_size_xxs;
				font-weight: variables.$fontWeightBold;
				border: 1px solid variables.$kyoboPurple;
				border-radius: 25px;
				vertical-align: top;
				box-sizing: border-box;
				text-align: center;
			}

			.step_text {
				width: calc(100% - 79px);
				margin-top: 3px;
			}
		}

		.step_cont_box {
			display: block;
			margin-top: 12px;
			font-size: 0;

			.cont_img_box {
				display: inline-block;
				width: calc((100% - 8px) / 2);
				border-radius: 10px;
				overflow: hidden;

				img {
					width: 100%;
				}

				& ~ .cont_img_box {
					margin-left: 8px;
				}

				&.full {
					width: 100%;
				}
			}

			.bul_list {
				& + .cont_img_box {
					margin-top: 10px;
				}
			}
		}
	}
}

/* 포인트 텍스트 */
.point_text {
	display: inline-block;
	@include mixins.font_size_sm;
	font-weight: variables.$fontWeightBold;
	color: variables.$kyoboGreen;
	vertical-align: top;

	.ico_point_green {
		margin-top: 1px;
	}
}
.ico_point_green {
	display: inline-block;
	width: 20px;
	height: 20px;
	vertical-align: top;

	@include mixins.ratio_images('../images/ico_point_green', 'png', 20px, 20px);
}

// 체크
.chk_info_desc {
	display: inline-block;
	position: relative;
	padding-left: 16px;
	@include mixins.font_size_xs;
	color: variables.$textColorLightGray;
	font-weight: variables.$fontWeightMedium;
	vertical-align: top;

	&:before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 6px;
		width: 12px;
		height: 8px;
		background: {
			repeat: no-repeat;
		};
		@include mixins.ratio_images('../images/ico_chk_desc', 'png', 12px, 8px);
	}

	&.type_green{
		color: variables.$textColorGreen;

		&:before{
			@include mixins.ratio_images('../images/ico_chk_desc_green', 'png', 12px, 8px);
		}
	}
}