@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
$badgeProps: (
		'border-radius': 4px,
		'border-radius-lg': 6px,
		'height-lg': 24px,
		'height-md': 22px,
		'height-sm': 20px,
		'padding': 1px 5px,
		'padding-sm' : 0 4px,
);

/* 뱃지 */
[class^=badge_] {
	display: inline-block;
	box-sizing: border-box;
	text-align: center;
	vertical-align: top;

	// 아이콘
	[class^=ico_] {
		display: inline-block;
		position: relative;
		vertical-align: top;
	}

	.text {
		display: inline-block;
		position: relative;
		vertical-align: top;
	}
}

/* 뱃지 - 사이즈 */
.badge_lg {
	min-height: map.get($badgeProps, 'height-lg');
	padding: map.get($badgeProps, 'padding');
	border-radius: map.get($badgeProps, 'border-radius');
	@include mixins.font_size_min;

	// 뱃지 - pill
	&.badge_pill {
		padding: 3px 7px 0;
		border-radius: map.get($badgeProps, 'height-md');
		font-size: 11px;
		font-weight: variables.$fontWeightBold;
	}
}
.badge_md {
	min-height: map.get($badgeProps, 'height-md');
	padding: map.get($badgeProps, 'padding');
	border-radius: map.get($badgeProps, 'border-radius');
	@include mixins.font_size_min;

	// 뱃지 - pill
	&.badge_pill {
		border-radius: map.get($badgeProps, 'height-md');
	}
}
.badge_sm {
	min-height: map.get($badgeProps, 'height-sm');
	padding: map.get($badgeProps, 'padding-sm');
	border-radius: map.get($badgeProps, 'border-radius');
	font-weight: variables.$fontWeightMedium;
	@include mixins.font_size_min;

	// 뱃지 - pill
	&.badge_pill {
		padding: 3px 7px 0;
		border-radius: map.get($badgeProps, 'height-sm');
		line-height: 1;
	}
}

/* 뱃지 - 리스트 */
.badge_list_box {
	margin-top: -4px;
	font-size: 0;
	text-align: left;

	[class^=badge_] {
		margin-right: 4px;
		margin-top: 4px;
	}
}

