/* 태그 */
.tag_wrap {
	margin: -4px -3px;
	font-size: 0;

	.tag {
		margin: 4px 3px;
		display: inline-block;
		@include mixins.font_size_xxs;
		color: variables.$textColorGray;
		padding: 7px 16px 8px;
		background-color: variables.$colorGray06;
		border: 1px solid variables.$colorGray06;
		border-radius: 20px;
		box-sizing: border-box;
		@include mixins.transition(background-color .23s, border-color .23s, color .23s);

		.text {
			position: relative;
		}

		.gap {
			display: inline-block;
			width: 5px;
			height: 9px;
			font-size: 0;
			margin: 5px 4px 0;
			background: {
				position: center;
				repeat: no-repeat;
			}
			vertical-align: top;
			@include mixins.ratio_images('../images/ico_field_gap', 'png', 5px, 9px);
		}
	}

	button.tag {
		&:hover {
			cursor: pointer;
		}

		&.active {
			background-color: variables.$textColorGray;
			border-color: variables.$textColorGray;
			color: #fff;
		}

		&.size_lg {
			&.active {
				color: #fff;
			}
		}
	}

	.tag_list {
		display: block;

		.tag_item {
			display: block;
		}
	}

	&.size_lg {
		.tag {
			padding: 9px 16px 7px;
			@include mixins.font_size_xs;
			border-color: variables.$colorGray04;
			color: variables.$textColorBlack;
		}
	}

	&.size_sm {
		margin: -2px;

		.tag {
			margin: 2px;
			padding: 2px 10px;
			@include mixins.font_size_min;

			.gap{
				margin-top: 4px;
			}
		}
	}

	&.type_white {
		.tag {
			color: variables.$textColorGray;
			background-color: #fff;
			border: 1px solid #fff;
		}
	}

	&.type_line_white {
		.tag {
			background-color: #fff;
			border: 1px solid variables.$colorGray03;

			&.active {
				background-color: #fff;
				border-color: variables.$textColorBlack;
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightBold;
			}
		}
	}
}

// flag state
.flag_state {
	display: inline-block;
	padding: 4px 8px 3px;
	@include mixins.font_size_min();
	color: #fff;
	border-radius: 13px 13px 13px 0;
	vertical-align: middle;

	&.ing {
		background-color: #4dac27;
	}

	&.end {
		background-color: #767675;
	}
}

.badge_flag {
	display: inline-block;
	min-width: 20px;
	padding: 3px 3px 2px;
	font-size: variables.$fontSizeMin;
	letter-spacing: variables.$letterSpacing;
	font-weight: variables.$fontWeightBold;
	line-height: 1;
	color: #fff;
	border-radius: 6px 0;
	vertical-align: top;
	text-align: center;

	&.size_lg {
		padding: 5px 8px;
		border-radius: 8px 0;
		font-weight: variables.$fontWeightRegular;
	}
	&.size_xl {
		padding: 4px 10px 3px 10px;
		border-radius: 16px 0;
		@include mixins.font_size_xxs;
		font-weight: variables.$fontWeightRegular;
	}

	&.badge_primary{
		border: 1px solid variables.$kyoboPurple;
		background-color: variables.$kyoboPurple;

		.text {
			color: #fff;
		}
	}

	&.best{
		position: relative;
		padding-left: 17px;
		margin: 8px 0 0 14px;

		.text{
			font-weight: variables.$fontWeightMedium;
		}

		&:before{
			content: '';
			position: absolute;
			bottom: -1px;
			left: -14px;
			display: block;
			width: 28px;
			height: 27px;
			@include mixins.ratio_images('../images/ico_best_badge', 'png', 28px, 27px);
			vertical-align: top;
		}
	}
}

