@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
// SimpleBar
/* custom scroll */
.custom_scroll_wrap {
	//-ms-overflow-style: -ms-autohiding-scrollbar; // IE 스크롤 마우스 오버시에만 노출
	width: 100%;
	height: 100%;
	overflow-x: hidden;
	overflow-y: auto;
	box-sizing: border-box;

	.simplebar-track {
		width: 2px;
		background: rgba(variables.$textColorBlack, .06);
		border-radius: 2px;

		.simplebar-scrollbar {
			width: 2px;
			left: 0;
			border-radius: 2px;

			&:before {
				border-radius: 2px;
			}

			&.simplebar-visible {
				&:before {
					opacity: 1;
				}
			}
		}

		&.simplebar-vertical {
			.simplebar-scrollbar {
				&:before {
					left: 0;
					right: 0;
					top: 0;
					bottom: 0;
				}
			}
		}

		&.simplebar-horizontal {
			height: 2px;

			.simplebar-scrollbar {
				height: 2px;
				top: 0;

				&:before {
					left: 0;
					right: 0;
				}
			}
		}

	}

	&.horizontal {
		overflow: {
			x: auto;
			y: hidden;
		}
	}

	&.active {
		padding-right: 18px;
	}

	&.type_white {
		.simplebar-track {
			background: rgba(255, 255, 255, .06);

			.simplebar-scrollbar {
				&:before {
					background-color: #fff;
				}
			}
		}
	}
}

/* 팝업 - dialog */
body.dialog_open {
	position: static;
	overflow-y: hidden;
}

.dialog_wrapper {
	display: none;
	min-width: variables.$defaultMinWidth;
	z-index: 1000;
	align-items: center;

	.ui-widget-overlay {
		background: #000;
		opacity: 0.5;
		filter: alpha(opacity=50);
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
	}

	.ui-dialog {
		@include mixins.justify_width(start, start, column);
		position: relative;
		width: 560px;
		max-height: 750px;
		margin: 0 auto;
		padding: 0;
		border: none;
		color: #000;

		/*&:after {
			content: '';
			display: block;
			min-height: inherit;
			font-size: 0;
		}*/

		.ui-dialog-titlebar {
			display: none;
		}

		.btn_dialog_close {
			position: absolute;
			top: 28px;
			right: 30px;
			width: 24px;
			height: 24px;

			.ico_dialog_close {
				position: relative;
				z-index: 3;
				display: block;
				width: 24px;
				height: 24px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/btn_dialog_close', 'png', 24px, 24px);
			}
		}

		.dialog_wrap {
			&.ui-dialog-content {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				display: -webkit-flex;
				-moz-box-orient: vertical;
				-ms-flex-direction: column;
				-webkit-flex-direction: column;
				flex-direction: column;
				overflow: hidden;
				width: 100% !important;
				max-height: 750px !important;
				padding: 0 30px 34px;
				background: #fff;
				border-radius: 16px;
				border: 0;
				box-sizing: border-box;

				/*&:after {
					content: '';
					display: block;
					min-height: inherit;
					font-size: 0;
				}*/

				.dialog_header {
					flex-shrink: 0;
					padding: 25px 30px 20px 0;
					text-align: left;
					background: #fff;
					border-bottom: 1px solid variables.$colorGray04;

					.dialog_title {
						@include mixins.font_size_md;
						font-weight: variables.$fontWeightMedium;
						@include mixins.ellipsis(false, false, true);
					}
				}

				.dialog_contents {
					flex: 1 1 auto;
					overflow: hidden;
					overflow-y: auto;
					position: relative;
					max-height: 616px;
					background: #fff;
					padding: 0;
					margin-top: 24px;
					box-sizing: border-box;
					-webkit-overflow-scrolling: touch;

					/*&:after {
						content: '';
						display: block;
						min-height: inherit;
						font-size: 0;
					}*/

					.custom_scroll_wrap {
						max-height: inherit;

						.simplebar-content {
							height: 100%;
						}
					}

					.alert_text {
						@include mixins.font_size_sm;
						font-weight: variables.$fontWeightMedium;

						& + .alert_text {
							margin-top: 12px;
						}
					}

					.alert_text_sm {
						margin-top: 6px;
						@include mixins.font_size_xs;
						color: variables.$textColorGray;

						.val {
							display: inline-block;
							font-weight: variables.$fontWeightBold;
							color: variables.$textColorPurple;
							vertical-align: top;
						}
					}

					// 팝업 내 아이콘 + 텍스트
					.ico_info_wrap {
						display: block;
						text-align: center;

						.ico_img_box {
							margin-bottom: 20px;

							[class^=ico_] {
								display: inline-block;
								vertical-align: top;
							}
						}

						.ico_text_box {
							.ico_title {
								display: inline-block;
								color: variables.$textColorBlack;
								@include mixins.font_size_sm;
								font-weight: variables.$fontWeightBold;

								& ~ .ico_desc {
									margin-top: 12px;
								}
							}

							.ico_desc {
								display: inline-block;
								color: variables.$textColorGray;
								@include mixins.font_size_xs;
								font-weight: variables.$fontWeightRegular;
							}

							& + .round_gray_box {
								margin-top: 20px;
							}
						}
					}

					// 팝업 내 안내영역
					.notice_info_wrap {
						display: block;
						text-align: center;

						.notice_title {
							display: block;
							margin-bottom: 12px;
							color: variables.$textColorBlack;
							@include mixins.font_size_sm;
							font-weight: variables.$fontWeightBold;
						}

						.notice_text {
							display: block;
							color: variables.$textColorBlack;
							@include mixins.font_size_xs;
							font-weight: variables.$fontWeightRegular;

							.val {
								color: variables.$textColorBlack;
								font-weight: variables.$fontWeightBold;
							}
						}

						.notice_text_sm {
							display: block;
							margin-top: 12px;
							color: variables.$textColorLightGray;
							@include mixins.font_size_xxs;
						}

						.info_text_box {
							margin-top: 20px;

							.info_text_list {
								text-align: left;
							}

							& + .info_text {
								margin-top: 20px;
							}
						}
					}

					.thumbnail_round_box {
						padding: 16px;
						border: 1px solid variables.$colorGray04;
						border-radius: 10px;

						.prod_area {
							.prod_info_box {
								.rating-container {
									margin-top: 18px;
								}
							}
						}

						+ .info_text_box {
							margin-top: 10px;
						}

						&.size_md {
							padding: 20px;
						}

						+ .delivery_tracking_wrap {
							margin-top: 20px;
						}
						+ .title_wrap {
							margin-top: 24px;
						}
					}
				}

				.dialog_footer {
					@include mixins.justify_width(center, center);
					flex-shrink: 0;
					flex-wrap: wrap;
					margin-top: 34px;

					a[class^=btn_], button[class^=btn_] {
						flex: 0 auto;

						& ~ a[class^=btn_], & ~ button[class^=btn_] {
							margin-left: 6px;
						}
					}
				}

				&.has_btn {
					padding-bottom: 30px;

					.dialog_contents {
						max-height: 542px;
					}
				}
			}

			// no title
			&.no_title {
				padding: 0 30px 30px;

				.dialog_contents {
					max-height: 586px;
					margin-top: 56px;
					text-align: center;
					font-size: 0;
				}
			}

			&.no_title_line {
				.dialog_header {
					border-bottom: none;
					padding-bottom: 0;
				}

				.dialog_contents {
					max-height: 631px;
					margin-top: 30px;
				}

				&.has_btn {
					.dialog_contents {
						max-height: 557px;
					}
				}
			}
		}

		// 팝업 내 select 최대 높이값 설정 (최대 5개)
		.ui-selectmenu-menu {
			.ui-menu {
				max-height: calc(44px * 5 + 2px);
			}

			&.size_sm {
				.ui-menu {
					max-height: calc(38px * 5 + 2px);
				}
			}
		}

		// large popup
		&.dialog_lg {
			width: 920px;

			.dialog_wrap {
				&.no_title{
					.dialog_contents {
						max-height: 664px;
					}
				}
			}
		}

		// small popup
		&.dialog_sm {
			width: 400px;

			.dialog_contents {
				.no_data {
					&:not(.type_gray) {
						padding: 100px 0;
					}
				}

				.no_data {
					&:not(.type_gray) {
						padding: 100px 0;
					}

					&.first {
						padding-top: 76px;
					}
				}
			}

			.dialog_wrap {
				&.ui-dialog-content {
					&.type_col_btn {
						.dialog_contents {
							max-height: 502px;
						}

						.dialog_footer {
							flex-wrap: wrap;

							.btn_wrap {
								flex: 1 0 100%;

								+ .btn_more_view, + .btn_text_link {
									margin-top: 20px;
								}
							}
						}
					}
				}
			}
		}

		//좌석배치도
		&.dialog_seating_chart {
			width: 1020px;

			img {
				width: 100%;
			}
		}

		// alert popup
		&.dialog_alert {
			width: 360px;

			.dialog_wrap {
				padding: 0 37px 30px;

				.dialog_contents {
					margin-top: 40px;
					@include mixins.justify_width(center, center);
					flex-direction: column;
					align-items: stretch;
					min-height: 45px;
					text-align: center;
				}
			}
		}

		// h_auto
		&.h_auto {
			height: 100vh !important;
		}
	}

	&.open {
		display: flex;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		overflow-y: hidden;
	}

	// no dim
	&.no_dim {
		overflow: visible;
		width: auto;
		min-width: 0;
		height: auto;
	}

}

/* sps */
.header_wrapper {
	&.sps {
		~ .container_wrapper {
			.sps {
				&.sps-blw {
					top: 80px;
				}
			}
		}
	}
}
.sps {
	&.sps_scroll_fixed {
		position: absolute;
		top: 0;
		width: 100%;
		background-color: #fff;
		z-index: 10;

		.sps_inner {
			width: variables.$defaultInnerWidth;
			margin: 0 auto;
			height: 100%;
		}

		~ .sps-bg {
			position: absolute;
			top: 0;
			left: 0;
		}

		&.sps-blw {
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			min-width: variables.$defaultMinWidth;
			width: 100%;
		}

		&.pos-right {
			right: 0;
			left: auto;
			margin-left: 0;
		}
	}
}

/* spinner */
.form_spinner_box {
	display: inline-block;
	vertical-align: top;

	&.form_spinner_box.disabled {
		.ui-spinner {
			.ui-spinner-button {
				.ui-icon {
					@include mixins.ratio_images('../images/ico_spinner_down_off', 'png', 12px, 12px);
				}

				&.ui-spinner-button.ui-spinner-up {
					.ui-icon {
						@include mixins.ratio_images('../images/ico_spinner_up_off', 'png', 12px, 12px);
					}
				}
			}
		}
	}

	&.size_sm {
		.ui-spinner {
			width: 80px;
			padding: 0 26px;

			.form_spinner {
				height: 30px;
				font-size: variables.$fontSizeMin;
			}

			.ui-spinner-button {
				width: 27px;
				height: 30px;

				.ui-icon {
					width: 10px;
					height: 10px;
					@include mixins.ratio_images('../images/ico_spinner_down_sm', 'png', 10px, 10px);
				}

				&.ui-spinner-button.ui-spinner-up {
					.ui-icon {
						@include mixins.ratio_images('../images/ico_spinner_up_sm', 'png', 10px, 10px);
					}
				}
			}

			.ui-spinner-button.ui-spinner-up {
				border-radius: 0 4px 4px 0;
			}

			.ui-spinner-button.ui-spinner-down {
				border-radius: 4px 0 0 4px;
			}
		}
	}

	&.size_sm.disabled {
		.ui-spinner {
			.ui-spinner-button {
				.ui-icon {
					@include mixins.ratio_images('../images/ico_spinner_down_sm_off', 'png', 10px, 10px);
				}

				&.ui-spinner-button.ui-spinner-up {
					.ui-icon {
						@include mixins.ratio_images('../images/ico_spinner_up_sm_off', 'png', 10px, 10px);
					}
				}
			}
		}
	}

	.ui-spinner {
		position: relative;
		display: block;
		width: 102px;
		padding: 0 33px;
		background-color: #fff;
		box-sizing: border-box;
		font-size: 0;

		.form_spinner {
			width: 100%;
			height: 44px;
			margin: 0;
			padding: 1px 0 0 0;
			border-top: 1px solid variables.$colorGray02;
			border-bottom: 1px solid variables.$colorGray02;
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
			font-family: variables.$fontEng;
			outline: 0;
			text-align: center;
			box-sizing: border-box;
			vertical-align: top;
		}

		.ui-spinner-button {
			display: block;
			top: 0;
			width: 34px;
			height: 44px;
			background-color: #fff;
			border: 1px solid variables.$colorGray02;
			box-sizing: border-box;
			cursor: pointer;

			.ui-icon {
				width: 12px;
				height: 12px;
				@include mixins.ratio_images('../images/ico_spinner_down', 'png', 12px, 12px);
				text-align: center;
				vertical-align: top;
				margin: 0;
			}

			&.ui-spinner-button.ui-spinner-up {
				.ui-icon {
					@include mixins.ratio_images('../images/ico_spinner_up', 'png', 12px, 12px);
				}
			}

		}

		.ui-spinner-button.ui-spinner-up {
			border-left: none;
			border-radius: 0 6px 6px 0;
		}

		.ui-spinner-button.ui-spinner-down {
			left: 0;
			border-right: none;
			border-radius: 6px 0 0 6px;
		}
	}
}

// jquery ui selectmenu
.ui-selectmenu-menu {
	display: block;
	height: 0;
	overflow: hidden;
	box-sizing: border-box;
	background-color: #fff;
	margin-top: -1px;
	border-radius: 0 0 6px 6px;
	opacity: 0;
	border: 1px solid #000;
	border-top: none;
	@include mixins.transition(opacity .25s);
	@include mixins.transform(scale(0));

	&.ui-selectmenu-open {
		height: auto;
		opacity: 1;
		@include mixins.transform(scale(1));
		box-sizing: border-box;
	}

	.ui-menu {
		max-width: 100%;
		padding-bottom: 0;
		box-sizing: border-box;
		border-radius: 0 0 6px 6px;
		overflow-y: auto;

		.ui-menu-item {
			.ui-menu-item-wrapper {
				padding: 11px 14px;
				color: #000;
				@include mixins.font_size_xs;
				font-family: variables.$fontKor;
				box-sizing: border-box;
				@include mixins.transition(background-color 0.2s ease-out);

				&.ui-state-active {
					margin: 0;
					background-color: #f5f5f5;
				}

				&.prod_option {
					@include mixins.justify_width(center);

					.option_label_box {
						display: block;
						margin-right: 15px;
					}

					.option_price_box {
						margin-left: auto;
						flex-shrink: 0;
						display: block;

						.option_price {
							font-family: variables.$fontEng;
						}
					}
				}
			}

			&.ui-state-disabled {
				.ui-menu-item-wrapper {
					color: variables.$textColorLightGray;
				}
			}

			&:last-child {
				margin-bottom: 5px;
			}
		}
	}

	// select - sm type option
	&.type_sm {
		.ui-menu {
			.ui-menu-item {
				margin-bottom: 0;

				.ui-menu-item-wrapper {
					padding: 9px 14px 10px;
					@include mixins.font_size_xxs;
				}
			}
		}
	}

	// select - arw type option
	&.type_arw {
		width: auto;
		min-width: 72px;
		border-radius: 10px;

		.ui-menu {
			padding: 12px 12px;
			border: 0;

			.ui-menu-item {
				.ui-menu-item-wrapper {
					padding: 0;
					background: none;
					@include mixins.font_size_xxs;
					color: variables.$textColorLightGray;
					@include mixins.transition(color 0.2s ease-out);

					&.ui-state-active {
						margin: 0;
						color: #000;
					}
				}

				& ~ .ui-menu-item {
					margin-top: 10px;
				}
			}
		}

		&.ui-selectmenu-open {
			border: 1px solid #000;
		}
	}

	// select - calendar type option
	&.type_calendar {
		width: auto;
		min-width: 60px;
		border-radius: 10px;

		.ui-menu {
			padding: 12px 12px;
			border: 0;

			.ui-menu-item {
				.ui-menu-item-wrapper {
					padding: 0;
					background: none;
					@include mixins.font_size_xxs;
					color: variables.$textColorLightGray;
					@include mixins.transition(color 0.2s ease-out);

					&.ui-state-active {
						margin: 0;
						color: #000;
					}
				}

				& ~ .ui-menu-item {
					margin-top: 10px;
				}
			}
		}

		&.ui-selectmenu-open {
			border: 1px solid #000;
		}
	}

	// select - gnb
	&.type_gnb {
		border-radius: 0 0 20px 20px;

		.ui-menu {
			border-radius: 0 0 20px 20px;
			padding-bottom: 10px;

			.ui-menu-item {
				margin-bottom: 0;

				.ui-menu-item-wrapper {
					padding: 3px 24px;
					color: variables.$textColorGray;

					&.ui-state-active {
						//background-color: transparent;
					}
				}
			}
		}
	}
}

/* rating */
.rating-container {
	position: relative;
	display: inline-block;
	text-align: left;
	font-size: 0;
	vertical-align: top;
	margin-left: -2px;

	.clear-rating {
		display: none;
	}

	.rating-stars {
		.empty-stars {
			.star {
				margin: 0 2px;

				.glyphicon-star-empty:before {
					content: '';
					display: inline-block;
					width: 18px;
					height: 18px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_none', 'png', 18px, 18px);
				}
			}
		}

		.filled-stars {
			.star {
				margin: 0 2px;

				.glyphicon-star:before {
					content: '';
					display: inline-block;
					width: 18px;
					height: 18px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover', 'png', 18px, 18px);
				}
			}
		}

		.form_rating {
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
			font-family: variables.$fontEng;
			outline: 0;
		}
	}

	.caption {
		display: none;
	}

	&.rating-sm {
		margin-left: -1px;

		.empty-stars {
			.star {
				margin: 0 0 0 1px;

				.glyphicon-star-empty:before {
					content: '';
					display: inline-block;
					width: 14px;
					height: 14px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_sm_none', 'png', 14px, 14px);
				}
			}
		}

		.filled-stars {
			.star {
				margin: 0 0 0 1px;

				.glyphicon-star:before {
					content: '';
					display: inline-block;
					width: 14px;
					height: 14px;
					background: {
						repeat: no-repeat;
						position: center;
					}
					@include mixins.ratio_images('../images/ico_klover_sm', 'png', 14px, 14px);
				}
			}
		}
	}

	&.has_caption {
		.caption {
			display: inline-block;
