/* jQueryUI - Datepicker */
.ui-datepicker {
	width: 284px;
	padding: 19px 12px 17px;
	background: #fff;
	border: 1px solid #000;
	border-radius: 10px;
	box-sizing: border-box;

	.ui-datepicker-header {
		padding: 0;
		margin: 0 7px 16px;

		.ui-datepicker-title {
			@include mixins.font_size_sm;
			color: variables.$textColorBlack;
			font-weight: variables.$fontWeightBold;

			.ui-datepicker-year,
			.ui-datepicker-month {
				display: inline-block;
				width: auto;
				height: 24px;
				margin: 0;
				padding-right: 15px;
				@include mixins.font_size_sm;
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightBold;
				text-align: right;
				background: {
					color: #fff;
					position: right center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/arw_datepicker_select', 'png', 11px, 11px);
				border: none;
				vertical-align: top;
				box-sizing: border-box;
				-webkit-appearance: none;
				-moz-appearance: none;
				-o-appearance: none;
				appearance: none;

				&::-ms-expand {
					display: none;
				}
			}

			.ui-datepicker-year {
				& + .ui-datepicker-month {
					margin-left: 8px;
				}
			}
		}
	}

	.ui-datepicker-prev, .ui-datepicker-next {
		width: 24px;
		height: 24px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/btn_datepicker_nav', 'png', 24px, 24px);
		cursor: pointer;

		&:focus {
			@include mixins.focus_outline;
		}
	}

	.ui-datepicker-prev {
		top: 0;
		left: 0;
	}

	.ui-datepicker-next {
		top: 0;
		right: 0;
		@include mixins.transform(rotate(180deg));
	}

	.ui-datepicker-calendar {
		table-layout: fixed;
		margin-bottom: 0;

		th {
			width: 37px;
			padding: 0 0 2px 0;
			@include mixins.font_size_min;
			color: variables.$textColorBlack;
			font-weight: variables.$fontWeightMedium;
			text-align: center;

			// 일요일
			&.ui-datepicker-week-end:first-child {
				color: variables.$textColorRed;
			}
		}

		td {
			width: 37px;
			height: 35px;
			padding: 2px 3px;
			text-align: center;
			box-sizing: border-box;

			.ui-state-default {
				display: inline-block;
				width: 100%;
				height: 100%;
				padding: 0;
				font-size: variables.$fontSizeXxs;
				line-height: 29px;
				letter-spacing: variables.$letterSpacing;
				font-weight: variables.$fontWeightRegular;
				text-align: center;
				background-color: #fff;
				border-radius: 50%;
				border: 1px solid transparent;
				vertical-align: top;
				box-sizing: border-box;
				@include mixins.transition(border 0.2s, color 0.2s, background 0.2s);

				&.ui-state-hover,
				&.ui-state-active {
					color: #fff;
					font-weight: variables.$fontWeightBold;
					border-color: variables.$kyoboPurple;
					background-color: variables.$kyoboPurple;
				}
			}

			// 오늘
			&.ui-datepicker-today {
				.ui-state-highlight {
					color: variables.$textColorPurple;
					font-weight: variables.$fontWeightBold;
					border-color: rgba(variables.$kyoboPurple, 0.2);

					&.ui-state-hover {
						color: #fff;
					}
				}
			}

			// 일요일
			&.ui-datepicker-week-end:first-child {
				.ui-state-default {
					color: variables.$textColorRed;

					&.ui-state-hover,
					&.ui-state-active {
						color: #fff;
					}
				}
			}
		}
	}
}

/* awesomplete */
.awesomplete {
	position: relative;

	.auto_complete_box {
		display: none;
		position: absolute;
		top: 44px;
		left: 0;
		width: 100%;
		padding-bottom: 5px;
		border: 1px solid variables.$textColorBlack;
		border-top: none;
		border-radius: 0 0 6px 6px;
		background-color: #fff;
		z-index: 10;
		box-sizing: border-box;
	}

	.custom_scroll_wrap {
		max-height: 226px;
	}

	ul {
		height: auto;

		li {
			display: block;
			width: 100%;
			padding: 11px 14px;
			color: #000;
			@include mixins.font_size_xs;
			box-sizing: border-box;
			@include mixins.transition(background-color 0.2s ease-out);
			text-align: left;
			cursor: pointer;

			mark {
				background: none;
			}

			&:hover, &:focus, &:active {
				background-color: #f5f5f5;
			}

			&[aria-selected="true"] {
				background-color: #f5f5f5;
			}
		}
	}

	.visually-hidden {
		display: none;
	}

	&.active {
		.auto_complete_box {
			display: block;
		}

		.form_ip {
			border-radius: 6px 6px 0 0;
			border-color: variables.$textColorBlack;
		}
	}
}