@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
// fold default
.fold_box_wrap {
	.fold_box_list {
		.fold_box {
			+ .fold_box {
				margin-top: 10px;
			}
		}
	}

	.fold_box {
		border: 1px solid variables.$colorGray03;
		border-radius: 10px;
		overflow: hidden;
		box-sizing: border-box;

		.fold_box_header, .fold_box_contents {
			padding: 0 23px;
		}

		.fold_box_header {
			position: relative;
			padding: 21px 30px 21px 23px;
			cursor: pointer;
			@include mixins.font_size_sm;
			font-weight: variables.$fontWeightMedium;

			&:after {
				content: '';
				position: absolute;
				display: block;
				width: 11px;
				height: 7px;
				right: 20px;
				top: 50%;
				@include mixins.ratio_images('../images/arw_fold_toggle', 'png', 11px, 7px);
				@include mixins.transform(translateY(-50%));
			}

			.btn_fold {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 1;
			}

			.form_chk {
				z-index: 2;
			}

			.text {
				margin-left: 10px;
			}
		}

		.fold_box_contents {
			height: 0;
			overflow: hidden;
			box-sizing: border-box;

			// 주소 아이템
			.address_item {
				.address {
					margin-top: 0;

					& + .address_person {
						margin-top: 10px;
					}
				}
				.msg {
					margin-top: 10px;
				}
			}
		}

		&.expanded {
			border-color: variables.$textColorBlack;

			.fold_box_header {
				&:after {
					@include mixins.transform(translateY(-50%) rotate(180deg));
				}
			}

			> .fold_box_contents {
				height: auto;
				padding: {
					top: 9px;
					bottom: 23px;
				}

				.form_wrap {
					+ .btn_wrap {
						margin-top: 20px;
					}
				}
			}
		}

		&.no_fold {
			.fold_box_header {
				&:after {
					content: none;
				}

				cursor: default;

				.btn_fold {
					pointer-events: none;
				}
			}
		}

		// fold select type 추가
		&.type_select {
			border-radius: 6px;
			background-color: #fff;

			.fold_box_header {
				padding: 11px 37px 11px 13px;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightRegular;

				&.type_prd {
					@include mixins.justify_width(center, start, row);
					box-sizing: border-box;
					width: 100%;
					padding: 10px 30px 10px 13px;
					text-align: left;

					.cover {
						width: 48px;
						height: 48px;
						border: 1px solid variables.$colorGray04;
						margin: 0 10px 0 0;
						box-sizing: border-box;

						.img_box {
							position: relative;
							display: block;
							width: 100%;
							height: 100%;
							overflow: hidden;
							background: {
								repeat: no-repeat;
								size: contain;
								position: center;
							}

							img {
								display: none;
							}
						}
					}

					.info {
						width: calc(100% - 58px);
						padding-top: 2px;

						.flag {
							display: block;
							margin-bottom: 5px;
							color: variables.$textColorGray;
							font-size: variables.$fontSizeMin;
							letter-spacing: variables.$letterSpacing;
							line-height: 1;
							font-weight: variables.$fontWeightMedium;
						}

						.name {
							display: block;
							font-size: variables.$fontSizeXs;
							letter-spacing: variables.$letterSpacing;
							line-height: 1;
							@include mixins.ellipsis;
						}
					}
				}
			}

			.fold_box_contents {
				padding: 0;
			}

			.fold_select_wrap {
				padding: 0 0 5px;
				border-top: 1px solid #000;

				.fold_select_list {
					.fold_select_item {
						@include mixins.justify_width(center, between, row);
						box-sizing: border-box;
						width: 100%;
						padding: 12px 13px 12px 11px;;
						text-align: left;
						cursor: pointer;
						@include mixins.transition(background .2s ease-out);

						.name {
						}

						.opt {
							flex-shrink: 0;
							padding-left: 10px;

							&.type_gray {
								color: variables.$textColorGray;
							}

							&.type_free {
								font-weight: variables.$fontWeightBold;
								color: variables.$textColorPurple;
							}
						}

						&:hover, &:focus, &:active {
							background-color: #f5f5f5;
						}

						&:disabled {
							cursor: default;

							.name, .opt {
								color: variables.$textColorLightGray;
							}

							&:hover, &:focus, &:active {
								background: none;
							}
						}
					}

					// 상품 옵션 노출
					.fold_select_prd {
						@include mixins.justify_width(center, start, row);
						box-sizing: border-box;
						width: 100%;
						padding: 10px 13px;
						text-align: left;
						cursor: pointer;
						@include mixins.transition(background .2s ease-out);

						.cover {
							position: relative;
							width: 48px;
							height: 48px;
							border: 1px solid variables.$colorGray04;
							margin-right: 10px;
							box-sizing: border-box;
							background-color: #fff;

							.img_box {
								position: relative;
								display: block;
								width: 100%;
								height: 100%;
								overflow: hidden;
								background: {
									repeat: no-repeat;
									size: contain;
									position: center;
								}

								img {
									display: none;
								}
							}
						}

						.info {
							position: relative;
							width: calc(100% - 58px);
							padding-top: 2px;

							.flag {
								display: block;
								margin-bottom: 5px;
								color: variables.$textColorGray;
								font-size: variables.$fontSizeMin;
								letter-spacing: variables.$letterSpacing;
								line-height: 1;
								font-weight: variables.$fontWeightMedium;
							}

							.name {
								display: block;
								@include mixins.font_size_xs;
								font-size: variables.$fontSizeXs;
								letter-spacing: variables.$letterSpacing;
								line-height: 1;
								@include mixins.ellipsis;
							}
						}

						&:hover, &:focus, &:active {
							background-color: #f5f5f5;
						}

						&:disabled {
							cursor: default;

							.cover {
								opacity: .5;
							}

							.info {
								.flag, .name {
									color: variables.$textColorLightGray;
								}
							}

							&:hover, &:focus, &:active {
								background-color: transparent;
							}
						}
					}
				}
			}

			&.type_floating {
				position: relative;
				border: none;
				overflow: visible;

				.fold_box_header {
					border: 1px solid variables.$colorGray03;
					border-radius: 6px;
					@include mixins.transition(border-color .23s);
				}

				.fold_box_contents {
					position: absolute;
					top: 69px;
				}

				&.expanded {
					.fold_box_header {
						border-radius: 6px 6px 0 0;
						border-color: variables.$textColorBlack;
					}

					.fold_box_contents {
						width: 100%;
						max-height: 342px;
						border: 1px solid variables.$textColorBlack;
						background-color: #fff;
						border-radius: 0 0 6px 6px;
						box-sizing: border-box;
						z-index: 2;

						.fold_select_wrap {
							max-height: 342px;
							border-top: none;
						}
					}
				}
			}

			&.disabled {
				background-color: variables.$colorGray06;

				.fold_box_header {
					color: variables.$textColorLightGray;
					cursor: default;
				}

				.btn_fold {
					pointer-events: none;
					cursor: default;

					&[disabled] {
						pointer-events: none;
						cursor: default;
					}
				}
			}
		}
	}

	&.type_order {
		.fold_box_list {
			.fold_box {
				border: none;
				border-radius: 0;

				.fold_box_header {
					padding: 20px 36px 20px 0;
					border-top: 1px solid variables.$textColorBlack;
					border-bottom: 1px solid variables.$colorGray03;

					.form_chk {
						input {
							+ label {
								padding-top: 0;
								@include mixins.font_size_def;
								font-weight: variables.$fontWeightBold;
							}
						}
					}

					&:after {
						width: 16px;
						height: 17px;
						right: 10px;
						@include mixins.ratio_images('../images/arw_fold_toggle_lg', 'png', 16px, 17px);
					}
				}

				.fold_box_contents {
					padding: 0;

					.tbl_prod_wrap {
						.tbl_prod {
							border-top: none;
						}
					}
				}

				&.type_package {
					.fold_box_header {
						border-bottom: none;
					}

					.fold_box_contents {
						border-bottom: 1px solid variables.$colorGray04;
					}

					&.expanded {
						.fold_box_contents {
							padding-bottom: 21px;
						}
					}
				}

				~ .fold_box {
					margin-top: 30px;
				}
			}
		}

		~ .title_size_md {
			margin-top: 50px
		}
	}

	&.type_gray {
		.fold_box {
			border: 1px solid variables.$colorGray04;

			.fold_box_header {
				position: relative;
				padding: 20px 40px 20px 30px;
				cursor: pointer;
				@include mixins.font_size_sm;
				font-weight: variables.$fontWeightMedium;
				background-color: variables.$colorGray06;

				&:after {
					right: 30px;
					width: 10px;
					height: 5px;
					@include mixins.ratio_images('../images/arw_fold_toggle_sm', 'png', 10px, 5px);
				}
			}

			.fold_box_contents {
				padding: 0;

				.fold_contents_inner {
					padding: 24px 30px 25px;
				}
			}

			&.expanded {
				.fold_box_contents {
					border-top: 1px solid variables.$colorGray04;
				}
			}
		}
	}

	// faq
	&.type_faq {
		border: {
			top : 1px solid variables.$textColorBlack;
			bottom: 1px solid variables.$colorGray04;
		}

		.fold_box {
			margin: 0;
			border: 0;
			border-top: 1px solid variables.$colorGray04;
			
			&.expanded {
				.fold_box_contents {
					padding: {
						top: 0;
						bottom: 30px;
					}
				}
			}
			& + .fold_box {
				margin-top: 0;
			}
		}
		.fold_box_header {
			padding: 30px 60px 30px 40px;

			.faq_q {
				position: absolute;
				left: 10px;
				width: 24px;
				height: 24px;
				color: #fff;
				background-color: variables.$kyoboPurple;
				border-radius: 50%;
				font-family: variables.$fontEng;
				text-align: center;
				line-height: 24px;
			}
			
			&:first-child {
				border-top: 0;
			}
		}

		.fold_box_header:after {
			width: 16px;
			height: 17px;
			@include mixins.ratio_images('../images/arw_fold_toggle_lg', 'png', 16px, 17px);
		}

		.fold_box_contents {
			padding-left: 10px;
			padding-right: 10px;
		}

		.reply_content {
			position: relative;
			padding: 24px 24px 24px 40px;
			border-radius: 10px;
			background-color: variables.$colorGray06;

			p {
				margin-top: 10px;
				line-height: 22px;

				&:first-child {
					margin-top: 0;
				}
			}

			.etc {
				@include mixins.font_size_xxs;
				color: variables.$textColorGray;

				a {
					color: variables.$textColorGray;
				}
			}

			&:before {
				content: '';
				position: absolute;
				left: 24px;
				top: 28px;
				width: 8px;
				height: 9px;
				@include mixins.ratio_images('../images/arw_reply', 'png', 8px, 9px);
			}
		}
	}
}