	border: 1px solid #fff;
	background: transparent;
	@include mixins.transition(background-color 0.2s ease-out, border-color 0.2s ease-out);

	&:hover, &:focus, &:active {
		background: transparent;
		border-color: #fff;
	}

	&[disabled], &[aria-disabled], &.disabled {
		color: rgba(#fff, .5);
		background: transparent;
		border-color: #fff;

		[class^=ico_] {
			opacity: 0.4;
		}
	}
}

/* 알림 on/off 버튼 */
a.btn_alarm, button.btn_alarm {
	color: variables.$textColorLightGray;
	font-weight: variables.$fontWeightMedium;
	border: 1px solid variables.$colorGray02;

	.ico_alarm {
		display: inline-block;
		width: 14px;
		height: 18px;
		font-size: 0;
		background: {
			position: left 3px;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/ico_alarm_off', 'png', 14px, 14px);
	}

	.text {
		position: relative;
		padding-right: 1px;
	}

	&.active {
		color: variables.$kyoboPurple;
		border-color: variables.$kyoboPurple;

		.ico_alarm {
			background: {
				position: 1px center;
			}
			@include mixins.ratio_images('../images/ico_alarm_on', 'png', 12px, 18px);
		}
	}

	&.type_white {
		color: #fff;
		border-color: rgba(255, 255, 255, .5);

		.ico_alarm {
			background: {
				position: left 2px;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_alarm_wh', 'png', 14px, 15px);
		}
	}
}

/* 구독 on/off 버튼 */
