/* 버튼 - 링크 텍스트 */
a.btn_text_link, button.btn_text_link {
	color: variables.$textColorLightGray;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightRegular;
	text-align: left;

	.text {
		text-decoration: underline;
	}

	&.ellipsis {
		max-width: 100%;

		.text {
			@include mixins.ellipsis(false);
		}
	}

	&.row_2_ellipsis {
		.text {
			@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 2, false);
		}
	}

	&.size_lg {
		@include mixins.font_size_sm;
		font-weight: variables.$fontWeightMedium;

		&.row_2_ellipsis {
			.text {
				@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 2, false);
			}
		}
	}

	&.size_sm {
		@include mixins.font_size_xxs;

		&.row_2_ellipsis {
			.text {
				@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 2, false);
			}
		}
	}

	[class^=ico_] {
		margin-left: 4px;
	}

	&:hover, &:focus, &:active {
		.text {
			text-decoration: underline;
		}
	}
}

/* 파일 다운로드 버튼 */
a.btn_file_down, button.btn_file_down {
	min-width: auto;
	max-width: 100%;
	padding: 0 15px;
	border: 1px solid variables.$colorGray04;

	.file_name {
		@include mixins.justify_width;
		min-width: 0;

		.file_base, .file_extension, .memory {
			display: inline-block;
			@include mixins.font_size_xs;
			color: variables.$textColorBlack;
			vertical-align: top;
		}

		.file_base {
			@include mixins.ellipsis(false);
		}
		.file_extension {
			flex-shrink: 0;
		}
	}

	.memory {
		display: inline-block;
		margin-left: 6px;
		@include mixins.font_size_xs;
		color: variables.$textColorLightGray;
		vertical-align: top;

		+ .ico_file_down {
			margin-left: 12px;
		}
	}

	// 첨부파일
	.ico_file {
		display: inline-block;
		flex-shrink: 0;
		width: 13px;
		height: 14px;
		margin-right: 6px;
		@include mixins.ratio_images('../images/ico_file', 'png', 13px, 14px);
		background: {
			repeat: no-repeat;
			position: center center;
		}
	}

	// 다운로드
	.ico_file_down {
		display: inline-block;
		flex-shrink: 0;
		width: 11px;
		height: 14px;
		margin-left: 6px;
		@include mixins.ratio_images('../images/ico_file_down', 'png', 11px, 14px);
		background: {
			repeat: no-repeat;
			position: center center;
		}
	}
}

