// 더보기 버튼 (결과)
.btn_more_plus {
	position: relative;
	display: inline-block;
	padding-top: 31px;
	text-align: center;
	font-weight: variables.$fontWeightRegular;
	color: variables.$textColorLightGray;
	vertical-align: top;

	&:before {
		content: '';
		position: absolute;
		left: 50%;
		top: 0;
		display: block;
		width: 25px;
		height: 25px;
		margin-left: -12.5px;
		border: 1px solid variables.$colorGray02;
		border-radius: 50%;
		background: {
			color: #fff;
			position: 50%;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/ico_more_pick', 'png', 9px, 9px);
		box-sizing: border-box;
	}

	.text {
		@include mixins.font_size_xs;
		color: variables.$textColorLightGray;
	}

	&.type_white{
		&:before{
			border: none;
			background-color: transparent;
			@include mixins.ratio_images('../images/ico_more_pick_white', 'png', 25px, 25px);
		}

		.text{
			color: #fff;
		}
	}
}

