		letter-spacing: variables.$letterSpacing;
		vertical-align: top;

		&.prev, &.next {
			background: {
				position: center;
				repeat: no-repeat;
			}
		}

		&.prev {
			@include mixins.ratio_images('../images/btn_pagination_prev', 'png', 24px, 24px);
		}

		&.next {
			@include mixins.ratio_images('../images/btn_pagination_next', 'png', 24px, 24px);
		}
	}

	.page_num {
		display: inline-block;
		margin: 0 8px;
		box-sizing: border-box;
		vertical-align: top;

		.btn_page_num, .text_ellipsis {
			display: inline-block;
			text-align: center;
			vertical-align: top;
			box-sizing: border-box;
		}

		.btn_page_num {
			min-width: 24px;
			height: 24px;
			padding: 5px 4px 6px 4px;
			font-size: variables.$fontSizeXxs;
			line-height: 1;
			letter-spacing: variables.$letterSpacing;
			font-family: variables.$fontEng;
			color: variables.$textColorLightGray;
			border-radius: 12px;
			background-color: transparent;
			@include mixins.transform(color 0.2s ease-out, font-weight 0.2s ease-out, background 0.2s ease-out);

			& ~ .btn_page_num {
				margin-left: 8px;
			}

			&.active {
				color: #fff;
				font-weight: variables.$fontWeightBold;
				background-color: variables.$textColorLightGray;
			}
