/* 버튼 - 목록 스위칭 */
.switch_list_btn_wrap {
	display: inline-block;
	width: 75px;
	font-size: 0;
	/*margin: 0 6px 0 20px;*/
	vertical-align: top;

	[class^='btn_'] {
		display: inline-block;
		width: 50%;
		height: 38px;
		border: 1px solid variables.$colorGray02;
		padding: 0;
		margin: 0;
		border-radius: 0;
		vertical-align: top;
		box-sizing: border-box;

		& + [class^='btn_'] {
			border-left: 0;
		}

		&:first-child {
			border-radius: 6px 0 0 6px;
		}

		&:last-child {
			border-radius: 0 6px 6px 0;
		}

		&:after{
			content: '';
			display: block;
			width: 14px;
			height: 14px;
			margin: 0 auto;
		}

		&.ico_img {
			&:after {
				position: relative;
				@include mixins.ratio_images('../images/ico_view_img', 'png', 14px, 14px);
			}
		}

		&.ico_list {
			&:after {
				content: '';
				@include mixins.ratio_images('../images/ico_view_list', 'png', 14px, 14px);
			}
		}

		&.ico_txt {
			&:after {
				position: relative;
				display: block;
				content: '';
				width: 14px;
				height: 14px;
				@include mixins.ratio_images('../images/ico_view_txt', 'png', 14px, 14px);
			}
		}

		&.active {
			&.ico_img {
				&:after {
					@include mixins.ratio_images('../images/ico_view_img_active', 'png', 14px, 14px);
				}
			}

			&.ico_list {
				&:after {
					@include mixins.ratio_images('../images/ico_view_list_active', 'png', 14px, 14px);
				}
			}

			&.ico_txt {

				&:after {
					@include mixins.ratio_images('../images/ico_view_txt_active', 'png', 14px, 14px);
				}
			}
		}
	}
}

