/* 버튼 - 버튼 정렬 */
.btn_wrap {
	display: block;
	font-size: 0;
	text-align: center;

	[class^=btn_] {
		& + [class^=btn_] {
			margin-left: 6px;
		}
	}

	.btn_lg {
		& + .btn_lg {
			margin-left: 10px;
		}
	}

	.btn_xs {
		& + .btn_xs {
			margin-left: 3px;
		}
	}

	&.page_bottom {
		margin-top: 40px;

		+ .info_text_box {
			margin-top: 60px;
		}
	}

	&.justify {
		display: flex;

		[class^=btn_] {
			flex: 1;
			min-width: auto;

			& + [class^=btn_] {
				margin-left: 6px;
			}
		}
	}

	&.full {
		[class^=btn_] {
			width: 100%;
			min-width: auto;

			& + [class^=btn_] {
				margin: 6px 0 0;
			}
		}

		.btn_info_text {
			& + .btn_info_text {
				margin-top: 10px;
			}
		}
	}

	&.overlap {
		.btn_lg, .btn_sm {
			&:not(:only-child) {
				border-radius: 0;

				&:first-child {
					border-radius: 6px 0 0 6px;
				}

				&:last-child {
					border-radius: 0 6px 6px 0;
				}
			}
		}

		.btn_lg {
			+ .btn_lg {
				margin-left: -1px;
			}
		}

		.btn_sm {
			+ .btn_sm {
				margin-left: -1px;
			}
		}

		&.single {
			border-radius: 6px;
		}
	}
}

.btn_more_list {
	.btn_item {
		& ~ .btn_item {
			margin-top: 8px;
		}
	}

	& + .bul_item_asterisk {
		margin-top: 6px;
	}
}

