@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
/* 토스트 팝업 */
.toast_wrap {
	display: none;
	position: fixed;
	top: 50%;
	left: 50%;
	z-index: 1005;
	@include mixins.transform(translate(-50%, -50%));
	font-size: 0;

	.toast_message {
		@include mixins.justify_width(center, center, row, true);
		box-sizing: border-box;
		min-width: 300px;
		height: 60px;
		padding: 2px 30px 0;
		border-radius: 10px;
		background: rgba(variables.$kyoboPurple, 0.9);
		border: 1px solid variables.$kyoboPurple;
		box-shadow: 0 5px 10px rgba(variables.$textColorBlack, 0.16);
		color: #fff;
		@include mixins.font_size_sm;
		text-align: center;
	}
}

.dialog_wrapper {
	.ui-dialog {
		// 구매방법 별 배송안내
		&.dialog_delivery_info {
			.dialog_wrap {
				height: 750px !important;

				.dialog_contents {
					height: 100%;
					overflow: hidden;

					.tab_wrap {
						height: 100%;

						.tab_content {
							height: calc(100% - 68px);
						}
					}
				}
			}
		}

		// 배송정보 입력
		&.dialog_input_delivery {
			.dialog_wrap {
				height: 750px !important;

				.dialog_contents {
					height: 100%;
					overflow: hidden;

					.tab_wrap {
						height: 100%;

						.tab_content {
							height: calc(100% - 68px);
						}
					}

					.form_wrap {
						.form_box {
							&.type_row {
								.from_cont {
									.btn_wrap.justify {
										width: 212px;
									}
								}
							}
						}

						& + .tbl_row_wrap {
							margin-top: 20px;
						}

						& + .btn_wrap.full {
							margin-top: 20px;
						}
					}

					.btn_wrap.full {
						& + .info_text {
							margin-top: 20px;
						}
					}
				}
			}
		}

		// 바로드림 서비스 안내
		&.dialog_service_info {
			.dialog_wrap {
				height: 750px !important;

				.dialog_contents {
					height: 100%;
					overflow: hidden;

					.tab_wrap {
						height: 100%;

						.tab_content {
							height: calc(100% - 68px);
						}
					}
				}
			}
		}

		// 이미지 크게보기 팝업
		&.dialog_show_img {
			width: 100%;
			height: 100% !important;
			max-height: none !important;

			.btn_dialog_close {
				.ico_dialog_close {
					width: 24px;
					height: 24px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/btn_dialog_close_w', 'png', 24px, 24px);
				}
			}

			.dialog_wrap {
				.btn_dialog_close {
					top: 50px;
					right: 50px;
				}

				&.ui-dialog-content {
					flex-shrink: 0;
					background-color: transparent;
					padding: 0;
					border-radius: 0;
					max-height: none !important;
					min-width: 1440px;
					box-sizing: border-box;

					.dialog_contents {
						@include mixins.justify_width(center, center);
						flex-direction: column;
						background-color: transparent;
						margin-top: 0;
						max-height: none !important;
						height: 100vh;
						box-sizing: border-box;

						.gallery_top_wrap {
							.gallery_top {
								height: calc(100vh - 100px - 180px);
								width: calc(100vh - 100px - 180px);
								max-width: 1440px;
								max-height: 1440px;

								.swiper-wrapper {
									.swiper-slide {
										width: 100%;
									}
								}

								.swiper-pagination {
									bottom: 30px;
								}
							}

							.swiper-button-next,
							.swiper-button-prev {
								position: absolute;
								top: 50%;
								margin-top: -22px;
								z-index: 10;
								cursor: pointer;
								width: 44px;
								height: 44px;
								background: {
									position: center;
									repeat: no-repeat;
									color: transparent;
								}
								@include mixins.ratio_images('../images/btn_bigimg_pop_arrow', 'png', 20px, 40px);
							}

							.swiper-button-next, .swiper-button-prev {
								&.swiper-button-disabled {
									opacity: .6;
								}
							}

							.swiper-button-next {
								right: 50px;
							}

							.swiper-button-prev {
								transform: rotate(180deg);
								left: 50px;
							}

							&.no_thumbnail{
								.gallery_top {
									height: calc(100vh - 180px);
									width: calc(100vh - 180px);
								}
							}
						}

						.gallery_thumbs_wrap {
							position: relative;
							width: 704px;
							margin-top: 36px;

							.gallery_thumbs {
								max-width: 568px;
								width: 100%;
								height: 64px;
								box-sizing: border-box;
								margin: 0 auto;

								.swiper-wrapper {
									&.align_center {
										justify-content: center;
									}

									.swiper-slide {
										width: 64px;
										height: 64px;
										border-radius: 8px;
										overflow: hidden;
										margin: 0 4px;
										box-sizing: border-box;

										&:first-child,
										&.swiper-slide-active {
											margin-left: 0;
										}

										&:last-child,
										&:nth-child(8) {
											margin-right: 0;
										}

										.btn_thumbs {
											width: 100%;
											height: 100%;

											.thumb_img_box {
												position: relative;
												display: block;
												width: 100%;
												padding-top: 100%;
												background-size: cover;
												background-repeat: no-repeat;
												background-position: center;
											}

											img {
												display: none;
											}
										}
									}

									.swiper-slide-thumb-active {
										opacity: 1;

										&:before {
											content: '';
											position: absolute;
											top: 0;
											left: 0;
											width: 100%;
											height: 100%;
											border: 2px solid #fff;
											box-sizing: border-box;
											border-radius: 8px;
											z-index: 2;
										}

									}
								}
							}

							.swiper-button-next,
							.swiper-button-prev {
								position: absolute;
								top: 50%;
								margin-top: -22px;
								z-index: 10;
								cursor: pointer;
								width: 44px;
								height: 44px;
							}

							.swiper-button-next {
								right: 50%;
								margin-right: -352px;
							}

							.swiper-button-prev {
								transform: rotate(180deg);
								left: 50%;
								margin-left: -352px;
							}
						}
					}
				}
			}
		}

		// 미리보기 팝업
		&.dialog_preview {
			width: 1020px;
			height: 720px !important;
			max-height: 720px;

			.ui-dialog-content {

				.dialog_contents {
					.preview_viewer_wrap {
						height: 528px;
						@include mixins.clearfix;

						.viewer_box, .viewer_info_box {
							float: left;
							height: 100%;
						}

						.viewer_box {
							position: relative;
							width: 706px;
							overflow: auto;

							.custom_scroll_wrap {
								height: 528px;
							}

							.preview_area {
								margin: 0 22px;
								overflow: hidden;
								box-sizing: content-box;
								@include mixins.justify_width;
								transform-origin: 0 0;

								.preview_item {
									flex-basis: 330px;
									height: 526px;
									border: 1px solid #d5d5d5;

									&:nth-child(2n + 1) {
										flex-basis: 329px;
										border-right: 0;
									}

									.preview_item_inner {
										height: 100%;
										text-align: center;
										@include mixins.justify_width(center, center);

										img {
											max-width: 100%;
											max-height: 100%;
										}
									}

									&.logo_item {
										.preview_item_inner img {
											max-width: 50%;
										}
									}
								}

								&.view_single {
									.preview_item {
										flex-basis: 660px;

										&:nth-child(2n + 1) {
											border-right: 1px solid #d5d5d5;
										}
									}
								}

								&.view_collect {
									flex-wrap: wrap;

									.preview_item {
										flex-basis: 99px;
										height: 150px;
										cursor: pointer;

										.preview_item_inner {
											width: 99px;
										}

										&.logo_item {
											visibility: hidden;
										}

										&:nth-child(2n + 1) {
											flex-basis: 98px;
										}

										&:nth-child(n + 7) {
											margin-top: 20px;
										}

										&:nth-child(2n + 1) {
											margin-left: 27px;
										}

										&:nth-child(6n + 1) {
											margin-left: 0;
										}

										&:nth-child(n + 19) {
											margin-top: 0;
										}
									}
								}
							}

							.swiper-button-prev {
								left: 0;
							}

							.swiper-button-next {
								right: 0;
							}
						}

						.preview_handle_box {
							position: absolute;
							width: 706px;
							left: 0;
							top: 50%;
							pointer-events: none;

							.swiper-button-prev, .swiper-button-next {
								pointer-events: auto;
							}
						}

						.viewer_info_box {
							width: 244px;
							margin-left: 10px;

							.viewer_info_body {
								.box_info_wrap {
									background: #f7f7f7;
									padding: 20px;
									border-radius: 10px;

									.title_wrap {
										margin-top: 20px;
										padding-bottom: 10px;

										&:last-of-type {
											padding-bottom: 0;
										}
									}

									.thumbnail_product_item {
										margin-bottom: 10px;
									}

									.btn_wrap {
										@include mixins.justify_width(start, between);

										.btn_cart {
											flex: 1;
										}

										.btn_preview_type, .btn_interval {
											flex: 1;
											background: #fff;
											border: 1px solid #d5d5d5;
											border-radius: 6px;
											font-size: variables.$fontSizeMin;

											&.active {
												border-color: #595959;
											}
										}

										.btn_preview_type {
											height: 65px;

											~ .btn_preview_type {
												margin-left: 4px;
											}

											.ico_preview_type {
												width: 100%;
												height: 24px;
												background: {
													position: center;
													repeat: no-repeat;
												}
												margin-bottom: 2px;

												&.double {
													@include mixins.ratio_images('../images/ico_preview_type_double', 'png', 30px, 24px);
												}

												&.single {
													@include mixins.ratio_images('../images/ico_preview_type_single', 'png', 30px, 24px);
												}

												&.collect {
													@include mixins.ratio_images('../images/ico_preview_type_collect', 'png', 30px, 24px);
												}
											}
										}

										.btn_interval {
											height: 30px;

											+ .btn_interval {
												margin-left: 4px;
											}
										}
									}

									.preview_zoom_box {
										&.hide {
											visibility: hidden;
										}
									}

									.preview_index_box {
										height: 144px;

										.preview_index_list {
											.bul_item_dot {
												a, span {
													color: #595959;
												}

												span {
													opacity: .5;
												}

												&.active a {
													color: #000;
													font-weight: variables.$fontWeightMedium;
												}
											}
										}
									}

									.btn_sns_share {
										width: 24px;
										height: 24px;
										background: {
											position: center;
											repeat: no-repeat;
										}

										&.kakao {
											@include mixins.ratio_images('../images/btn_kakao_share_sm', 'png', 24px, 24px);
										}

										&.band {
											@include mixins.ratio_images('../images/btn_band_share_sm', 'png', 24px, 24px);
										}

										&.facebook {
											@include mixins.ratio_images('../images/btn_facebook_share_sm', 'png', 24px, 24px);
										}

										&.twitter {
											@include mixins.ratio_images('../images/btn_twitter_share_sm', 'png', 24px, 24px);
										}

										&.url_copy {
											@include mixins.ratio_images('../images/btn_copy_share_sm', 'png', 24px, 24px);
										}

										~ .btn_sns_share {
											margin-left: 6px;
										}
									}
								}
							}

							.viewer_info_footer {
								@include mixins.justify_width(center, end);
								margin-top: 9px;

								.swiper_control_box {
									flex: 1;
									text-align: left;
								}

								.tooltip_wrap {
									height: 19px;

									.info_text {
										color: variables.$textColorLightGray;
									}

									.btn_tooltip {
										margin-top: 1px;
									}
								}
							}
						}
					}

					.preview_title {
					}
				}
			}
		}

		// 메인 팝업
		&.dialog_main {
			position: absolute;
			width: 400px;
			top: 100px;
			left: 100px;

			.dialog_wrap {
				&.ui-dialog-content {
					padding: 0;
					border-bottom-left-radius: 0;
					border-bottom-right-radius: 0;

					.dialog_contents {
						margin-top: 0;

						.swiper-container {
							height: 400px;

							.swiper-button-prev {
								left: 20px;
							}

							.swiper-button-next {
								right: 20px;
							}
						}
					}

					.dialog_main_btn_wrap {
						display: flex;
						height: 50px;
						border: 1px solid variables.$colorGray03;
						border-top: 0;
						border-radius: 0 0 16px 16px;
						box-sizing: border-box;

						.btn_main_pop {
							flex: 1;
							color: variables.$textColorLightGray;
							letter-spacing: variables.$letterSpacing;
							background-color: #fff;
							border-bottom-left-radius: 16px;
							border-bottom-right-radius: 16px;

							&:first-child {
								position: relative;

								&:after {
									content: '';
									position: absolute;
									top: 50%;
									right: 0;
									width: 1px;
									height: 14px;
									background-color: variables.$colorGray03;
									@include mixins.transform(translateY(-50%));
								}
							}

							.text {
								padding-top: 2px;
								font-weight: variables.$fontWeightMedium;
							}
						}
					}
				}
			}

		}

		&.dialog_sm {
			// 쿠폰
			.coupon_btn_item,
			.coupon_chk_item {
				width: 100%;
			}
		}

		&.dialog_lg {
			// 결합상품상세보기 팝업
			&.dialog_combine_prod {
				.dialog_wrap {
					&.ui-dialog-content {
						height: 750px !important;
						padding: 0;

						.dialog_header {
							padding: {
								left: 30px;
								right: 30px;
							}
						}

						.dialog_contents {
							max-height: none;
							height: calc(100% - 75px);
							margin-top: 0;
							padding-bottom: 40px;

							.combine_prod_header {
								@include mixins.justify_width(center);
								padding: 19px 30px;
								border-bottom: 1px solid variables.$colorGray04;
								background-color: variables.$colorGray06;

								.fold_box_wrap {
									flex: 1;
								}

								.detail_view_control {
									margin: 0 40px;
									flex-shrink: 0;

									.btn_view_control {
										padding-bottom: 8px;
										border-bottom: 3px solid transparent;
										@include mixins.font_size_sm;
										font-weight: variables.$fontWeightBold;
										color: variables.$textColorLightGray;
										@include mixins.transition(color .23s, border-color .23s);

										&.active {
											color: variables.$textColorBlack;
											border-color: variables.$textColorBlack;
										}

										+ .btn_view_control {
											margin-left: 40px;
										}
									}
								}

								.btn_wrap {
									flex: 0 0 190px;

									.btn_md {
										width: 92px;
										min-width: 92px;
										padding: 0 10px;
										@include mixins.font_size_xs;
										font-weight: variables.$fontWeightRegular;
									}
								}
							}

							.combine_prod_detail {
								height: calc(100% - 109px);
								padding: 40px 31px 0;
								box-sizing: border-box;

								.combine_prod_info, .combine_prod_review {
									display: none;
									height: 100%;

									&.active {
										display: block;
									}
								}

								.product_detail_area {
									&:first-child {
										margin-top: 0;
									}

									&.book_card {
										.book_card_contents {
											&.book_card_swiper {
												padding: 0 22px;

												.swiper-slide {
													width: 388px;
												}

												.swiper-button-prev {
													left: 0;
												}

												.swiper-button-next {
													right: 0;
												}
											}
										}
									}

									&.killing_part_contents {
										&.killing_part_swiper {
											padding: 0 22px;

											.swiper-button-prev {
												left: 0;
											}

											.swiper-button-next {
												right: 0;
											}
										}
									}

									&.klover_review_wrap {
										.review_swiper {
											padding: 0 22px;

											.swiper-button-prev {
												left: 0;
											}

											.swiper-button-next {
												right: 0;
											}
										}
									}

									&.killing_part {
										.killing_part_swiper {
											padding: 0 22px;

											.swiper-slide {
												width: 388px;
											}

											.swiper-button-prev {
												left: 0;
											}

											.swiper-button-next {
												right: 0;
											}
										}
									}
								}
							}
						}
					}
				}
			}

			// 캘린더 일정 안내
			.schedule_calendar{
				height: 750px !important;

				.calendar_head{
					margin-bottom: 20px;
					font-size: 0;
					text-align: center;

					.calendar_title{
						display: inline-block;
						margin: 0 30px;
						vertical-align: top;

						.year, .month{
							@include mixins.font_size_sm;
							font-weight: variables.$fontWeightBold;
						}

						.month{
							margin-left: 8px;
						}
					}

					.btn_calendar_prev, .btn_calendar_next{
						display: inline-block;
						width: 24px;
						height: 24px;
						border-radius: 12px;
						text-align: center;
						font-size: 13px;
						letter-spacing: variables.$letterSpacing;
						vertical-align: top;
					}

					.btn_calendar_prev{
						@include mixins.ratio_images('../images/btn_pagination_prev', 'png', 24px, 24px);
					}

					.btn_calendar_next{
						@include mixins.ratio_images('../images/btn_pagination_next', 'png', 24px, 24px);
					}
				}

				.calendar_body{
					text-align: left;

					.tbl_legend_box{
						margin-bottom: 13px;

						.schedule_list{
							font-size: 0;

							.schedule_item{
								display: inline-block;
								color: variables.$textColorLightGray;
								vertical-align: top;

								~ .schedule_item{
									margin-left: 10px;
								}
							}
						}
					}

					.lecture, .travel, .exhibition{
						position: relative;
						padding-left: 12px;
						display: block;
						@include mixins.font_size_min;

						&:before{
							content: '';
							position: absolute;
							top: 5px;
							left: 0;
							display: inline-block;
							width: 6px;
							height: 6px;
							border-radius: 100%;
							vertical-align: top;
						}
					}

					.lecture{
						&:before{
							background-color: #06BBC0;
						}
					}

					.travel{
						&:before{
							background-color: #4DAC27;
						}
					}

					.exhibition{
						&:before{
							background-color: variables.$kyoboPurple;
						}
					}

					.tbl_calendar_wrap{
						border-top: 1px solid variables.$textColorBlack;

						.tbl_calendar{
							width: 100%;
							table-layout: fixed;

							th{
								padding: 10px 10px;
								border-bottom: 1px solid variables.$colorGray03;
								background-color: variables.$colorGray06;
								@include mixins.font_size_xxs;
								color: variables.$textColorGray;
								text-align: center;

								~ th{
									border-left: 1px solid variables.$colorGray04;
