							@include mixins.ratio_images('../images/img_powder_iron_pop_arw', 'png', 40px, 40px);
						}
					}
					+.step_pill_list {
						margin-top: 30px;
					}
				}
				
				.step_pill_list {
					.step_item {
						padding-right: 0;
					}
					.img_box {
						border-radius: 0;
						border: 0;
						
						> img {
							width: 100%;
						}
					}
					
					+.info_text_box {
						margin-top: 20px;
					}
				}
			}
			
			
			//분철서비스 이용안내 SCR-BIZ15-21-P101
			&.pop_service_powderiron_apply_list {
				
				.prod_list {
					.prod_item {
						width: 142px;
						margin-left: 28px;
						
						&:nth-child(6n+1) { margin-left: 0; }
						&:nth-child(3n+1) { margin-left: 0; }
						&:nth-child(n+4) { margin-top: 24px; }
						&:nth-child(n+7) { margin-top: 24px; }
					}
					
				}
				.pagination{
					margin-top: 40px;
					padding-top: 20px;
					border-top: 1px solid variables.$colorGray04;
				}
			}
			

			// 스펙비교 - 도서검색
			&.spec_compare_search {
				height: 750px !important;

				.no_data{
					padding: 150px 0 0;
				}

				.spec_compare_layer_wrap{
					position: absolute;
					min-width: auto;
					width: 560px;
					height: 74px;
					left: -30px;
					border-top: 1px solid variables.$colorGray03;

					.compare_layer_inner{
						width: 100%;
						padding: 19px 30px 30px;
						border: none;
						border-radius: 0;
						box-sizing: border-box;

						.compare_prod_area {
							float: none;
							width: 100%;
							@include mixins.justify_width(center);

							.compare_prod_list{
								padding-right: 20px;
								margin-right: auto;

								.prod_item{
									width: 64px;
									.prod_area{

									}

									~ .prod_item{
										margin-left: 5px;
									}
								}
							}
						}
					}
				}

				&.ui-dialog-content{
					.dialog_contents{
						overflow: visible;
					}
				}

				.dialog_contents{
					&.active{
						.spec_compare_layer_wrap{
							display: block;
							bottom: 0;
						}

						.custom_scroll_wrap{
							max-height: calc(100% - 74px);
						}

						.tab_wrap{
							padding-bottom: 30px;
						}
					}
				}
			}
		}

		// TODO: 임시 적용 - 공통으로 빼야하는지 확인 후 정리 필요
		&:not(.dialog_lg) {
			.dialog_contents {
				.tbl_row, .tbl_row_line {
					tbody {
						td {
							.valid_check, .form_ip, .form_sel, .input_btn_box, .form_col_group,
							.pw_valid_wrap, .form_ip_pw, .byte_check_wrap, .file_upload_group,
							.form_filter_box, .input_text_box, .input_price_box, .price_value_box {
								width: 100%;
							}
						}
					}
				}
			}

			.form_wrap {
				.form_box {
					&.type_row {
						.from_cont {
							.valid_check, .form_ip, .form_sel, .input_btn_box, .form_col_group,
							.pw_valid_wrap, .form_ip_pw, .byte_check_wrap, .file_upload_group,
							.form_filter_box, .input_text_box, .input_price_box, .price_value_box {
								width: 100%;
							}
						}
					}
				}
			}
		}
	}
}

// sns 공유하기
.sns_share_wrap {
	position: relative;
	display: inline-block;
	width: 42px;
	height: 42px;
	vertical-align: top;

	// 공유하기 버튼
	.btn_share {
		width: 42px;
		height: 42px;
		line-height: 40px;
		border: 1px solid variables.$colorGray02;
		text-align: center;
		border-radius: 50%;
		box-sizing: border-box;

		.ico_share {
			display: inline-block;
			width: 14px;
			height: 14px;
			margin-top: 13px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_share', 'png', 14px, 14px);
			vertical-align: top;
		}
	}

	.sns_share_box {
		position: absolute;
		top: 0;
		right: 0;
		width: 231px;
		border: 1px solid variables.$colorGray02;
		border-radius: 31px;
		padding: 7px 0 7px 10px;
		display: none;
		background-color: #fff;
	}

	&.size_sm {
		width: 36px;
		height: 36px;

		.btn_share {
			width: 36px;
			height: 36px;
			line-height: 34px;

			.ico_share {
				margin-top: 10px;
			}
		}

		.sns_share_box {
			width: 205px;
			padding: 4px 0 4px 5px;

			.btn_sns_share {
				~ .btn_sns_share {
					margin-left: 6px;
				}
			}

			.btn_sns_share_close {
				margin-left: 11px;
			}
		}
	}

	&.active {
		.sns_share_box {
			@include mixins.justify_width(center);
		}

		.btn_share {
			display: none;
		}
	}
}

.sns_share_box {
	@include mixins.justify_width(center);
	box-sizing: border-box;

	.btn_sns_share {
		width: 28px;
		height: 28px;
		background: {
			position: center;
			repeat: no-repeat;
		}

		&.kakao {
			@include mixins.ratio_images('../images/btn_kakao_share', 'png', 28px, 28px);
		}

		&.band {
			@include mixins.ratio_images('../images/btn_band_share', 'png', 28px, 28px);
		}

		&.facebook {
			@include mixins.ratio_images('../images/btn_facebook_share', 'png', 28px, 28px);
		}

		&.twitter {
			@include mixins.ratio_images('../images/btn_twitter_share', 'png', 28px, 28px);
		}

		&.url_copy {
			@include mixins.ratio_images('../images/btn_copy_share', 'png', 28px, 28px);
		}

		~ .btn_sns_share {
			margin-left: 10px;
		}
	}

	.btn_sns_share_close {
		width: 13px;
		height: 13px;
		margin-left: 14px;
		@include mixins.ratio_images('../images/btn_share_close', 'png', 13px, 13px);
	}
}

.caster_basis_wrap {
	.caster_basis_box {
		padding: 16px;
		border-radius: 16px;
		text-align: center;

		&.caster_popular {
			background: rgba(#50b19e, .06);
		}

		&.caster_partner {
			background: rgba(variables.$kyoboPurple, .06);
		}

		.cast_popular, .cast_partner {
			display: inline-block;
			padding: 0 12px;
			border-radius: 13px;
			height: 26px;

			.ico_caster_badge {
				display: inline-block;
				position: relative;
				width: 15px;
				height: 26px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				margin-right: 3px;
				vertical-align: top;
			}

			.text {
				color: #fff;
				font-size: variables.$fontSizeMin;
				line-height: 26px;
			}
		}

		.cast_popular {
			background: #50b19e;

			.ico_caster_badge {
				@include mixins.ratio_images('../images/ico_badge_popular', 'png', 13px, 12px);
			}
		}

		.cast_partner {
			background: variables.$kyoboPurple;

			.ico_caster_badge {
				background-position: top 5px center;
				@include mixins.ratio_images('../images/ico_badge_partner', 'png', 12px, 15px);
			}
		}

		.caster_basis_desc {
			margin-top: 10px;
			@include mixins.font_size_sm;
		}

		+ .caster_basis_box {
			margin-top: 14px;
		}
	}
}


