@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';

// 이미지 가로형/세로형 체크 컨텐츠 공통
.portrait_img_box {
	position: relative;
	display: block;
	width: 100%;
	height: 100%;
	overflow: hidden;

	img {
		position: absolute;
		display: block;
		margin: 0 auto;
		top: 50%;
		left: 50%;
		@include mixins.transform(translate(-50%, -50%));
	}

	&.landscape, &.even {
		img {
			max-width: 100%;
		}
	}

	&.portrait {
		img {
			max-height: 100%;
		}
	}
}

// 블러이미지 컨텐츠 공통
.blur_img_box {
	position: relative;
	overflow: hidden;

	img {
		position: absolute;
		width: 160%;
		top: 50%;
		left: 50%;
		@include mixins.transform(translate(-50%, -50%));
	}
}

.blur_img_wrap {
	position: relative;

	&.landscape, &.even {
		.blur_img_box {
			height: 100%;

			img {
				height: 100%;
			}
		}

		.portrait_img_box {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			img {
				max-width: 100%;
			}
		}
	}

	&.portrait {
		.blur_img_box {
			display: none;
		}

		.portrait_img_box {
			img {
				position: static;
				width: 100%;
				@include mixins.transform(none);
			}
		}
	}
}

/* 컨텐츠 박스 */
.round_gray_box {
	display: block;
	padding: 36px 30px 40px;
	border-radius: 20px;
	background-color: variables.$colorGray06;
	box-sizing: border-box;

	.bottom_btn_area {
		margin: 30px 0 -10px;
		text-align: center;
		font-size: 0;

		[class^=btn_] {
			& + [class^=btn_] {
				margin-left: 5px;
			}
		}
	}

	.bottom_info_area {
		display: block;
		margin-top: 30px;
		padding-top: 20px;
		border-top: 1px solid variables.$colorGray04;
		box-sizing: border-box;
	}

	.explan_text_list {
		font-size: 0;

		.explan_title {
			display: inline-block;
			width: 122px;
			padding-right: 12px;
			@include mixins.font_size_xxs;
			font-weight: variables.$fontWeightMedium;
			vertical-align: top;
			box-sizing: border-box;

		}

		.explan_desc {
			display: inline-block;
			width: calc(100% - 122px);
			@include mixins.font_size_xxs;
			color: variables.$textColorLightGray;
			vertical-align: top;

			~ .explan_title, ~ .explan_desc {
				border-top: 1px solid variables.$colorGray04;
				margin-top: 18px;
				padding-top: 18px;
			}
		}
	}

	.order_num {
		display: inline-block;
		@include mixins.font_size_xs;
		color: variables.$textColorBlack;
		font-weight: variables.$fontWeightMedium;
		vertical-align: top;

		.num {
			margin-left: 2px;
			@include mixins.font_size_def;
		}

		& + .info_text {
			margin-top: 12px;
		}
	}

	+ .round_gray_box {
		margin-top: 20px;
	}

	&.type_sub {
		padding: 24px;
		border-radius: 10px;
	}

	&.type_sub_sm {
		padding: 20px;
		border-radius: 10px;
	}

	&.type_md {
		padding: 30px;
		border-radius: 10px;
	}

	&.type_divide{
		padding: 0;
		background-color: #fff;
		border: 1px solid variables.$colorGray04;
		border-radius: 10px;

		.box_header{
			@include mixins.justify_width(center);
			border-radius: 10px 10px 0 0;
			padding: 19px 19px;
			border-bottom: 1px solid variables.$colorGray04;
			background-color: variables.$colorGray06;
			word-break: break-all;

			.right_box{
				flex-shrink: 0;
				margin-left: auto;
				padding-left: 20px;
				font-size: 0;
			}
		}

		.box_body{
			border-radius: 0 0 10px 10px;
			padding: 19px 19px;

			.prod_swiper_wrap {
				margin: 0 -23px;
				padding: 0 23px;
			}
		}
	}

	// 단순 텍스트 리스트 공통 예외 여백
	.info_text_list {
		.info_text {
			& + .info_text,
			& + .font_size_min,
			& + .font_size_sm {
				margin-top: 6px;
			}
		}
	}
}

.round_green_box {
	@include mixins.justify_width(center);
	flex-wrap: wrap;
	padding: 23px 23px;
	background: rgba(variables.$kyoboGreen, .04);
	border-radius: 10px;
	box-sizing: border-box;

	.desc {
		@include mixins.font_size_sm;

		.ico_location {
			vertical-align: top;
		}

		.gap {
			display: inline-block;
			margin: 0 12px;
			transform: translateY(-2px);
			@include mixins.font_size_min;
			color: variables.$colorGray03;
		}
	}

	.right_area {
		margin-left: auto;
		color: variables.$textColorGray;

		b {
			font-weight: variables.$fontWeightMedium;
		}
	}

	.point_label {
		display: block;
		width: 100%;
		@include mixins.font_size_xs;
		color: variables.$textColorBlack;
		text-align: center;
	}

	.point_val {
		display: block;
		width: 100%;
		margin-top: 6px;
		@include mixins.font_size_def;
		color: variables.$textColorGreen;
		font-weight: variables.$fontWeightBold;
		text-align: center;
	}

	&.type_sub_sm {
		padding: 20px 20px;
	}
}

/* auto overflow 공통 */
.auto_overflow_wrap {
	.auto_overflow_contents {
		overflow: hidden;
	}

	.auto_overflow_footer {
		display: none;
	}

	&.overflow {
		.auto_overflow_footer {
			display: block;
			margin-top: 12px;

			&.type_cont {
				margin-top: 30px;
				text-align: center;
			}
		}
	}

	&.active {
		> .auto_overflow_contents {
			max-height: none !important;
		}

		&.type_tbl_col {
			.auto_overflow_contents {
				&:after {
					content: none;
				}
			}
		}
	}

	&.type_tbl_col {
		.auto_overflow_contents {
			position: relative;
			border-bottom: 1px solid variables.$colorGray04;

			&:after {
				content: '';
				position: absolute;
				display: block;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 10px;
				background-color: #fff;
			}
		}

		.tbl_col_wrap {
			border-bottom: none;
		}
	}

	&.type_text {
		&.overflow {
			.auto_overflow_contents {
				position: relative;

				&:after {
					content: '';
					width: 100%;
					height: 15px;
					background: linear-gradient(180deg, transparent, #fff 15px);
					position: absolute;
					bottom: 0;
				}
			}

			&.active {
				.auto_overflow_contents {
					&:after {
						content: none;
					}
				}
			}
		}
	}
}

/* 상품/리뷰 컨텐츠 공통 사용 */
// " 좋았어요 텍스트
.review_quotes_text {
	display: inline-block;
	position: relative;
	padding-left: 18px;
	color: variables.$textColorPurple;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightMedium;
	vertical-align: top;

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 3px;
		width: 13px;
		height: 13px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/ico_quotes', 'png', 13px, 13px);
	}

	&.font_size_xxs {
		@include mixins.font_size_xxs;

		&:before {
			top: 2px;
		}
	}
}

// 클로버 아이콘 + 점수 텍스트
.review_klover_text {
	display: inline-block;
	position: relative;
	padding-left: 19px;
	color: variables.$textColorBlack;
	@include mixins.font_size_xs;
	font-weight: variables.$fontWeightMedium;
	vertical-align: top;

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 4px;
		width: 14px;
		height: 14px;
		background: {
			repeat: no-repeat;
			position: center;
		}
		@include mixins.ratio_images('../images/ico_klover_sm', 'png', 14px, 14px);
	}

	&.font_size_xxs {
		@include mixins.font_size_xxs;

		&:before {
			top: 3px;
		}
	}
	&.font_size_min {
		@include mixins.font_size_min;

		&:before {
			top: 2px;
		}
	}
}

// 클로버 아이콘 + 점수 텍스트 / 리뷰 갯수 텍스트
.review_klover_box {
	display: inline-block;
	font-size: 0;
	vertical-align: top;

	.review_desc {
		display: inline-block;
		margin-left: 4px;
		color: variables.$textColorLightGray;
		@include mixins.font_size_xxs;
		vertical-align: top;
	}
}

.review_summary_wrap {
	display: block;
	font-size: 0;

	.gap {
		display: inline-block;
		width: 4px;
		height: 8px;
		margin: 6px 6px 0;
		@include mixins.ratio_images('../images/ico_slash', 'png', 4px, 8px);
		vertical-align: top;
	}

	.review_quotes_text {
		margin-right: 1px;

		& + .gap {
			margin: 6px 6px 0;
		}
	}

	.rating-container {
		&.rating-sm {
			margin-top: 3px;
		}
	}

	&.type_sm {
		.gap {
			margin: 6px 6px 0;

			& + .review_quotes_text {
				margin-top: 1px;
			}
		}

		.review_quotes_text {
			& + .gap {
				margin: 6px 6px 0 5px;
			}
		}

		.rating-container {
			&.rating-sm {
				margin-top: 3px;
			}
		}

		.review_summary_desc {
			padding-top: 6px;
			margin-top: 6px;

			&:before {
				content: '';
				position: absolute;
				width: 12px;
				height: 7px;
				top: 0;
				left: 17px;
				@include mixins.ratio_images('../images/bg_review_summary', 'png', 12px, 7px);
			}

			.review_summary_inner {
				overflow: hidden;
				@include mixins.justify_width(start, start, row, true);
				padding: 8px 12px 7px;
				border-radius: 6px;
				font-size: 0;
			}

			.caption {
				flex-shrink: 0;
				@include mixins.font_size_min;
				color: variables.$textColorLightGray;

				&:after {
					content: '';
					display: inline-block;
					width: 1px;
					height: 7px;
					margin: 6px 8px 0;
					background-color: rgba(variables.$textColorBlack, .2);
					vertical-align: top;
				}
			}

			.gap {
				display: none;
			}

			.desc {
				@include mixins.ellipsis(false);
				@include mixins.font_size_xxs;
				color: variables.$textColorGray;
			}
		}
	}
}
.review_summary_desc {
	position: relative;
	padding-top: 9px;
	margin-top: 13px;

	&:before {
		content: '';
		position: absolute;
		width: 18px;
		height: 10px;
		top: 0;
		left: 41px;
		background: {
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/bg_review_summary_lg', 'png', 18px, 10px);
	}

	.review_summary_inner {
		padding: 23px 23px;
		border: 1px solid variables.$colorGray04;
		background-color: variables.$colorGray06;
		border-radius: 16px;
		box-sizing: border-box;
		max-width: 100%;
		vertical-align: top;
	}

	.prod_review{
		.review_info{
			@include mixins.justify_width(center, between);

			.user_info{
				font-size: 0;

				.info_item{
					display: inline-block;
					@include mixins.font_size_min;
					color: variables.$textColorLightGray;
					vertical-align: top;
				}

				.gap{
					display: inline-block;
					font-size: 0;
					width: 1px;
					height: 9px;
					background-color: variables.$colorGray03;
					margin: 4px 8px 0;
					vertical-align: top;
					box-sizing: border-box;
				}
			}

			.review_summary_wrap{
				margin-top: 0;
			}
		}

		.desc{
			margin-top: 10px;
			@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 3, false);
			@include mixins.font_size_xxs;
			color: variables.$textColorGray;
			white-space: normal;

			.mark{
				font-weight: variables.$fontWeightBold;
			}
		}
	}
}

.prod_recommend_text {
	position: relative;
	padding-left: 19px;
	@include mixins.font_size_xs;
