/* 할인쿠폰 */
// .coupon_wrap : 쿠폰 가운데 정렬 필요 시 사용
.coupon_wrap {
	display: block;
	text-align: center;
}
.coupon_item {
	display: inline-block;
	width: 290px;
	box-sizing: border-box;
	text-align: center;
	font-size: 0;
	vertical-align: top;

	.coupon_inner {
		display: inline-block;
		position: relative;
		width: 100%;
		height: 140px;
		text-align: left;

		// 앞면
		.coupon_front_area {
			@include mixins.justify_width;
			align-items: stretch;
			position: relative;
			height: 100%;
			vertical-align: top;
		}

		// 뒷면
		.coupon_back_area {
			@include mixins.justify_width;
			align-items: stretch;
			position: relative;
			height: 100%;
			vertical-align: top;
		}

		.left_box {
			@include mixins.justify_width(start, center, column);
			flex: 1;
			overflow: hidden;
			padding: 25px 25px 24px;
			background: #fff;
			border: 1px solid variables.$colorGray03;
			border-right: 0;
			border-radius: 20px 0 0 20px;
			box-sizing: border-box;

			.coupon_logo {
				display: inline-block;
				//width: 72px;
				height: 20px;
				margin-bottom: 11px;

				img {
					height: 100%;
				}
			}

			.e_coupon {
				position: absolute;
				color: variables.$kyoboPurple;
				background: #f5f5fb;
				border-radius: 10px;
				padding: 0 8px;
				font-size: variables.$fontSizeMin;
				line-height: 20px;
				letter-spacing: variables.$letterSpacing;
				top: 12px;
				right: 72px;
			}
			.coupon_val {
				display: block;
				width: 100%;
				color: variables.$textColorBlack;
				font-size: 44px;
				line-height: 1;
				font-weight: variables.$fontWeightBold;
				letter-spacing: variables.$letterSpacing;
				word-break: break-all;

				.unit {
					color: variables.$textColorBlack;
					font-size: variables.$fontSizeMd;
					font-weight: variables.$fontWeightRegular;
					letter-spacing: variables.$letterSpacing;
					vertical-align: 2px;
				}
			}
		}

		.right_box {
			display: inline-block;
			position: relative;
			flex: 0 60px;
			overflow: hidden;
			background-color: variables.$kyoboPurple;
			border-radius: 0 20px 20px 0;
			box-sizing: border-box;

			.img_coupon_text {
				display: block;
				width: 100%;
				height: 100%;
				background: {
					position: center;
					repeat: no-repeat;
				};
				@include mixins.ratio_images('../images/img_coupon_text', 'png', 10px, 109px);
			}

			.btn_coupon_down {
				display: inline-block;
				position: relative;
				width: 100%;
				height: 100%;
				text-align: center;

				.text {
					display: block;
					margin-top: 6px;
					color: variables.$textColorBlack;
					font-size: variables.$fontSizeMin;
					letter-spacing: variables.$letterSpacing;
					line-height: 15px;
				}

				// 아이콘
				.ico_coupon_down {
					display: inline-block;
					width: 20px;
					height: 19px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/ico_coupon_down_wh', 'png', 20px, 19px);
					vertical-align: top;
				}

				.ico_down_complete {
					display: inline-block;
					width: 15px;
					height: 14px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/ico_coupon_down_complete', 'png', 17px, 16px);
					vertical-align: top;
				}

				&[disabled], &[aria-disabled], &.disabled {
					pointer-events: none;
					cursor: default;
				}
			}
		}

		.coupon_status_box {
			display: inline-block;
			position: absolute;
			right: -12px;
			top: -12px;
			width: 54px;
			height: 54px;
			padding-top: 1px;
			border-radius: 54px;
			background-color: rgba(0, 0, 0, 0.85);
			color: #fff;
			font-size: 22px;
			line-height: 54px;
			letter-spacing: variables.$letterSpacing;
			font-weight: variables.$fontWeightBold;
			text-align: center;
			box-sizing: border-box;

			&.point {
				right: -21px;
				top: -16px;
				width: 58px;
				height: 58px;
				border-radius: 58px;
				font-size: variables.$fontSizeXxl;
				line-height: 58px;
			}
		}
	}

	.coupon_info_box {
		display: block;
		padding: 0 10px;
		text-align: left;

		.bul_list {
			margin-top: 10px;
		}
	}

	// TODO 쿠폰 sm 사이즈 가이드 나오면 재적용 필요
	&.size_sm {
		width: 216px;

		.coupon_inner {
			height: 116px;

			.left_box {
				padding: 16px 25px 15px;
				border-radius: 14px 0 0 14px;

				.coupon_logo {
					//width: 64px;
					height: 18px;
					margin-bottom: 11px;
				}

				.coupon_val {
					font-size: 32px;

					.unit {
						font-size: variables.$fontSizeSm;
					}
				}

				.coupon_title {
					margin-top: 7px;
					@include mixins.font_size_min;
				}
			}

			.right_box {
				flex: 0 40px;
				border-radius: 0 14px 14px 0;

				.img_coupon_text {
					@include mixins.ratio_images('../images/img_coupon_text_sm', 'png', 10px, 86px);
				}
			}

			.coupon_status_box {
				right: -13px;
				top: -15px;
				width: 42px;
				height: 42px;
				border-radius: 42px;
				font-size: variables.$fontSizeDef;
				line-height: 42px;
			}
		}
	}

	// 단위(원, %)가 없는 이용권, 상품권
	&.voucher {
		.coupon_inner {
			.left_box {
				.coupon_logo {
					margin-bottom: 16px;
				}

				.coupon_val {
					font-size: 34px;
				}
			}
		}

		&.size_sm {
			.coupon_inner {
				.left_box {
					.coupon_logo {
						margin-bottom: 14px;
					}

					.coupon_val {
						font-size: 26px;
					}
				}
			}
		}
	}

	// 온라인/오프라인 통합 쿠폰
	&.all {
		.coupon_inner {
			.right_box {
				background-color: #6db329;
			}
		}
	}

	// 기프트/핫트랙스
	&.gift {
		.coupon_inner {
			.right_box {
				background-color: #f08300;
			}
		}
	}

	// 톡소다
	&.tocsoda {
		.coupon_inner {
			.right_box {
				background-color: #ff432d;
			}
		}
	}

	// sam
	&.sam {
		.coupon_inner {
			.right_box {
				background-color: #1ea5a8;
			}
		}
	}

	// 비활성화
	&.disabled {
		.coupon_inner {
			.right_box {
				background-color: variables.$colorGray05;
				border: 1px solid variables.$colorGray03;
			}
		}
	}
}
.coupon_list {
