.coupon_list_title {
	display: block;
	margin-bottom: 30px;
	color: variables.$textColorBlack;
	@include mixins.font_size_sm;
	font-weight: variables.$fontWeightMedium;
	text-align: center;
}

/* 할인쿠폰 - 다운로드 타입 */
.coupon_btn_item {
	@include mixins.justify_width();
	align-items: stretch;
	position: relative;
	width: 340px;
	min-height: 153px;
	background: #fff;
	box-sizing: border-box;
	font-size: 0;
	vertical-align: top;

	&:before,
	&:after {
		content: '';
		position: absolute;
		right: 41px;
		z-index: 1;
		width: 17px;
		height: 6px;
		background: {
			repeat: no-repeat;
			position: center;
		}
	}

	&:before {
		top: 0;
		@include mixins.ratio_images('../images/ico_coupon_dent_top', 'png', 17px, 6px);
	}

	&:after {
		bottom: 0;
		@include mixins.ratio_images('../images/ico_coupon_dent_bottom', 'png', 17px, 6px);
	}

	.left_box {
		@include mixins.justify_width(start, center, column);
		flex: 1;
		flex-wrap: wrap;
		overflow: hidden;
		padding: 23px 25px 17px;
		background: #fff;
		border: 1px solid variables.$colorGray02;
		border-right: 0;
		border-radius: 10px 0 0 10px;
		box-sizing: border-box;

		.coupon_val {
			display: block;
			width: 100%;
			line-height: 1;
			word-break: break-all;

			.val {
				display: inline-block;
				color: variables.$textColorGreen;
				font-size: 34px;
				font-weight: variables.$fontWeightBold;
				vertical-align: top;

				.unit {
					color: variables.$textColorGreen;
					font-size: variables.$fontSizeMd;
					font-weight: variables.$fontWeightRegular;
					vertical-align: 2px;
				}
			}
		}

		.coupon_title {
			display: block;
			width: 100%;
			margin-top: 9px;
			color: variables.$textColorBlack;
			@include mixins.font_size_xs;
		}

		.coupon_info {
			display: block;
			width: 100%;
			margin-top: 8px;

			.coupon_info_list {
				.coupon_info_item {
					display: block;
					color: variables.$textColorLightGray;
					@include mixins.font_size_xxs;
				}
			}
		}
	}

	.right_box {
		display: inline-block;
		position: relative;
		flex: 0 50px;
		overflow: hidden;
		background: variables.$colorGray05;
		border: 1px solid variables.$colorGray02;
		border-left: 0;
		border-radius: 0 10px 10px 0;
		box-sizing: border-box;

		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 1px;
			height: calc(100% - 20px);
			margin: 10px 0;
			border-left: 1px dashed variables.$colorGray03;
			box-sizing: border-box;
		}

		.btn_coupon_down {
			display: inline-block;
			position: relative;
			width: 100%;
			height: 100%;
			background: {
				position: 15px center;
				repeat: no-repeat;
			}

			// 아이콘
			[class^=ico] {
				position: absolute;
				left: 50%;
				top: 50%;
				@include mixins.transform(translate(-50%, -50%));
			}

			.ico_down {
				width: 21px;
				height: 20px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_coupon_down', 'png', 21px, 20px);
			}

			.ico_down_done {
				width: 17px;
				height: 16px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_coupon_down_disabled', 'png', 17px, 16px);
			}

			&[disabled], &[aria-disabled], &.disabled {
				pointer-events: none;
				cursor: default;
			}
		}
	}

	&.disabled {
		opacity: 0.5;
	}
}
.coupon_btn_list {
	.coupon_btn_item {
		& ~ .coupon_btn_item {
			margin-top: 10px;
		}
	}

	& + .bul_list,
	& + [class^=bul_item_] {
		margin-top: 11px;
	}
}

/* 할인쿠폰 - 선택 타입 */
.coupon_chk_item {
	@include mixins.justify_width;
	align-items: stretch;
	position: relative;
	overflow: hidden;
	width: 340px;
	border: 1px solid variables.$colorGray03;
	border-radius: 10px;
	box-sizing: border-box;
	text-align: left;

	.coupon_chk_box {
		@include mixins.justify_width(center, center);
		flex-shrink: 0;
		width: 62px;
		background-color: variables.$colorGray06;
		text-align: center;

		& + .coupon_info_box {
			border-left: 1px solid variables.$colorGray03;
		}
	}

	.coupon_info_box {
		flex: 1 0;
		padding: 19px 20px;
		background-color: #fff;

		.coupon_name {
			display: block;
			@include mixins.font_size_sm;
			color: variables.$textColorBlack;
			font-weight: variables.$fontWeightMedium;
		}
		.coupon_desc {
			display: block;
			margin-top: 5px;
			@include mixins.font_size_xxs;
			color: variables.$textColorGray;
		}
		.coupon_period {
			display: block;
			margin-top: 12px;
			@include mixins.font_size_min;
			color: variables.$textColorPurple;
		}

		& + .coupon_chk_box {
			border-left: 1px solid variables.$colorGray03;
		}
	}
}
.coupon_chk_list {
	.coupon_chk_item {
		& ~ .coupon_chk_item {
			margin-top: 10px;
		}
	}

	& + .btn_wrap {
		margin-top: 16px;
	}
}

/* 말풍선 */
.bubble_speech_box {
	position: relative;
}

.bubble_speech_desc {
	position: absolute;
	min-height: 30px;
	padding: 6px 13px 5px;
	border-radius: 15px;
	background-color: #f7f7fb;
	border: 1px solid #b8badf;
	@include mixins.font_size_min;
	font-weight: variables.$fontWeightMedium;
	color: variables.$textColorPurple;
	box-sizing: border-box;
	text-align: center;
	box-shadow: 0 5px 10px rgba(variables.$textColorBlack, 0.1);
	white-space: nowrap;
	vertical-align: top;

	&:before {
		content: '';
		position: absolute;
		display: block;
		width: 10px;
		height: 7px;
		@include mixins.ratio_images('../images/bg_bubble_speech', 'png', 10px, 7px);
	}

	&.type_primary {
		background-color: #f6f6fa;
		border: 1px solid variables.$kyoboPurple;

		&:before {
			@include mixins.ratio_images('../images/bg_bubble_speech_primary', 'png', 10px, 7px);
		}
	}

	&:not([class*=arw_]):before {
		top: -7px;
		left: 50%;
		@include mixins.transform(translateX(-50%));
	}

	&.arw_mid_bottom:before {
		left: 50%;
		bottom: -7px;
		@include mixins.transform(translateX(-50%) rotate(-180deg));
	}

	&.arw_left_top:before {
		top: -7px;
		left: 24px;
	}

	&.arw_right_bottom:before {
		right: 24px;
		bottom: -7px;
		@include mixins.transform(rotate(-180deg));
	}

	.ico_showing {
		display: inline-block;
		width: 20px;
		height: 13px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		margin-right: 2px;
		@include mixins.ratio_images('../images/ico_showing_purple', 'png', 20px, 13px);
		vertical-align: text-top;
	}
}

/* no data */
.no_data {
	@include mixins.justify_width(center, center, column);
	width: 100%;
	// dialog_sm 팝업에서는 100px 0으로 적용
	padding: 180px 0;

	.no_data_desc {
		display: block;
		@include mixins.font_size_xs;
		color: variables.$textColorLightGray;
		text-align: center;

		&:before {
			content: '';
			display: block;
			width: 40px;
			height: 40px;
			margin: 0 auto 12px;
			@include mixins.ratio_images('../images/ico_nodata', 'png', 40px, 40px);
		}
	}

	&.size_sm {
		// 상품상세, 장바구니에서 60px로 작업되어 우선적용함
		padding: 60px 0;

		.no_data_desc {
			@include mixins.font_size_xxs;

			&:before {
				width: 30px;
				height: 30px;
				margin-bottom: 10px;
				@include mixins.ratio_images('../images/ico_nodata_sm', 'png', 30px, 30px);
			}
		}
	}

	&.type_gray {
		padding: 60px 0;
		background-color: variables.$colorGray06;
		border-radius: 20px;
		overflow: hidden;
	}

	&.has_desc_lg {
		.no_data_desc_lg {
			@include mixins.font_size_sm;
			text-align: center;

			&:before {
				content: '';
				display: block;
				width: 40px;
				height: 40px;
				margin: 0 auto 12px;
				@include mixins.ratio_images('../images/ico_nodata', 'png', 40px, 40px);
			}

			+.no_data_desc {
				margin-top: 12px;
			}
		}

		.no_data_desc {
			&:before {
				content: none;
			}
		}

		.btn_wrap {
			margin-top: 30px;
			width: 250px;

			[class^=btn_] {
				& + [class^=btn_] {
					margin-top: 10px;
				}
			}
		}
	}

	&.has_line {
		border-top: 1px solid variables.$colorGray03;
		border-bottom: 1px solid variables.$colorGray04;
	}

	&.has_black_line {
		border-top: 1px solid #000;
		border-bottom: 1px solid variables.$colorGray04;
	}
	
	&.has_bottom_line {
		border-bottom: 1px solid variables.$colorGray04;
	}
}

/* 리뷰/댓글 상단 유저 정보 영역 */
.user_info_box {
	display: block;
	color: variables.$textColorLightGray;
	font-size: 0;

	.badge_box {
		display: inline-block;
		margin-right: 6px;
		vertical-align: top;

		.badge_sm {
			& + .badge_sm {
				margin-left: 4px;
			}
		}

		& ~ .info_item {
			padding-top: 1px;
		}

		& ~ .gap {
			margin: 6px 6px 0 8px;
		}
	}

	.info_item {
		display: inline-block;
		@include mixins.font_size_min;
		vertical-align: top;

		.btn_comment_util {
			.text {
				display: inline-block;
				position: relative;
				text-align: left;
			}

			&:hover, &:focus, &:active {
				.text {
					text-decoration: underline;
				}
			}
		}
	}

	.gap {
		display: inline-block;
		font-size: 0;
		width: 1px;
		height: 9px;
		background-color: variables.$colorGray03;
		margin: 5px 6px 0 8px;
		vertical-align: top;
		box-sizing: border-box;
	}
}

/* 리뷰(댓글) */
.comment_wrap {
	display: block;
	word-break: break-all;

	// 리뷰(댓글) 상태 박스
	.comment_status_box {
		display: block;
		overflow: hidden;
		width: 100%;
		padding: 19px 15px;
		background-color: variables.$colorGray06;
		border-radius: 6px;
		text-align: center;
		font-size: 0;
		box-sizing: border-box;

		[class^=ico_] {
			display: inline-block;
			margin: 2px 7px 0 0;
			vertical-align: top;
		}

		.ico_spoiler {
			width: 17px;
			height: 19px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_locker', 'png', 17px, 19px);
		}

		.ico_report {
			width: 17px;
			height: 17px;
			margin: 2px 6px 0 0;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_warning_gray', 'png', 17px, 17px);
		}

		.comment_status_text {
			position: relative;
			display: inline-block;
			color: variables.$textColorLightGray;
			@include mixins.font_size_xs;
			vertical-align: top;

			.btn_xxs {
				margin-left: 3px;
				vertical-align: 0;
			}
		}
	}

	// 리뷰(댓글) 리스트
	.comment_list {
		display: block;
		border-top: 1px solid variables.$colorGray04;
		border-bottom: 1px solid variables.$colorGray04;

		.comment_item {
			display: block;
			position: relative;
			padding: 20px 10px;
			box-sizing: border-box;

			& ~ .comment_item {
				border-top: 1px solid variables.$colorGray04;
			}

			.comment_header {
				@include mixins.justify_width(center, between);
				margin-bottom: 16px;

				.left_area {
					flex: 1 0;
				}

				.right_area {
					flex: 0 auto;
				}
			}

			.comment_contents {
				display: block;

				.comment_contents_inner {
					max-height: variables.$fontSizeXsLineHeight * 3;
					overflow: hidden;

					.comment_view_wrap {
						@include mixins.justify_width();
						align-items: stretch;

						.comment_text_box {
							display: block;
							flex: 1;
							width: 100%;
							padding-top: 1px;

							.comment_text {
								color: variables.$textColorBlack;
								@include mixins.font_size_xs;
								@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 3, false);
							}
						}

						.comment_thumb_box {
							display: block;
							position: relative;
							flex: 0 auto;
							overflow: hidden;
							width: 64px;
							height: 64px;
							margin-left: 60px;
							border-radius: 6px;

							img {
								width: 100%;
							}

							.comment_thumb_view {
								display: block;
								position: relative;
								z-index: 1;
								width: 100%;
								height: 100%;
								background: {
									size: cover;
									position: center;
									repeat: no-repeat;
								};

								img {
									display: none;
								}
							}

							.thumb_count {
								display: inline-block;
								position: absolute;
								bottom: 0;
								right: 0;
								z-index: 2;
								width: 16px;
								height: 16px;
								padding-top: 1px;
								font-size: 11px;
								line-height: 16px;
								letter-spacing: variables.$letterSpacing;
								color: #fff;
								background-color: rgba(0, 0, 0, .85);
								text-align: center;
								border-radius: 6px 0 0 0;
								box-sizing: border-box;
							}
						}
					}

					.comment_swiper_wrap {
						display: block;
						position: relative;
						margin-top: 16px;
						box-sizing: border-box;

						.swiper-container {
							.swiper-wrapper {
								.swiper-slide {
									width: 300px;
									height: 300px;
									margin-right: 20px;

									&:last-child {
										margin-right: 0;
									}

									.comment_img_box {
										display: block;
										width: 100%;
										height: 100%;
										background-color: #000;
										border-radius: 10px;
										overflow: hidden;
										background: {
											repeat: no-repeat;
											size: contain;
											position: center;
										};

										.btn_view_img {
											display: block;
											width: 100%;
											height: 100%;
										}

										img {
											display: none;
										}
									}
								}
							}
						}

						.swiper-button-next {
							right: -22px;
						}

						.swiper-button-prev {
							left: -22px;
						}
					}
				}
			}

			.comment_footer {
				display: block;
				position: relative;
				margin-top: 12px;

				.btn_more_body {
					display: none;
				}

				// 리뷰(댓글) 하단 버튼 영역
				.comment_util_box {
					@include mixins.justify_width(center, between);

					.left_area {
						display: none;
						flex: 0 auto;
						font-size: 0;
					}

					.right_area {
						flex: 0 auto;
						margin-left: auto;
						font-size: 0;
					}

					.btn_like {
						& + .btn_reply {
							margin-left: 16px;
						}
					}
				}
			}

			// 넘친 경우 (텍스트 3줄 이상 or 포토리뷰(swiper))
			&.overflow {
				.comment_footer {
					.btn_more_body {
						display: inline-block;
					}

					.comment_util_box {
						position: absolute;
						right: 0;
						bottom: 0;
						z-index: 2;
					}
				}
			}

			// 펼쳐진 경우 (텍스트 3줄 이상 or 포토리뷰(swiper))
			&.active {
				.comment_contents {
					.comment_contents_inner {
						max-height: none;
						overflow: visible;

						.comment_view_wrap {
							.comment_text_box {
								.comment_text {
									@include mixins.clear_multi_ellipsis;
								}
							}

							.comment_thumb_box {
								display: none;
							}
						}
					}
				}
			}

			// 한달 후/이 구매자의 첫 리뷰 보기 가지고 있는 경우
			&.has_btn {
				.comment_footer {
					.comment_util_box {
						margin-top: 13px;

						.left_area {
							display: block;
						}
					}
				}

				&.overflow {
					.comment_footer {
						.comment_util_box {
							position: static;
						}
					}
				}
			}
		}
	}

	// 제목
	&.has_title {
		.title_wrap {
			margin: -20px -10px 20px;
			padding: 20px 0;
			border-bottom: 1px solid variables.$colorGray04;

			.title_heading {
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 3;
				-webkit-box-orient: vertical;
				max-height: 69px;
			}
		}
		.comment_list {
			.comment_item {
				&  ~ .comment_item {
					border-color: variables.$colorGray03;
				}
			}
		}
	}
}
.tab_wrap {
