/* 카테고리 태그 */
.category_tag_list {
	.category_item {
		display: inline-block;
		margin-right: 30px;
		vertical-align: top;

		.btn_category {
			position: relative;
			@include mixins.font_size_sm;
			color: variables.$textColorLightGray;
			@include mixins.transition(color .23s, font-weight .23s);

			&:before {
				content: '';
				position: absolute;
				left: 50%;
				top: -8px;
				display: block;
				width: 6px;
				height: 6px;
				background-color: transparent;
				border-radius: 100%;
				@include mixins.transform(translateX(-50%));
				@include mixins.transition(background-color .23s);
			}
		}

		&:last-child {
			margin-right: 0;
		}

		&.active {
			.btn_category {
				overflow: visible;
				font-weight: variables.$fontWeightBold;
				color: variables.$textColorPurple;

				&:before {
					background-color: variables.$kyoboGreen;
				}
			}
		}
	}

	&.type_white{
		.category_item{
			.btn_category{
				color: #fff;
				opacity: .5;
			}

			&.active{
				.btn_category {
					opacity: 1;
				}
			}
		}
	}
}

// 비밀번호 유효성 검사
.pw_valid_wrap {
	margin-top: 6px;

	.valid_step_box {
		position: relative;
		@include mixins.justify_width(center);

		.valid_step_list {
			font-size: 0;
			height: 3px;

			.step_item {
				display: inline-block;
				width: 80px;
				height: 3px;
				background-color: variables.$colorGray04;
				border-radius: 2px;
				vertical-align: top;

				/*&:before {
					content: '';
					position: absolute;
					width: 10px;
					height: 7px;
					left: 0;
					top: 3px;
					@include mixins.ratio_images('../images/arw_valid_step', 'png', 10px, 7px);
				}*/

				+ .step_item {
					margin-left: 3px;
				}
			}
		}

		.bubble_speech_box {
			margin-left: auto;

			.valid_state {
				display: block;
				@include mixins.font_size_xxs;
			}
		}

		&.valid_fail {
			.valid_state {
				color: variables.$textColorBlack;
			}
		}

		&.valid_warning {
			.valid_step_list {
				.step_item {
					&:first-child {
						background-color: variables.$textColorRed;
					}
				}
			}

			.valid_state {
				color: variables.$textColorRed;
			}
		}

		&.valid_normal {
			.valid_step_list {
				.step_item {
					&:nth-child(-n + 2) {
						background-color: #ffbf00;
					}
				}
			}

			.valid_state {
				color: variables.$textColorBlack;
			}
		}

		&.valid_success {
			.valid_step_list {
				.step_item {
					background-color: variables.$kyoboGreen;
				}
			}

			.valid_state {
				color: variables.$kyoboGreen;
			}
		}
	}

	.pw_valid_list {
		margin-top: 4px;

		.pw_valid_item {
			padding-left: 14px;
			@include mixins.font_size_xxs;
			@include mixins.ratio_images('../images/arw_valid_step', 'png', 10px, 7px);
			background: {
				position: left center;
				repeat: no-repeat;
			}
			color: variables.$textColorLightGray;

			+ .pw_valid_item {
				margin-top: 6px;
			}

			&.valid_success {
				@include mixins.ratio_images('../images/arw_valid_step_success', 'png', 10px, 7px);
				color: variables.$textColorGreen;
			}

			&.valid_fail {
				@include mixins.ratio_images('../images/arw_valid_step_fail', 'png', 10px, 7px);
				color: variables.$textColorRed;
			}
		}
	}
}

