/* 결제정보 가격 리스트 */
.payment_price_wrap	{
	display: block;
	box-sizing: border-box;
	word-break: break-all;

	.payment_price_list {
		display: block;
		padding: 20px 0;
		border-top: 1px solid #000;
		border-bottom: 1px solid variables.$colorGray04;
		box-sizing: border-box;

		.payment_price_item {
			@include mixins.justify_width;
			padding: 0 10px;

			& ~ .payment_price_item {
				margin-top: 20px;
			}

			.label {
				display: inline-block;
				flex-shrink: 0;
				@include mixins.font_size_xs;
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;
				text-align: left;
				box-sizing: border-box;
				vertical-align: top;

				.required {
					display: inline-block;
					width: 6px;
					height: 6px;
					padding-left: 3px;
					margin-top: 5px;
					vertical-align: top;

					&:before {
						content: '*';
						display: inline-block;
						position: relative;
						width: 6px;
						height: 6px;
						@include mixins.ratio_images('../images/ico_required', 'png', 6px, 6px);
						color: transparent;
					}
				}

				.bank_account_box {
					display: inline-block;
					@include mixins.font_size_xs;
					color: variables.$textColorBlack;
					font-weight: variables.$fontWeightMedium;
					vertical-align: top;

					.bank_number {
						display: block;
						margin-top: 5px;
						font-size: variables.$fontSizeDef;
						font-weight: variables.$fontWeightBold;
						letter-spacing: variables.$letterSpacing;
						line-height: 1;
						vertical-align: top;
