// 문장수집
.killing_part_contents {
	&.killing_part_swiper {
		position: relative;
		box-sizing: border-box;

		.swiper-wrapper {
			.swiper-slide {
				position: relative;
				width: calc((100% - 20px) / 2);
				margin-right: 20px;
				text-align: center;
				box-sizing: border-box;


			}
		}
		.swiper-button-prev {
			left: -22px;
		}

		.swiper-button-next {
			right: -22px;
		}
	}

	.killing_part_bg {
		display: block;
		position: relative;
		width: 100%;
		height: 397px;
		overflow: hidden;
		padding: 13px 12px 24px 20px;
		border-radius: 20px;
		box-sizing: border-box;
		background: {
			position: center;
			repeat: no-repeat;
		};

		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			background: rgba(#000, 0.4);
		}
	}

	.killing_part_bg01 {
		@include mixins.ratio_images('../images/bg_killing_part_01', 'png', cover);
	}

	.killing_part_bg02 {
		@include mixins.ratio_images('../images/bg_killing_part_02', 'png', cover);
	}

	.slide_inner_top {
		@include mixins.justify_width(center, between);
		position: relative;
		z-index: 1;
		margin-bottom: 10px;

		.btn_like {
			.ico_like {
				width: 20px;
				height: 20px;
				background-repeat: no-repeat;
				@include mixins.ratio_images('../images/ico_like_wh', 'png', 20px, 20px);
				margin-right: 4px;
			}

			.text {
				position: relative;
				@include mixins.font_size_xxs;
				display: inline-block;
				vertical-align: middle;
				font-weight: variables.$fontWeightBold;
				color: #fff;
			}

			&.active {
				.ico_like {
					@include mixins.ratio_images('../images/ico_like_wh_active', 'png', 20px, 20px);
				}
			}
		}

		.sns_share_wrap {
			&.size_sm {
				.btn_share {
					border: 0;

					.ico_share {
						width: 20px;
						height: 20px;
						margin-top: 7px;
						background: {
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/btn_share_killing_part', 'png', 20px, 20px);
					}
				}
			}
		}
	}

	.slide_inner_middle {
		@include mixins.justify_width(center, center, column);
		position: relative;
		z-index: 1;
		padding: 0 18px 0 10px;
		height: calc(100% - 36px - 34px - 30px);
		box-sizing: border-box;

		.custom_scroll_wrap {
			height: auto;
			max-height: 100%;

			&.active {
				padding-right: 5px;
			}
		}

		.text_killing_part {
			color: #fff;
			@include mixins.font_size_xs;
		}

		.from_page {
			margin-top: 12px;
			color: rgba(255, 255, 255, .6);
			@include mixins.font_size_xxs;
		}
	}

	.btn_wrap {
		display: block;
		position: relative;
		z-index: 1;
		margin-top: 24px;
	}

	.btn_killing_part_down {
		display: inline-block;
		position: relative;
		padding: 5px 10px 7px;
		border: 1px solid rgba(255, 255, 255, .4);
		border-radius: 6px;
		vertical-align: top;
		box-sizing: border-box;

		.text {
			display: inline-block;
			position: relative;
			padding-left: 15px;
			color: #fff;
			@include mixins.font_size_min;

			&:before {
				content: '';
				display: inline-block;
				position: absolute;
				top: 0;
				left: 0;
				width: 10px;
				height: 18px;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_download', 'png', 10px, 18px);
			}
		}
	}

	.review_user_info {
		display: block;
		margin-top: 18px;
		@include mixins.font_size_min;
		color: variables.$textColorLightGray;
		text-align: center;

		.gap {
			display: inline-block;
			font-size: 0;
			width: 1px;
			height: 9px;
			background-color: variables.$colorGray03;
			margin: 0 6px;
		}
	}

	&.has_title {
		border: 1px solid variables.$colorGray04;
		border-radius: 20px;

		.killing_part_bg {
			height: 376px;
			border-bottom-right-radius: 0;
			border-bottom-left-radius: 0;
		}

		.review_user_info {
			margin: 0;
			padding: 18px 30px 20px;
			text-align: left;
		}
		.title_wrap {
			padding: 20px 30px;
			border-top: 1px solid variables.$colorGray04;

			.title_heading {
				overflow: hidden;
				display: -webkit-box;
				max-height: 69px;
				-webkit-line-clamp: 3;
				-webkit-box-orient: vertical;
				text-overflow: ellipsis;
			}
		}
	}
