@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
/* 상품 리스트 */
.prod_list {
	font-size: 0;

	.prod_item {
		display: inline-block;
		width: map.get(variables.$prodProps, 'thumb-v-lg');
		margin-left: 36px;
		vertical-align: top;

		.form_chk, .form_rdo {
			margin-bottom: 10px;
		}

		&:nth-child(6n+1) {
			margin-left: 0;
		}

		&:nth-child(n+7) {
			margin-top: 40px;
		}
	}

	&.type_bookcast{
		.prod_item {
			width: calc((100% - 144px) / 5);
			margin-left: 36px;

			&:nth-child(5n+1) {
				margin-left: 0;
			}

			&:nth-child(n+6) {
				margin-top: 60px;
			}
		}

		&.col4 {
			.prod_item {
				width: calc((100% - 108px) / 4);

				&:nth-child(5n+1) {
					margin-left: 36px;
				}

				&:nth-child(4n+1) {
					margin-left: 0;
				}

				&:nth-child(n+5) {
					margin-top: 60px;
				}
			}
		}

		&.col3 {
			.prod_item {
				width: calc((100% - 72px) / 3);

				&:nth-child(5n+1) {
					margin-left: 36px;
				}

				&:nth-child(3n+1) {
					margin-left: 0;
				}

				&:nth-child(n+4) {
					margin-top: 60px;
				}
			}
		}

		&.col2 {
			.prod_item {
				width: calc((100% - 36px) / 2);

				&:nth-child(5n+1) {
					margin-left: 36px;
				}

				&:nth-child(2n+1) {
					margin-left: 0;
				}

				&:nth-child(n+3) {
					margin-top: 60px;
				}
			}
		}

		&.swiper-wrapper {
			.prod_item {
				margin-top: 0 !important;
				margin-left: 0 !important;
				margin-right: 36px;

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	&.col5 {
		.prod_item {
			margin-left: 36px;

			&:nth-child(5n+1) {
				margin-left: 0;
			}

			&:nth-child(n+6) {
				margin-top: 40px;
			}
		}
	}
}

.prod_row_list {
	.prod_item {
		~ .prod_item {
			margin-top: 20px;
		}
	}

	&.has_line{
		&:not(.col2){
			.prod_item {
				~ .prod_item {
					border-top: 1px solid variables.$colorGray04;
					padding-top: 20px;
				}
			}
		}

		.prod_area {
			align-items: flex-start;
		}
	}

	&.col2{
		font-size: 0;

		.prod_item {
			display: inline-block;
			width: calc((100% - 56px) / 2);
			vertical-align: top;

			~ .prod_item {
				margin-top: 0;
			}

			&:nth-child(2n){
				padding-left: 56px;
			}

			&:nth-child(n+3){
				margin-top: 24px;
				padding-top: 24px;
			}

			&:nth-child(2n+1){
				position: relative;

				&:before{
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					width: 984px;
					height: 1px;
					background-color: variables.$colorGray04;
				}
			}

			&:first-child{
				&:before{
					content: none;
				}
			}
		}

		&.no_line {
			.prod_item {
				padding-top: 0;

				&:nth-child(n+3){
					margin-top: 0;
					padding-top: 40px;
				}

				&:before{
					content: none;
				}
			}
		}
	}
	&.type_round{
		.prod_item {
			~ .prod_item {
				margin-top: 24px;
			}
		}
	}

	&.align_top {
		.prod_area {
			align-items: flex-start;

			.prod_info_box {
				padding-top: 10px;
			}
		}
	}
}

/* 상품 아이템 */
.prod_area {
	.prod_thumb_box {
		position: relative;
		overflow: hidden;
		width: map.get(variables.$prodProps, 'thumb-v-def');
		@include mixins.focus_within;

		.prod_link {
			display: block;
			height: 100%;

			&.disabled{
				pointer-events: none;
			}
		}

		.prod_viewer_control {
			margin-top: 10px;
			font-size: 0;
			text-align: center;

			.btn_prod_viewer {
				display: inline-block;
				vertical-align: top;

				[class^=ico_] {
					display: inline-block;
					width: 12px;
					height: 18px;
					margin-right: 3px;
					vertical-align: top;

					&.ico_preview {
						@include mixins.ratio_images('../images/ico_prod_preview', 'png', 12px, 18px);
					}

					&.ico_listening {
						width: 14px;
						@include mixins.ratio_images('../images/ico_listening', 'png', 14px, 18px);
					}

					&.ico_blank {
						@include mixins.ratio_images('../images/ico_blank_link', 'png', 12px, 18px);
					}
				}

				.text {
					@include mixins.transform(translateY(1px));
					@include mixins.font_size_min;
					color: variables.$textColorLightGray;
				}

				~ .btn_prod_viewer {
					&:before {
						content: '';
						display: inline-block;
						width: 1px;
						height: 7px;
						margin: 5px 10px 0;
						background-color: variables.$colorGray04;
						vertical-align: top;
					}
				}
			}
		}

		img {
			width: 100%;
		}

		.img_box {
			overflow: hidden;
			position: relative;
			display: block;
			box-sizing: border-box;

			&:before{
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border: 1px solid variables.$colorGray04;
				box-sizing: border-box;
			}

			img {
				width: 100%;
				max-height: calc(#{map.get(variables.$prodProps, 'thumb-v-def')} * 1.618 - 10px);
			}

			&.video {
				&:after {
					content: '';
					position: absolute;
					top: 50%;
					left: 50%;
					display: block;
					width: 42px;
					height: 42px;
					@include mixins.ratio_images('../images/ico_prod_play', 'png', 42px, 42px);
					@include mixins.transform(translate(-50%, -50%));
				}
			}

			&.thesis_no_img{
				&:before{
					content: none;
				}
			}
		}

		.btn_wish {
			display: block;
			position: absolute;
			right: 7px;
			bottom: 7px;
			width: 30px;
			height: 30px;
			border: none;
			background: {
				repeat: no-repeat;
				position: center;
			}
			background-color: transparent;
			@include mixins.ratio_images('../images/ico_cast_heart_h15', 'png', 20px, 20px);

			&.active {
				@include mixins.ratio_images('../images/ico_cast_heart_h15_active', 'png', 20px, 20px);
			}
		}

		.btn_prod_detail {
			display: inline-block;
			position: absolute;
			right: 8px;
			bottom: 8px;
			width: 30px;
			height: 30px;
			background: {
				repeat: no-repeat;
				position: center;
			};
			@include mixins.ratio_images('../images/ico_prod_detail', 'png', 30px, 30px);
			border-radius: 50%;
			box-sizing: border-box;
		}

		.thumb_flag_group {
			position: absolute;
			left: 0;
			top: 0;
			z-index: 2;
			max-width: 100%;

			.flag {
				display: inline-block;
				padding: 4px 10px 5px;
				@include mixins.font_size_xxs;
				@include mixins.ellipsis(false);
				color: #fff;
				border-radius: 10px 0 16px 0;
				background-color: rgba(variables.$textColorBlack, .5);
				vertical-align: top;
				box-sizing: border-box;

				&.type_gray {
					background-color: rgba(variables.$textColorGray, .85);
				}

				&.type_green {
					background-color: rgba(variables.$kyoboGreen, .85);
				}
			}
		}

		&.type_cover {
			.img_box {
				border-radius: 10px;
				padding-top: 100%;
				background: {
					repeat: no-repeat;
					size: cover;
					position: center;
				}

				img {
					position: absolute;
					visibility: hidden;
					overflow: hidden;
					height: 0;
					line-height: 0;
					width: 0;
					margin: 0;
					padding: 0;
					font: 0/0 Arial;
				}

				&:before{
					//content: none;
					border-radius: 10px;
				}
			}

			&.size_xs {
				.img_box {
					border-radius: 12px;

					&:before{
						border-radius: 12px;
					}
				}
			}

			&.size_sm {
				.img_box {
					border-radius: 14px;

					&:before{
						border-radius: 14px;
					}
				}
			}

			&.size_lg {
				.img_box {
					border-radius: 16px;

					&:before{
						border-radius: 16px;
					}
				}
			}

			&.size_xl {
				.img_box {
					border-radius: 20px;

					&:before{
						border-radius: 20px;
					}
				}
			}
		}

		&.type_contain {
			.img_box {
				padding-top: 100%;
				background: {
					repeat: no-repeat;
					size: contain;
					position: center;
				}

				img {
					position: absolute;
					visibility: hidden;
					overflow: hidden;
					height: 0;
					line-height: 0;
					width: 0;
					margin: 0;
					padding: 0;
					font: 0/0 Arial;
				}
			}
		}

		&.type_bookcover{
			.img_box {
				border-radius: 16px 16px 16px 0;

				&:before{
					border-radius: 16px 16px 16px 0;
				}
			}

			&.size_xs {
				.img_box {

					border-radius: 12px 12px 12px 0;
					&:before{
						border-radius: 12px 12px 12px 0;
					}
				}
			}

			&.size_sm {
				.img_box {
					border-radius: 14px 14px 14px 0;

					&:before{
						border-radius: 14px 14px 14px 0;
					}
				}
			}

			&.size_lg {
				.img_box {
					border-radius: 16px 16px 16px 0;

					&:before{
						border-radius: 16px 16px 16px 0;
					}
				}
			}

			&.size_xl {
				.img_box {
					border-radius: 20px 20px 20px 0;

					&:before{
						border-radius: 20px 20px 20px 0;
					}
				}
			}
		}

		&.type_square_round{
			.img_box {
				border-radius: 16px;

				img{
					max-height: none !important;
				}

				&:before{
					border-radius: 16px;
				}
			}

			&.size_xs {
				.img_box {
					border-radius: 12px;

					&:before{
						border-radius: 12px;
					}
				}
			}

			&.size_sm {
				.img_box {
					border-radius: 14px;

					&:before{
						border-radius: 14px;
					}
				}
			}

			&.size_lg {
				.img_box {
					border-radius: 16px;

					&:before{
						border-radius: 16px;
					}
				}
			}

			&.size_xl {
				.img_box {
					border-radius: 20px;

					&:before{
						border-radius: 20px;
					}
				}
			}
		}

		&.type_gift_card{
			.img_box{
				&:before{
					content: none;
				}
			}
		}

		&.type_cover_circle {
			margin: 0 auto;

			.img_box {
				border-radius: 100%;
				padding-top: 100%;
				background: {
					repeat: no-repeat;
					size: cover;
					position: center;
				}

				img {
					position: absolute;
					visibility: hidden;
					overflow: hidden;
					height: 0;
					line-height: 0;
					width: 0;
					margin: 0;
					padding: 0;
					font: 0/0 Arial;
				}

				&:before{
					//content: none;
					border-radius: 100%;
				}
			}

			~ .prod_info_box{
				text-align: center;
			}
		}

		&.size_xs {
			width: map.get(variables.$prodProps, 'thumb-v-xs');

			.img_box{
				img{
					max-height: calc(#{map.get(variables.$prodProps, 'thumb-v-xs')} * 1.618 - 10px);
				}
			}
		}

		&.size_sm {
			width: map.get(variables.$prodProps, 'thumb-v-sm');

			.img_box {
				img {
					max-height: calc(#{map.get(variables.$prodProps, 'thumb-v-sm')} * 1.618 - 10px);
				}
			}
		}

		&.size_lg {
			width: map.get(variables.$prodProps, 'thumb-v-lg');

			.img_box {
				img {
					max-height: calc(#{map.get(variables.$prodProps, 'thumb-v-lg')} * 1.618 - 10px);
				}
			}
		}

		&.size_xl {
			width: map.get(variables.$prodProps, 'thumb-v-xl');

			.img_box {
				img {
					max-height: calc(#{map.get(variables.$prodProps, 'thumb-v-xl')} * 1.618 - 10px);
				}
			}
		}
	}

	.prod_info_box {


		.prod_rank{
			@include mixins.justify_width(end, between);
			margin: {
				top: -10px;
				bottom: 5px;
			}

			.badge_flag{
				margin-bottom: 0;
			}


		}

		// 정가인하 상단 할인율 영역
		.prod_cut_info {
			display: block;
			margin-bottom: 12px;
			font-size: 0;

			.percent {
				display: inline-block;
				width: 34px;
				height: 26px;
				padding: 5px 0 0 1px;
				margin-right: 3px;
				vertical-align: top;
				background: {
					repeat: no-repeat;
				};
				@include mixins.ratio_images('../images/bg_prod_cut_info', 'png', 34px, 26px);
				text-align: center;
				box-sizing: border-box;
				font-size: 0;

				.val, .unit {
					display: inline-block;
					@include mixins.font_size_min;
					line-height: 1;
					color: #fff;
					font-weight: variables.$fontWeightBold;
					vertical-align: top;
				}

				.unit {
					font-size: 8px;
					margin-top: 2px;
				}
			}
			.text {
				display: inline-block;
				margin-top: 4px;
				@include mixins.font_size_min;
				color: variables.$textColorGreen;
				font-weight: variables.$fontWeightBold;
				vertical-align: top;
			}
		}

		// 베스트 순위, 배송 뱃지
		.badge_flag, .badge_sm {
			margin-bottom: 5px;
		}

		.prod_info {
			display: block;
			height: 100%;
			font-size: 0;

			&.disabled{
				pointer-events: none;
			}
		}

		a.prod_info {
			&:hover, &:active, &:focus {
				.prod_name{
					text-decoration: underline;
				}
			}
		}

		.prod_category {
			display: block;
			margin-bottom: 5px;
			@include mixins.font_size_min;
			color: variables.$textColorLightGray;

			&.fc_spot, &.fc_green {
				font-weight: variables.$fontWeightMedium;
			}
		}

		.prod_category_box {
			display: block;
			margin-bottom: 5px;

			.prod_category {
				display: inline-block;
				margin-bottom: 0;
				@include mixins.font_size_xxs;
				vertical-align: top;

				&.fc_spot, &.fc_green {
					font-weight: variables.$fontWeightBold;
				}
			}

			.prod_view {
				display: none;
			}
		}

		.prod_culture_category {
			display: block;
			margin-bottom: 5px;
			@include mixins.font_size_min;
			color: variables.$textColorLightGray;
		}

		.prod_required {
			display: block;
			margin-bottom: 3px;
			@include mixins.font_size_min;
			color: variables.$textColorGray;
		}

		.prod_language {
			display: block;
			margin-bottom: 5px;
			@include mixins.font_size_xxs;
			color: variables.$textColorGray;
		}

		.prod_name {
			@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 2, false);
			@include mixins.font_size_sm;
			font-weight: variables.$fontWeightMedium;

			&.h_fixed {
				height: calc(#{variables.$fontSizeSmLineHeight} * 2);
			}

			&.ellipsis_single {
				@include mixins.ellipsis(false);
			}

			&.ellipsis_none {
				height: auto !important;
				max-height: none !important;
				-webkit-box-orient: inherit;
				-webkit-line-clamp: inherit;
			}
		}

		.prod_multi_count{
			@include mixins.justify_width;
			overflow: hidden;

			.prod_count{
				flex-shrink: 0;
				margin-left: 4px;
				@include mixins.font_size_sm;
				font-weight: variables.$fontWeightMedium;
			}
		}

		.prod_author {
			display: block;
			margin-top: 5px;
			@include mixins.font_size_xxs;
			color: variables.$textColorGray;
			@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 2, false);
		}

		.prod_option {
			margin-top: 4px;
			font-size: 0;

			> .text, .val {
				display: inline-block;
				@include mixins.font_size_xs;
				color: variables.$textColorGray;
				vertical-align: top;
			}

			.val {
				margin-left: 3px;
			}

			.btn_xxs {
				margin-left: 7px;
			}

			& + .prod_option {
				margin-top: 4px;
			}
		}

		.prod_price {
			display: block;
			margin-top: 12px;
			font-size: 0;

			.badge_flag {
				margin-right: 6px;
				margin-bottom: 0;

				~ .percent, ~ .price {
					padding-top: 1px;
				}
			}

			.percent {
				display: inline-block;
				margin-right: 3px;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightBold;
				color: variables.$kyoboGreen;
				vertical-align: top;
			}

			.price {
				display: inline-block;
				font-size: 0;
				vertical-align: top;

				.val, .unit {
					@include mixins.font_size_xs;
				}

				.val {
					font-weight: variables.$fontWeightBold;
				}
			}

			.price_normal {
				display: inline-block;
				margin: 3px 0 0 2px;
				font-size: 0;
				vertical-align: top;

				.val {
					@include mixins.font_size_min;
					color: variables.$textColorLightGray;
				}
			}

			.point {
				display: inline-block;
				padding: 3px 0 0 3px;
				@include mixins.font_size_min;
				color: variables.$textColorGray;
				vertical-align: top;
			}

			.gap {
				font-size: 0;

				+ .point {
					padding: 2px 0 0;

					&:before {
						content: '';
						display: inline-block;
						width: 1px;
						height: 7px;
						margin: 6px 6px 0;
						background-color: variables.$colorGray03;
						vertical-align: top;
					}
				}
			}

			&.type_cut {
				.price_info {
					font-size: 0;

					.price_normal {
						margin: 0 3px 0 0;

						.text {
							padding-right: 2px;
							color: variables.$textColorLightGray;
						}

						.text, .val, .unit {
							@include mixins.font_size_min;
						}
					}

					.price {
						margin-right: 3px;

						.val, .unit {
							@include mixins.font_size_min;
							color: variables.$textColorGray;
						}

						.val {
							font-weight: variables.$fontWeightRegular;
						}

						& + .price_normal {
							margin: 0;
						}
					}

					.percent {
						margin-right: 0;
						@include mixins.font_size_min;
						color: variables.$textColorGray;
						font-weight: variables.$fontWeightRegular;

						// TODO 필요없으면 삭제
						& + .price {
							margin-left: 3px;
						}
					}

					.price_cut {
						display: inline-block;
						vertical-align: top;

						.text, .val, .unit, .percent {
							@include mixins.font_size_xs;
						}

						.text{
							padding-right: 3px;
						}

						.val {
							font-weight: variables.$fontWeightBold;
						}

						.percent {
							margin-right: 3px;
							color: variables.$textColorGreen;
							font-weight: variables.$fontWeightBold;
						}
					}

					.point {
						padding-top: 3px;
						@include mixins.font_size_min;

						&:before {
