			.prod_register {
				margin-top: 6px;

				span {
					position: relative;
					margin: 0 0 0 3px;
					padding: 0 0 0 7px;
					@include mixins.font_size_xs;
					vertical-align: top;

					&:after {
						display: block;
						content: '';
						position: absolute;
						left: 0;
						top: 10px;
						width: 2px;
						height: 2px;
						border-radius: 100%;
						background-color: variables.$textColorGray;
					}

					&:first-child {
						margin: 0;
						padding: 0;

						&:after {
							display: none;
						}
