				& + .prod_tag {
					margin-top: 8px;
				}
			}

			&.size_xs {
				.prod_name {
					height: calc(#{variables.$fontSizeXxsLineHeight} * 2);
					max-height: calc(#{variables.$fontSizeXxsLineHeight} * 2);
				}
			}

			&.size_sm {
				.prod_name {
					height: calc(#{variables.$fontSizeXsLineHeight} * 2);
					max-height: calc(#{variables.$fontSizeXsLineHeight} * 2);
				}
			}

			&.size_lg {
				.prod_name {
					height: calc(#{variables.$fontSizeDefLineHeight} * 2);
					max-height: calc(#{variables.$fontSizeDefLineHeight} * 2);
				}
			}

			&.size_xl { // 북캐스트에서 사용
				.prod_name {
					@include mixins.font_size_md;

					&:not(.ellipsis_single) {
						height: calc(#{variables.$fontSizeMdLineHeight} * 2);
						max-height: calc(#{variables.$fontSizeMdLineHeight} * 2);
					}
				}

				.prod_author {
					@include mixins.font_size_xs;
				}
			}
		}

		// box-shadow 타입 박스
		&.box_shadow {
			border-radius: 20px;
			box-shadow: 10px 10px 30px rgba(0, 0, 0, .08);

			.prod_thumb_box {
				border-radius: 20px 20px 0 0;

				.thumb_flag_group {
					.flag {
						padding: 7px 16px 6px;
						border-radius: 20px 0 20px 0;
					}
				}

				.prod_link {
					.img_box {
						img{
							max-height: none;
						}

						&:before {
							display: block;
							content: '';
							position: absolute;
							left: 0;
							top: 0;
							width: 100%;
							height: 100%;
							background: rgba(0, 0, 0, .4);
							@include mixins.transform(translate(0, 0));
							border: none;
						}

						&:after {
							display: block;
							content: "";
							position: absolute;
							left: 50%;
							top: 50%;
							width: 70px;
							height: 70px;
							background: {
								repeat: no-repeat;
								position: center;
							}
							@include mixins.ratio_images('../images/ico_video_play_w70', 'png', 70px, 70px);
							@include mixins.transform(translate(-50%, -50%));
						}
					}

				}
			}

			.prod_info_box {
				margin: 0;
				padding: 24px 40px 36px;
			}
		}

		// 회색배경 타입 박스
		&.gray_box {
			padding: 40px 20px 50px;
			border-radius: 10px;
			background-color: variables.$colorGray06;

			.prod_thumb_box {
				overflow: visible;

				.prod_link {
					width: 150px;
					height: 150px;
					margin: 0 auto;

					.img_box {
						overflow: hidden;
						width: 100%;
						height: 100%;
						border-radius: 100%;
					}
				}

				.btn_wish {
					top: -25px;
					right: 15px;
					bottom: auto;
				}
			}

			.prod_info_box {
				margin-top: 26px;

				.prod_tag {
					text-overflow: initial;
					font-size: 0;
					margin-top: 20px;

					.btn_tag {
						display: inline-block;
						margin-right: 4px;
						padding: 6px 10px 5px;
						@include mixins.font_size_xxs;
						border-radius: 15px;
						background-color: #fff;
						vertical-align: top;

						&:last-child {
							margin-right: 0;
						}
					}
				}
			}
		}

		&.horizontal {
			.prod_thumb_box {
				width: map.get(variables.$prodProps, 'bc-thumb-h-def');

				&.size_sm {
					width: map.get(variables.$prodProps, 'bc-thumb-h-sm');
				}

				&.size_lg {
					width: map.get(variables.$prodProps, 'bc-thumb-h-lg');
				}

				&.size_prod_def {
					width: map.get(variables.$prodProps, 'thumb-h-def');
				}
			}

			// 이미지 우측정렬
			&.reverse {
				justify-content: space-between;
				flex-direction: row-reverse;

				.prod_thumb_box {
					width: 284px;
				}

				.prod_info_box {
					margin: 0 20px 0 0;
				}
			}
		}
	}

	// 품절
	&.sold_out {
		.prod_thumb_box {
			.prod_link {
				pointer-events: none;
			}
		}

		.prod_info_box {
			.prod_info {
				pointer-events: none;
			}
		}

		.prod_thumb_box, .prod_info_box {
			opacity: .5;
		}
	}

	// 사은품
	&.type_gift {
		.prod_thumb_box {
			.btn_gift_detail {
				display: block;
				height: 100%;
				width: 100%;

				&:after {
					content: '';
					position: absolute;
					bottom: 8px;
					right: 8px;
					display: block;
					width: 30px;
					height: 30px;
					@include mixins.ratio_images('../images/btn_gift_detail', 'png', 30px, 30px);
					@include mixins.transition(opacity .23s);
				}
			}

			.img_box{
				&:before{
					content: none;
				}
			}
		}

		&.sold_out {
			.prod_thumb_box {
				opacity: 1;

				.btn_gift_detail {
					pointer-events: none;

					&:after {
						content: none;
					}
				}

				&:after {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					border-radius: 10px;
					background-color: rgba(variables.$textColorBlack, .5);
				}

				.sold_out_msg {
					position: absolute;
					top: 50%;
					left: 0;
					width: 100%;
					@include mixins.font_size_min;
					font-weight: variables.$fontWeightBold;
					color: #fff;
					@include mixins.transform(translateY(-50%));
					text-align: center;
					z-index: 2;

					&:before {
						content: '';
						display: block;
						width: 36px;
						height: 36px;
						margin: 0 auto 8px;
						@include mixins.ratio_images('../images/ico_soldout', 'png', 36px, 36px);
					}
				}
			}
		}

		&.type_unselected {
			.prod_thumb_box {
				.btn_gift_detail {
					pointer-events: none;
				}

				.img_box {
					border: 1px solid variables.$colorGray04;
					background: none;

					&:after {
						content: '';
						position: absolute;
						display: block;
						width: 36px;
						height: 48px;
						top: 50%;
						left: 50%;
						@include mixins.transform(translate(-50%, -50%));
						@include mixins.ratio_images('../images/ico_prod_thumb_gift', 'png', 36px, 48px);
					}
				}
			}
		}
	}

	// 우리가 만드는 책 (설문)
	&.type_survey {
		.prod_thumb_box {
			border: none;
			border-radius: 16px;
			width: 280px;
			max-height: none;

			.img_box {
				border: none;

				img{
					max-height: calc(280px * 1.618 - 10px);
				}

				&:before{
					content: none;
				}
			}

			.thumb_flag_group {
				.flag {
					font-weight: variables.$fontWeightMedium;
				}
			}
		}
	}
}

