							display: inline;
							max-height: none;

							.auto_overflow_inner{
								display: inline;
								font-size: 0;
								line-height: 1;
							}
						}

						.auto_overflow_footer{
							display: none;
						}

						.author{
							&.rep{
								@include mixins.font_size_xxs;
								color: variables.$textColorGray;
							}

							&:not(.rep){
								display: none;
							}
						}

						.gap{
							display: none;
						}

						.count{
							padding-left: 4px;
							@include mixins.font_size_xxs;
							color: variables.$textColorGray;
						}

						~ .prod_publish{
							&:before{
								content: '·';
								display: inline;
								padding: 0 4px;
								@include mixins.font_size_xxs;
								vertical-align: top;
								color: variables.$textColorGray;
							}
						}
					}

					.prod_publish{
						position: relative;
						display: inline;
						margin: 0;
						padding-left: 0;
						vertical-align: top;

