/* 인물 리스트형, 썸네일형 */
.switch_profile_wrap{
	.profile_list{
		padding-top: 30px;
		border-top: 1px solid variables.$colorGray03;
	}

	.pagination{
		margin-top: 36px;
		padding-top: 30px;
		border-top: 1px solid variables.$colorGray04;
	}

	.form_chk{
		flex-shrink: 0;
		margin: 0 10px 10px 0;
	}

	.prod_chk_group{
		flex-shrink: 0;
		margin-right: 10px;

		.form_chk {
			margin-right: 0;
		}

		.rank_status {
			display: block;
			height: auto;
			padding: 9px 0 0;
			text-align: center;
			background-position: top center;

			&.new{
				padding-top: 0;
			}
		}
	}

	.person_info_area{
		flex: 1;
		width: 100%;
		border-radius: 16px;

		.profile_img{
			width: 118px;
			height: 118px;
			overflow: visible;
		}

		.prod_rank{
			@include mixins.justify_width(end, between);
			margin-bottom: 8px;

			.badge_flag{
				margin-bottom: 0;
			}
		}
	}

	&.view_type_list{
		.profile_list{
			.profile_item{
				@include mixins.justify_width;

				~ .profile_item{
					margin-top: 36px;
					padding-top: 36px;
					border-top: 1px solid variables.$colorGray04;
				}
			}
		}

		.person_info_area{
			position: relative;
			@include mixins.justify_width(center);
			padding: 39px 49px 39px 35px;

			.person_profile_box{
				flex: 1;
				margin-right: 40px;

				.profile_img{
					.btn_wish_circle{
						display: none;
					}
				}

				.profile_info{
					.prod_rank{
						.rank_status{
							display: none;
						}
					}
				}
			}

			.person_prod_box{
				position: relative;
				margin: 0 0 0 auto;
				padding-left: 40px;
				flex-shrink: 0;

				.person_prod_cont{
					margin-top: 0;

					.tag_wrap{
						display: none;
					}

					.person_prod_list{
						margin-top: 0;

						.person_prod_item{
							width: 94px;

							img{
								max-height: calc(84px * 1.618 - 10px);
							}
						}
					}
				}

				&:before{
					content: '';
					position: absolute;
					top: 50%;
					left: 0;
					width: 1px;
					height: 80px;
					background-color: variables.$colorGray04;
					@include mixins.transform(translateY(-50%));
				}
			}

			.btn_wish_icon{
				position: absolute;
				top: 23px;
				right: 23px;
			}
		}
	}

	&.view_type_img{
		.profile_list{
			font-size: 0;

			.profile_item{
				display: inline-block;
				width: 219px;
				vertical-align: top;

				~ .profile_item{
					margin-left: 36px;
				}

				&:nth-child(4n+1){
					margin-left: 0;
				}

				&:nth-child(n+5){
					margin-top: 40px;
				}
			}
		}

		.prod_chk_group{
			.rank_status{
				display: none;
			}
		}

		.person_info_area{
			position: relative;
			display: block;
			padding: 54px 24px 23px 23px;
			min-height: 403px;
			box-sizing: border-box;

			.person_profile_box{
				display: block;

				.profile_img{
					position: relative;
					margin: 0 auto;
				}

				.btn_wish_circle{
					position: absolute;
					right: 0;
					bottom: 0;
					background-color: #fff;
				}

				.profile_info{
					position: static;
					margin: 16px 0 0;
					min-height: 46px;

					.prod_rank{
						position: absolute;
						top: 19px;
						left: 19px;
						right: 19px;
						height: 19px;
						margin-bottom: 0;
					}

					.person_name, .person_sub_name{
						display: block;
						padding-right: 0;
						text-align: center;
					}

					.person_desc, .btn_wrap{
						display: none;
					}
				}
			}

			.person_prod_box{
				.person_prod_cont{
					.person_prod_list{
						display: none;
					}
				}
			}

			.btn_wish_icon{
				display: none;
			}
		}
	}
