// 리스트 필터 박스
.list_filter_wrap {
	display: none;
	position: relative;
	padding: 20px 180px 20px 20px;
	border-top: 1px solid variables.$colorGray03;

	.list_filter_option {
		margin: -20px 0 0;

		.list_filter_title {
			display: inline-block;
			margin: 20px 20px 0 60px;
			@include mixins.font_size_xs();
			vertical-align: top;

			&:first-child {
				margin: 20px 20px 0 0;
			}
		}

		.list_filter_option_bunddle {
			display: inline-block;
		}

		.form_chk {
			margin: 20px 20px 0 0;

			&:last-child {
				margin-right: 20px;
			}
		}
	}

	.util_filter {
		position: absolute;
		right: 60px;
		top: 15px;

		button {
			margin: 0 14px 0 0;
			vertical-align: middle;

			&:last-child {
				margin: 0;
			}
		}
	}

	.btn_filter_close {
		display: block;
		position: absolute;
		right: 20px;
		top: 20px;
		width: 20px;
		height: 20px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/btn_tooltip_close', 'png', 20px, 20px);
		box-sizing: border-box;
	}

	&.active {
		display: block;

		& + .prod_list_wrap {
			border-top: 1px solid variables.$colorGray04;
		}
	}
}

// 상품 리스트 박스
.prod_list_wrap {
	padding: 30px 0 0;
	border-top: 1px solid variables.$colorGray03;

	.no_data {
		margin-top: -30px;
		border-bottom: 1px solid variables.$colorGray03;
	}

	.pagination {
		margin-top: 40px;
		padding-top: 30px;
		border-top: 1px solid variables.$colorGray04;
	}

	.btn_wrap {
		margin-top: 40px;
	}
}

/* 이벤트 리스트 */
.event_list {
	&:not(.swiper-wrapper) {
		display: block;
		font-size: 0;

		.event_item {
			display: inline-block;
		}
	}

	.event_item {
		display: inline-block;
		width: calc((100% - 40px) / 3);
		margin-left: 20px;
		vertical-align: top;

		&:nth-child(3n+1) {
			margin-left: 0;
		}

		&:nth-child(n+4) {
			margin-top: 24px;
		}
	}

	&.col1 {
		.event_item {
			width: 100%;
			margin-left: 0;
		}
	}
	&.col4 {
		.event_item {
			width: calc((100% - 108px) / 4);
			margin-left: 36px;
			vertical-align: top;

			&:nth-child(4n + 1) {
				margin-left: 0;
			}
			&:nth-child(-n + 4) {
				margin-top: 0;
			}
		}
	}
}

/* 이벤트 스와이퍼 */
.event_swiper_wrap {
	position: relative;

	.swiper-container {
		.swiper-wrapper {
			margin: 0;

			.swiper-slide {
				width: 304px;
				margin: 0 20px 0 0;

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	.swiper-button-prev {
		left: -22px;
	}

	.swiper-button-next {
		right: -22px;
	}

	.swiper-scrollbar {
		position: static;
		margin-top: 18px;
		width: 100%;
		height: 2px;
		background: rgba(0, 0, 0, .06);
		border-radius: 2px;
		overflow: hidden;

		.swiper-scrollbar-drag {
			background: #000;
			border-radius: 2px;
		}
	}

	&.col3{
		.event_list{
			.event_item{
				width: calc((100% - 40px) / 3);
			}
		}

		.swiper-button-prev, .swiper-button-next {
			top: 64px;
			margin: 0;
		}
	}

	&.size_sm{
		.swiper-container {
			.swiper-wrapper {
				margin: 0;

				.swiper-slide {
					width: 258px;
					margin-right: 16px;

					&.prod_item {
						width: 100px;
					}
				}
			}
		}
	}
}

/* 이벤트 아이템 */
.event_area {
	.event_link {
		display: block;
		width: 100%;
	}

	.event_thumb_box {
		display: block;
