/* 섬네일 리스트 */
[class*='thumbnail_list'] {
	@include mixins.justify_width(start, start, row);
	box-sizing: border-box;

	&.row_stack {
		flex-wrap: wrap;
	}

	/* Vertical Type List */
	&.thumbnail_list_vertical {
		margin: -10px;

		.thumbnail_item {
			padding: 10px;
			box-sizing: border-box;
		}
	}

	/* Horizontal Type List */
	&.thumbnail_list_horizontal {
		margin: -8px;

		.thumbnail_item {
			padding: 8px;
			box-sizing: border-box;

			&:first-child {
				.thumbnail_product_item.horizontal_type {
					.info_area {
						.info_product_number {
							font-weight: variables.$fontWeightMedium;
							color: variables.$textColorGreen;
						}
					}
				}
			}
		}

		&.row_stack {
			min-width: auto;

			.thumbnail_item {
				.thumbnail_product_item {
					min-width: auto;
				}
			}
		}

		&.full_width {
			flex-wrap: wrap;

			.thumbnail_item {
				width: 100%;
				min-width: 100%;
			}
		}

		&.align_top {
			.thumbnail_item {
				.thumbnail_product_item {
					&.horizontal_type {
						@include mixins.justify_width(start, start, row);

						.info_area {
							padding-top: 10px;
						}
					}
				}
			}
		}
	}
}
.swiper-container {
	&.scrollbar_text {
		.swiper-wrapper.thumbnail_list_vertical {
			padding-bottom: 20px;

			.thumbnail_item.swiper-slide {
