/* 결합 상품 */
.prod_combined_list_wrap {
	.prod_combined_list {
		@include mixins.justify_width(start, row, true);
		align-items: stretch;
		flex-wrap: wrap;
		font-size: 0;

		.prod_combined_item {
			width: calc(50% - 24px);
			height: auto;
			padding: 0 24px 30px 0;
			border-bottom: 1px solid variables.$colorGray04;

			.prod_area {
				&.horizontal {
					@include mixins.justify_width(start, start, row, false);
				}

				.prod_info_box {
					.prod_num {
						display: block;
						margin-bottom: 5px;
						font-size: variables.$fontSizeDef;
						font-weight: variables.$fontWeightBold;
						color: variables.$textColorPurple;
					}

					.prod_price {
						+ .combine_price {
							margin-top: 23px;
						}
					}

					.combine_price {
						.badge_flag {
							background: transparent;
							margin-bottom: 0;

							+ .price {
								margin: 2px 0 0 6px;
							}
						}

						.price {
							display: inline-block;
							font-size: 0;
							color: variables.$textColorPurple;
							vertical-align: top;

							.val {
								display: inline-block;
								font-size: variables.$fontSizeXs;
								font-weight: variables.$fontWeightBold;
								vertical-align: top;
							}

							.unit {
								display: inline-block;
								font-size: variables.$fontSizeXs;
								vertical-align: top;
							}
						}
					}
				}
			}

			&.prod_combined_item:nth-child(n + 3) {
				padding-top: 30px;
			}
		}
	}

	.auto_overflow_contents {
		.prod_combined_list {
			.prod_combined_item {
				padding-top: 30px;
			}
		}
	}
}

/* 기프트카드 리스트 */
.gift_card_list {
	font-size: 0;

	&:not(.swiper-wrapper) {
		display: block;
	}

	.gift_card_item {
		display: inline-block;
		position: relative;
		width: 285px;
		vertical-align: top;

		& ~ .gift_card_item {
			margin-left: 20px;
		}

		.form_rdo, .form_chk {
			& + .img_box {
				margin-top: 10px;
			}
		}

		.img_box {
			position: relative;
			width: 100%;
			border-radius: 12px;
			overflow: hidden;

			img {
				width: 100%;
			}

			.gift_card_balance {
				display: block;
				position: absolute;
				left: 0;
				bottom: 0;
				width: 100%;
				height: 40px;
				padding: 10px 16px 0;
				@include mixins.font_size_xs;
				color: #fff;
				background-color: rgba(0, 0, 0, 0.5);
				box-sizing: border-box;

				.val {
					display: inline-block;
					font-size: variables.$fontSizeDef;
					font-weight: variables.$fontWeightBold;
					vertical-align: top;
				}
			}
		}

		.info_box {
			display: block;
			margin-top: 16px;
			text-align: center;

			.gift_card_name {
				display: block;
				color: variables.$textColorBlack;
				@include mixins.font_size_sm;
				font-weight: variables.$fontWeightRegular;
			}

			.gift_card_num {
				display: block;
				margin-top: 5px;
				color: variables.$textColorLightGray;
				@include mixins.font_size_xxs;
				font-weight: variables.$fontWeightRegular;
			}
		}

		.btn_gift_card {
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: 1;
		}
	}
