/* 주문결제 하단 상품금액정보 */
.total_price_info {
	@include mixins.justify_width(center, center);
	height: 72px;
	background-color: rgba(variables.$kyoboPurple, .04);
	text-align: center;

	.info_item {
		font-size: 0;

		& + .info_item {
			margin-left: 4px;
		}

		.label {
			display: inline-block;
			padding-top: 1px;
			margin-right: 3px;
			@include mixins.font_size_xs;
			vertical-align: top;
		}

		.price {
			display: inline-block;
			font-size: 0;
			vertical-align: top;

			.val {
				@include mixins.font_size_def;
				font-weight: variables.$fontWeightBold;
			}

			.unit {
				@include mixins.font_size_xs;
			}
		}

		.desc {
			display: inline-block;
			margin-left: 4px;
			margin-top: 1px;
			@include mixins.font_size_xs;
			color: variables.$textColorGray;
			vertical-align: top;
		}

		&.plus, &.total {
			font-size: 0;

			&:before {
				content: '';
				display: inline-block;
				width: 8px;
				height: 16px;
				margin: 3px 6px 0 2px;
				vertical-align: top;
			}
		}

		&.plus {
			&:before {
				@include mixins.ratio_images('../images/ico_total_price_plus', 'png', 8px, 16px);
			}
		}

		&.total {
			.btn_xxs {
				margin-left: 10px;
			}

			&:before {
				@include mixins.ratio_images('../images/ico_total_price_total', 'png', 8px, 16px);
			}
		}
	}
}