@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
/* 타이틀 영역 */
/*
	컨텐츠 + 타이틀 사이 여백(상품상세 예외)
	title_size_md(20px) : margin-top: 50px;
	title_size_lg(24px) : margin-top: 70px;
	title_size_xxl(30px) : margin-top: 110px;
*/
.title_wrap {
	position: relative;
	@include mixins.justify_width(center);

	// 타이틀 영역 왼쪽/오른쪽 영역
	.right_area {
		margin-left: auto;
		flex-shrink: 0;
		@include mixins.justify_width(center, start, row);

		// 기본 간격 조절
		[class^='btn_']:not(.btn_xs, [class^=btn_sns_share]) {
			~ [class^='btn_']:not(.btn_xs) {
				margin-left: 6px;
			}
		}

		.switch_list_btn_wrap {
			[class^='btn_']:not(.btn_xs) {
				~ [class^='btn_']:not(.btn_xs) {
					margin-left: 0;
				}
			}

			& + .btn_sm {
				margin-left: 6px;
			}
		}

		.btn_xs {
			~ .btn_xs {
				margin-left: 3px;
			}
		}

		.form_chk {
			+ [class^='btn_'] {
				margin-left: 20px;
			}

			& + .switch_list_btn_wrap {
				margin-left: 20px;
			}
		}

		.required {
			+ [class^='btn_'] {
				margin-left: 20px;
			}
		}
		.btn_sm {
			~ .switch_list_btn_wrap {
				margin-left: 4px;
			}
		}

		.info_text,
		.bul_item_asterisk {
			+ [class^='btn_'] {
				margin-left: 20px;
			}
			+ .form_sel {
				margin-left: 20px;
			}
			+ .prod_filter_wrap{
				margin-left: 20px;
			}
		}
	}

	// title_size_xxl 기준 여백임. 변경필요시 title_size_xxl 타임에서 재선언필요
	.category_tag_list {
		margin: 4px 0 0 30px;
	}

	// 타이틀 텍스트 부분 사이즈별
	.title_heading {
		display: inline-block;
		position: relative;
		font-weight: variables.$fontWeightBold;

		.title_prefix {
			display: inline-block;
			color: variables.$textColorGray;
			margin-right: 3px;
			font-weight: variables.$fontWeightRegular;
		}

		&.type_link {
			&:before {
				content: '';
				display: block;
				position: absolute;
				right: 0;
			}

			&:hover, &:focus, &:active {
				text-decoration: underline;
			}
		}

		.btn_title_toggle {
			&:not(.active) {
				.text {
					opacity: .2;
				}
			}
			+ .btn_title_toggle {
				position: relative;
				margin-left: 28px;

				&:before {
					content: '';
					position: absolute;
					display: inline-block;
					width: 2px;
					height: 13px;
					background: variables.$colorGray04;
					left: -18px;
					top: 17px;
				}
			}
		}
	}

	.tooltip_wrap {
		margin-left: 6px;
	}

	.tab_wrap {
		width: auto;

		.tab_list_wrap {
			margin-bottom: 0;
			height: auto;
		}
	}

	// font-size: 30px
	&.title_size_xxl {
		padding-bottom: 30px;

		.title_heading {
			@include mixins.font_size_xxl;
		}

		~ .title_size_xxl{
			margin-top: 110px;
		}

		~ .title_size_lg{
			margin-top: 70px;
		}

		~ .title_size_md {
			margin-top: 50px;
		}

		+ .title_size_xxl, + .title_size_lg, + .title_size_md, + .title_size_def {
			margin-top: 0
		}
	}

	// font-size: 24px
	&.title_size_lg {
		padding-bottom: 30px;

		.title_heading {
			@include mixins.font_size_lg;
		}

		&.has_sub_title {
			padding-bottom: 30px;

			> .info_text, > [class^='bul_item_'] {
				margin-top: 10px;
			}
		}

		&.has_btn {
			min-height: 38px;
			padding-bottom: 27px;
		}

		~ .title_size_md {
			margin-top: 50px;
		}

		~ .title_size_lg {
			margin-top: 70px;
		}

		+ .title_size_lg, + .title_size_md, + .title_size_def {
			margin-top: 0
		}
	}

	// H3, font-size: 20px
	&.title_size_md {
		padding-bottom: 20px;

		.title_heading {
			@include mixins.font_size_md;

			+ .tab_wrap {
				&.type_text {
					margin: 0 0 2px 16px;
				}
			}

			+ .btn_info_popup {
				margin-left: 6px;
			}

			&.type_link {
				position: relative;
				padding-right: 16px;

				&:before {
					top: 6px;
					width: 10px;
					height: 16px;
					@include mixins.ratio_images('../images/btn_more_view_black_lg', 'png', 10px, 16px);
				}

				&:focus, &:active {
					text-decoration: underline;
				}
			}
		}

		&.has_btn {
			min-height: 38px;
			padding-bottom: 16px;
		}

		&.has_sub_title {
			padding-bottom: 24px;
		}

		~ .title_size_md {
			margin-top: 50px
		}

		+ .title_size_md, + .title_size_def {
			margin-top: 0
		}
	}

	// H4, font-size: 16px
	&.title_size_def {
		padding-bottom: 20px;

		.title_heading {
			@include mixins.font_size_def;
		}

		&.has_btn {
			min-height: 30px;
			padding-bottom: 14px;
		}

		&.has_sub_title {
			padding-bottom: 20px;
		}

		+ .title_size_def {
			margin-top: 0
		}
	}

	// B1, font-size: 15px
	&.title_size_sm {
		min-height: 24px;
		padding-bottom: 12px;

		.title_heading {
			@include mixins.font_size_sm;

			+ .btn_info_popup {
				margin-left: 6px;
			}
		}

		&.has_sub_title {
			padding-bottom: 20px;
		}

		// 여백 맞는지 확인 필요
		~ .title_size_sm {
			margin-top: 20px;
		}

		+ .title_size_sm {
			margin-top: 0
		}
	}

	// B2, font-size: 14px
	&.title_size_xs {
		padding-bottom: 10px;

		.title_heading {
			@include mixins.font_size_xs;

			.ico_speech {
				margin: 2px 4px 0 0;
			}
		}

		&.has_sub_title {
			> .info_text, > [class^='bul_item_'] {
				margin-top: 7px;
			}
		}

		// 여백 맞는지 확인 필요
		~ .title_size_xs {
			margin-top: 18px;
		}

		+ .title_size_xs {
			margin-top: 0
		}
	}

	// B3, font-size: 13px
	&.title_size_xxs {
		padding-bottom: 8px;

		.title_heading {
			@include mixins.font_size_xxs;
		}

		+ .title_size_xxs {
			margin-top: 0
		}
	}

	.required {
		display: inline-block;
		@include mixins.font_size_xxs;
		color: variables.$textColorGray;
		vertical-align: top;

		&:before {
			content: '*';
			display: inline-block;
			position: relative;
			width: 6px;
			height: 6px;
			margin-right: 3px;
			@include mixins.transform(translateY(6px));
			@include mixins.ratio_images('../images/ico_required', 'png', 6px, 6px);
			color: transparent;
		}
	}

	.count {
		display: inline-block;
		min-width: 20px;
		padding: 0 6px;
		margin: -1px 0 0 5px;
		color: #fff;
		@include mixins.font_size_min;
		line-height: 20px;
		font-weight: variables.$fontWeightBold;
		background: #000;
		border-radius: 20px;
		text-align: center;
		box-sizing: border-box;
		vertical-align: top;
	}

	.standard_date {
		display: inline-block;
		position: relative;
		margin-left: 10px;
		padding-left: 20px;
		@include mixins.font_size_xxs;
		color: variables.$textColorLightGray;
		vertical-align: top;

		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 2px;
			width: 15px;
			height: 15px;
			background-repeat: no-repeat;
			@include mixins.ratio_images('../images/ico_standard_time', 'png', 15px, 15px);
		}

		.date {
			font-weight: variables.$fontWeightMedium;
		}
	}

	&.has_sub_title {
		flex-wrap: wrap;

		.title_heading {
			display: block;
			padding-bottom: 0;
		}

		> .info_text, > [class^='bul_item_'] {
			width: 100%;
			margin-top: 6px;
		}

		.sub_title_wrap {
			width: 100%;
			@include mixins.justify_width(center);
			margin-top: 6px;

			.tooltip_wrap {
				margin-left: 6px;
			}

			> .info_text, > [class^='bul_item_'] {
				width: auto;
				margin-top: 0;
			}

			.right_area {
				flex-shrink: 0;
				margin-left: auto;
			}
		}
	}

	&.has_line {
		margin-bottom: 30px;
		border-bottom: 1px solid variables.$textColorBlack;
	}
	&.has_line_gray {
		margin-bottom: 30px;
		border-bottom: 1px solid variables.$colorGray03;
	}

	& + .prod_list_wrap {
		padding: 0;
		border-top: none;
	}
}

@at-root .contents_wrap.aside{
	.title_wrap{
		&.title_size_md, &.title_size_lg{
			~ .title_size_md{
				margin-top: 70px;
			}

			+ .title_size_md{
				margin-top: 0;
			}
		}
	}
}

/* 컬러 */
.fc_spot {
	color: variables.$textColorPurple !important;
}
.fc_black {
	color: variables.$textColorBlack !important;
}
.fc_green {
	color: variables.$textColorGreen !important;
}
.fc_red {
	color: variables.$textColorRed !important;
}

/* 폰트 굵기 */
.fw_bold {
	font-weight: variables.$fontWeightBold !important;
}
.fw_medium {
	font-weight: variables.$fontWeightMedium !important;
}
.fw_regular {
	font-weight: variables.$fontWeightRegular !important;
}
