@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
/* Table Row Wrap */
.tbl_row_wrap {
	display: block;
	width: 100%;
	padding: 18px 0;
	border-top: 1px solid variables.$textColorBlack;
	border-bottom: 1px solid variables.$colorGray04;
	box-sizing: border-box;

	/* Table Row Type */
	.tbl_row, .tbl_row_line {
		width: 100%;
		table-layout: fixed;

		tbody {
			th {
				padding: 12px 10px;
				@include mixins.font_size_xs;
				text-align: left;
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;
				vertical-align: top;

				.required {
					display: inline-block;
					width: 6px;
					height: 6px;
					padding-left: 3px;
					margin-top: 5px;
					vertical-align: top;

					&:before {
						content: '*';
						display: inline-block;
						position: relative;
						width: 6px;
						height: 6px;
						@include mixins.ratio_images('../images/ico_required', 'png', 6px, 6px);
						color: transparent;
					}
				}

				.tooltip_wrap {
					&.type_sm {
						margin: 3px 0 0 3px;
					}
				}

				&.has_ip {
					padding-top: 24px;
				}

				&.has_btn_sm {
					padding-top: 20px;
				}

				&.has_btn_xxs {
					padding-top: 13px;
				}

				&.vertical_middle {
					vertical-align: middle;
				}
			}

			td {
				padding: 12px 10px;
				@include mixins.font_size_xs;
				text-align: left;
				color: variables.$textColorLightGray;
				font-weight: variables.$fontWeightRegular;
				vertical-align: top;

				.flex_both_wrap{
					width: 450px;
					@include mixins.justify_width(center, between);
				}

				.btn_text_box {
					@include mixins.justify_width(center);

					.text {
						+ .btn_xxs {
							margin-left: 6px;
						}

						+ .btn_sm {
							margin-left: 12px;
						}
					}

					.btn_sm {
						+ .text {
							margin-left: 10px;
						}
					}

					.btn_ip {
						+ .info_text {
							margin-left: 12px;
						}
					}

					&.between {
						justify-content: space-between;
					}
				}

				.btn_text_link {
					overflow: hidden;
					color: inherit;

					.text {
						text-decoration: underline;
					}
				}

				.form_col_group {
					.valid_check, .form_ip, .form_sel, .input_btn_box {
						width: 100%;
					}
				}

				.valid_check, .form_ip, .form_sel, .input_btn_box, .form_col_group,
				.pw_valid_wrap, .form_ip_pw, .byte_check_wrap, .file_upload_group,
				.form_filter_box, .input_text_box, .input_price_box, .price_value_box {
					width: 450px;

					+ .info_text, + [class^='bul_item_'] {
						display: block;
						margin-top: 10px;
					}
				}

				.form_sel {
					.ui-selectmenu-button {
						width: 100%;
					}
				}

				.input_btn_box {
					.form_ip, .form_sel, .form_ip_pw {
						width: 100%;
					}

					.form_filter_box {
						width: auto;
					}
				}

				.date_picker_box {
					width: 450px;

					.form_sel {
						width: 126px;
					}
				}

				.bul_list,
				[class^='bul_item_'] {
					&.text_bottom {
						margin-top: 6px;
					}
				}

				.cert_state {
					@include mixins.font_size_xs;

					&.complete {
						font-weight: variables.$fontWeightBold;
						color: variables.$textColorPurple;
					}

					+ .info_text {
						margin-top: 10px;
					}
				}

				.sel_text_box {
					.form_sel {
						width: 120px;
					}
				}

				// 말풍선 타입
				.bubble_speech_wrap {
					display: block;
					position: relative;
					padding-bottom: 44px;

					.bubble_speech_desc {
						visibility: hidden;
						left: 0;
						bottom: 0;
					}

					.form_filter_box {
						.form_filter {
							position: relative;

							input:checked + label {
								~ .bubble_speech_desc {
									visibility: visible;
								}
							}

							.bubble_speech_desc {
								bottom: -44px;
							}
						}
					}
				}

				// 주소 아이템
				.address_item {
					.address, .email {
						margin-top: 12px;
					}
					.msg {
						margin-top: 10px;
					}
					.address_person {
						&.has_btn {
							& + .address {
								margin-top: 10px;
							}
						}
					}
				}

				&.align_right{
					text-align: right;
				}

				&.vertical_top {
					vertical-align: top;
				}
			}
		}

		tr {
			th, td {
				.row_2_ellipsis {
					@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 2, false);
				}
			}
		}

		+ .tbl_row {
			margin-top: 18px;
			border-top: 1px solid variables.$colorGray04;

			tbody {
				tr {
					&:first-child {
						th, td {
							padding-top: 30px;
						}
						th.has_ip {
							padding-top: 42px;
						}
					}
				}
			}
		}
		.fc_gray{
			color: variables.$textColorLightGray;
		}
	}

	.tbl_row_line {
		tbody {
			tr {
				th, td {
					padding: 24px 10px;
				}

				+ tr {
					th, td {
						border-top: 1px solid variables.$colorGray04;
					}
				}

				&:first-child {
					th, td {
						padding-top: 12px;
					}
				}

				&:last-child {
					th, td {
						padding-bottom: 12px;
					}
				}
			}
		}
	}

	+ .bul_list,
	+ .bul_item_dot {
		margin-top: 16px;
	}

	+ .bul_item_asterisk {
		margin-top: 10px;
	}

	+ .round_gray_box {
		margin-top: 10px;
	}

	& + .info_text_box {
		margin-top: 30px; // TODO 추후 여백가이드 나오면 정리 (테이블 + 회색 박스 여백 30px 적용 필요)
	}

	+ .tbl_prod_wrap{
		margin-top: 70px;
	}

	&.type_th_group {
		padding: 0;
		border-bottom: none;

		.tbl_row {
			tr {
				th {
					text-align: center;

					&.th_group {
						padding: 20px 20px;
						background-color: variables.$colorGray06;
						border-right: none;
						text-align: center;
						color: variables.$textColorLightGray;
					}
				}

				th, td {
					padding: 20px 20px;
					border-right: 1px solid variables.$colorGray04;
					border-bottom: 1px solid variables.$colorGray04;
					vertical-align: middle;

					.fc_gray{
						color: variables.$textColorLightGray;
					}

					&.fc_gray{
						color: variables.$textColorLightGray;
					}
				}

				td{
					color: variables.$textColorBlack;

					&:last-child{
						border-right: none;
					}
				}

				&:last-child {
					th{
						&.th_group{
							//border-bottom: none;
						}
					}

					th, td {
						padding-bottom: 25px;
						//border-bottom: none;
					}
				}
			}
		}
	}

	&.no_line {
		padding: 0;
		border: none;

		.tbl_row,
		.tbl_row_line {
			tbody {
				tr {
					th {
						padding-top: 0;
						padding-left: 0;

						&.has_ip {
							padding-top: 12px;
						}
					}
					td {
						padding-top: 0;
						padding-right: 0;
					}

					& ~ tr {
						th, td {
							padding-top: 12px;
						}
						th {
							&.has_ip {
								padding-top: 24px;
							}
						}
					}

					&:last-child {
						th, td {
							padding-bottom: 0;
						}
					}
				}
			}
		}
	}
	&.type_space_lg {
		padding-top: 25px;
		padding-bottom: 25px;

		.tbl_row tbody,
		.tbl_row_line tbody {
			th,
			td {
				padding-top: 15px;
				padding-bottom: 15px;
				@include mixins.font_size_sm;
			}
		}
	}
}

/* Table Column Wrap */
.tbl_col_wrap {
	display: block;
	width: 100%;
	border-top: 1px solid variables.$textColorBlack;
	border-bottom: 1px solid variables.$colorGray04;
	box-sizing: border-box;

	/* Table Column Type */
	.tbl_col, .tbl_col_line {
		width: 100%;
		table-layout: fixed;

		tr {
			td {
				padding: 12px 10px;
				text-align: center;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightRegular;
				vertical-align: middle;

				.btn_text_link {
					color: inherit;

					.text {
						text-decoration: underline;
					}
				}

				.fc_light_gray{
					color: variables.$textColorLightGray;
				}

				.bul_list,
				[class^='bul_item_'] {
					&.text_bottom {
						margin-top: 6px;
					}
				}

				.earn_point {
					color: variables.$textColorBlack;

					.val {
						font-weight: variables.$fontWeightMedium;
						margin-right: 6px;
					}
				}

				// 검색어와 매치되는 단어
				.mark {
					color: variables.$textColorPurple;
					font-weight: variables.$fontWeightBold;
				}

				&.fc_light_gray{
					color: variables.$textColorLightGray;
				}
			}

			th {
				padding: 16px 10px 14px;
				text-align: center;
				@include mixins.font_size_xs;
				color: variables.$textColorLightGray;
				font-weight: variables.$fontWeightMedium;
				vertical-align: middle;
			}

			th, td {
				.row_2_ellipsis {
					@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 2, false);
				}

				&.vertical_top {
					vertical-align: top;
				}

				&.align_left {
					text-align: left;
				}

				&.align_right {
					text-align: right;
				}
			}
		}
	}

	.tbl_col {
		tbody {
			tr {
				&:first-child {
					border-top: 1px solid variables.$colorGray03;

					td {
						padding-top: 20px;
					}
				}

				&:last-child {
					td {
						padding-bottom: 30px;
					}
				}
			}
		}
	}

	.tbl_col_line {
		tr {
			th {
				padding: 16px 24px 14px;
			}

			td {
				padding: 28px 24px;

				.price {
					font-size: 0;

					.val, .unit {
						@include mixins.font_size_xs;
					}

					.val {
						font-weight: variables.$fontWeightBold;
					}
				}
			}

			&.has_btn{
				td{
					padding: 24px 24px;

					.btn_sm{
						padding: 0 12px;
					}
				}
			}
		}

		thead {
			th {
				border-bottom: 1px solid variables.$colorGray04;
			}

			&.hidden{
				position: relative;

				tr{
					th{
						height: 0;
						padding: 0;
						font-size: 0;
						border: none;
						line-height: 1;
					}
				}
			}
		}

		tbody {
			td {
				border-bottom: 1px solid variables.$colorGray04;

				.multi_count{
					@include mixins.justify_width;

					.text{
						@include mixins.ellipsis(false);
					}

					.count{
						flex-shrink: 0;
						margin-left: 5px;
					}
				}
				
				.btn_text_link {// 바로드림 - 이벤트&공지사항 목록 테이블
					&.ellipsis_single {
						@include mixins.ellipsis();
						.text {
							@include mixins.ellipsis();
						}
					}
				}
			}

			tr:last-child {
				td {
					border-bottom: 0;
				}
			}
		}

		&.type_td_line {
			tbody {
				tr {
					td {
						& ~ td {
							border-left: 1px solid variables.$colorGray04;
						}
						
						&.no_line_left {
							border-left: 0;
						}
					}
					th {
						border-bottom: 1px solid variables.$colorGray04;
						
						&.line_left {
							border-left: 1px solid variables.$colorGray04;
						}
					}
					
					&:last-child {
						th {
							border-bottom: 0;
						}
					}
				}
			}
		}
	}

	&.type_th_gray {
		.tbl_col {
			tr {
				th {
					background-color: variables.$colorGray06;
					border-bottom: 1px solid variables.$colorGray03;
				}

				~ tr {
					th {
						border-top: 1px solid variables.$colorGray04;
					}
				}
			}

			tbody {
				tr {
					td {
						padding: 20px 10px;
						border-top: 1px solid variables.$colorGray04; // TODO : 임의작업. 추후 케이스 나오면 케이스 맞춰 수정
					}
				}
			}
		}
	}

	& + .info_text_box {
		margin-top: 30px; // TODO 추후 여백가이드 나오면 정리 (테이블 + 회색 박스 여백 30px 적용 필요)
	}
}

/* 주문 / 장바구니 테이블 */
.tbl_prod_wrap {
	.btn_delete_ord {
		position: absolute;
		top: 17px;
		right: 7px;
		width: 21px;
		height: 21px;
		padding: 3px;
		@include mixins.ratio_images('../images/btn_delete_ord', 'png', 15px, 15px);
		background: {
			repeat: no-repeat;
			position: center;
		}
	}

	.tbl_prod {
		width: 100%;
		table-layout: fixed;
		border-top: 1px solid variables.$textColorBlack;

		.btn_delete_ord {
			position: absolute;
			top: 17px;
			right: 7px;
			width: 21px;
			height: 21px;
			padding: 3px;
			@include mixins.ratio_images('../images/btn_delete_ord', 'png', 15px, 15px);
			background: {
				repeat: no-repeat;
				position: center;
			}
		}

		thead{
			&.hidden{
				position: relative;
			}
		}

		tbody {
			td {
				position: relative;
				padding: 24px;
				text-align: center;
				border-bottom: 1px solid variables.$colorGray04;

				&.only_chk {
					padding: 24px 10px;
					vertical-align: top;

					+ .prod {
						border-left: none;
					}
				}

				&.prod {
					padding-left: 0;
					text-align: left;
					vertical-align: top;

					&.sold_out {
						~ td {
							border-left: none;
						}
					}

					.prod_area {
						align-items: flex-start;
					}
				}

				.price {
					font-size: 0;

					.val, .unit {
						@include mixins.font_size_def;
					}

					.val {
						font-weight: variables.$fontWeightBold;
					}

					.btn_info_popup{
						margin: 3px 0 0 6px;
					}
				}

				.prod_option {
					margin-top: 8px;

					> .text, .val {
						padding-top: 2px;
						@include mixins.font_size_xxs;
					}
				}

				.delivery_info {
					text-align: left;

					.delivery_desc {
						margin-top: 8px;
						@include mixins.font_size_xxs;
					}

					.info_inner {
						~ .info_inner {
							padding-top: 16px;
							border-top: 1px dashed variables.$colorGray03;
							margin-top: 16px;
						}
					}
				}

				.form_spinner_box {
					margin-top: 10px;
				}

				~ td {
					border-left: 1px solid variables.$colorGray04;
				}
			}
		}

		tfoot {
			td {
				border-bottom: 1px solid variables.$colorGray04;
			}
		}
	}

	.btn_wrap {
		margin-top: 16px;
	}

	// 장바구니 전용 패키지 상품타입
	&.type_package {
		&:not(.type_myroom){
			overflow: hidden;
			border-radius: 16px;
			border: 1px solid variables.$colorGray04;
			border-bottom: none;

			.tbl_header {
				position: relative;
				padding: 13px 24px;
				background-color: variables.$colorGray05;
				border-bottom: 1px solid variables.$colorGray04;

				.package_title {
					@include mixins.font_size_xs;
					font-weight: variables.$fontWeightBold;
				}

				.btn_delete_ord {
					top: 14px;
					right: 21px;
				}
			}

			.package_prod_box {
				@include mixins.justify_width;
				padding: 24px;
				border-bottom: 1px solid variables.$colorGray04;

				.form_chk {
					margin-right: 10px;
				}

				.prod_area {
					.prod_info_box {
						.prod_price {
							.percent {
								@include mixins.font_size_sm;
							}

							.price {
								.val, .unit {
									@include mixins.font_size_sm;
								}
							}
						}
					}
				}

				.control_box {
					flex-shrink: 0;
					align-self: center;
					margin-left: 24px;
					font-size: 0;

					.btn_xs {
						+ .form_spinner_box {
							margin-left: 4px;
						}
					}
				}
			}

			.tbl_prod {
				border-top: none;

				tbody {
					tr {
						td {
							border-bottom: none;

							.btn_delete_ord {
								right: 31px;
							}

							&:after {
								content: '';
								position: absolute;
								display: block;
								bottom: 0;
								left: 0;
								right: 0;
								border-bottom: 1px dashed variables.$colorGray04;
							}

							&:first-child {
								padding-left: 24px;

								&:after {
									left: 24px;
								}
							}

							&:last-child {
								padding-left: 24px;

								&:after {
									right: 24px;
								}
							}
						}

						&:last-child {
							td {
								&:after {
									content: none;
								}
							}
						}
					}
				}

				tfoot {
					tr {
						td {
							border-top: 1px solid variables.$colorGray04;
						}
					}
				}
			}

			+ .type_package {
				margin-top: 20px;
			}
		}
	}

	&.type_myroom{
		border-top: 1px solid variables.$textColorBlack;

		// 주문, 배송 목록 상단
		.tbl_order_info{
			height: 63px;
			padding: 0 10px;
			@include mixins.justify_width(center);
			border-bottom: 1px solid variables.$colorGray04;
			box-sizing: border-box;

			.order_date{
				padding-top: 1px;
				@include mixins.font_size_def;
				font-weight: variables.$fontWeightBold;
			}

			.btn_more_view{
				margin-left: 10px;
			}

			.right_area{
				margin-left: auto;
				font-size: 0;
			}
		}

		// 주문, 배송 상세 상단
		.tbl_summary{
			@include mixins.justify_width(center);
			height: 64px;
			padding: 0 10px;
			@include mixins.font_size_def;
			border-bottom: 1px solid variables.$colorGray02;
			font-weight: variables.$fontWeightBold;
			box-sizing: border-box;
		}

		.tbl_prod_type{
			@include mixins.justify_width(center);
			height: 60px;
			padding: 0 10px;
			@include mixins.font_size_xs;
			border-bottom: 1px solid variables.$colorGray04;
			font-weight: variables.$fontWeightBold;
			box-sizing: border-box;
		}

		.tbl_prod{
			border: none;

			tbody{
				tr{
					td{
						padding: 24px 10px;
						border-bottom: none;

						.delivery_info{
							text-align: center;

							.delivery_desc{
								margin-top: 5px;
								word-break: keep-all;
							}

							.btn_wrap{
								&.auto{
									margin-top: 10px;
								}
							}
						}

						.btn_wrap{
							&.full{
								margin-top: 0;

								.btn_sm{
									position: relative;
									padding: 0 10px 1px;

									.badge_primary_speech{
										position: absolute;
										right: -10px;
										top: -10px;
									}
								}

								.btn_select{
									&.active{
										border-color: variables.$textColorLightGray;
										background-color: variables.$textColorLightGray;

										.ico_check{
											@include mixins.ratio_images('../images/ico_check_white_h18', 'png', 10px, 18px);
										}

										.text{
											color: #fff;
										}
									}
								}
							}
						}

						&.prod{
							vertical-align: middle;

							.prod_area{
								align-items: center;

								.prod_info_box{
									.bul_item_asterisk{
										margin-top: 5px;
										word-break: keep-all;
									}

									.prod_link_wrap {
										margin-top: 12px;
									}

								}
							}
						}

						&.only_chk {
							+ .prod{
								padding-left: 0;
							}
						}

						&.align_right{
							text-align: right;
						}

						&.td_left_line{
							border-left: 1px solid variables.$colorGray04;
							padding-left: 20px;
						}

						~ td{
							border-left: none;
						}
					}

					// 패키지상품
					&.package_prod{
						td{
							border-bottom: none;
							text-align: left;

							.price{
								display: block;
								width: 144px;
								text-align: center;
							}

							&.align_right{
								text-align: right;

								.price{
									margin-left: auto;
									text-align: right;
								}
							}

						}
					}

					// 패키지상품 옵션
					&.package_option{
						border-bottom: none;

						> td{
							padding: 0 10px 24px;
							border-top: none;

							.package_tbl_wrap{
								border: 1px solid variables.$colorGray04;
								border-radius: 16px;
								overflow: hidden;
								background-color: rgba(variables.$colorGray06, .5);

								.tbl_prod{
									border-bottom: none;

									tr{
										td{
											&:first-child{
												padding-left: 23px;
											}

											&:last-child{
												padding-right: 23px;
											}
										}

										&:first-child {
											td {
												border-top: none;
											}
										}

										&:last-child{
											td{
												border-bottom: none;
											}
										}
									}
								}
							}
						}
					}

					~ tr {
						td {
							border-top: 1px solid variables.$colorGray04;
						}
					}
				}
			}

			&:last-child{
				border-bottom: 1px solid variables.$colorGray04;
			}
		}

		.tbl_prod_inner{
			.tbl_prod{
				border-bottom-color: variables.$colorGray02;
			}

			&:last-child{
				.tbl_prod{
					border-bottom-color: variables.$colorGray04;
				}
			}
		}

		.tbl_myorder_inner{
			.tbl_prod{
				border-bottom: none;
			}

			&:last-child{
				.tbl_prod{
					border-bottom: 1px solid variables.$colorGray04;
				}
			}

			~ .tbl_myorder_inner{
				border-top: 1px solid variables.$textColorBlack;
			}
		}

		.tbl_notice_box{
			padding: 10px 10px 24px;
			border-top: 1px solid variables.$colorGray04;
			border-bottom: 1px solid variables.$colorGray04;
		}

		+ .type_myroom{
			margin-top: 30px;
		}

		+ .payment_info_wrap{
			margin-top: 30px;
		}

		+ .tbl_row_wrap{
			margin-top: 30px;
		}
	}

	+ .btn_wrap {
		margin-top: 16px;
	}
}

.tbl_row_wrap {
	.address_bring {
		width: 450px;
	}
}