@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
$inputProps: (
		'border-radius': 6px,
		'padding': 0 23px,
);

/* form 공통 */
.form_label {
	display: inline-block;
	color: variables.$textColorBlack;
	@include mixins.font_size_xxs;
	font-weight: variables.$fontWeightMedium;
	cursor: default;

	.required {
		display: inline-block;
		width: 6px;
		height: 6px;
		padding-left: 3px;
		margin-top: 5px;
		vertical-align: top;

		&:before {
			content: '*';
			display: inline-block;
			position: relative;
			width: 6px;
			height: 6px;
			@include mixins.ratio_images('../images/ico_required', 'png', 6px, 6px);
			color: transparent;
		}
	}

	.desc_gray {
		color: variables.$textColorLightGray;
	}
}

/* form 공통 - 하단 메시지 */
.form_desc {
	display: block;
	position: relative;
	margin-top: 6px;
	@include mixins.font_size_xxs;
	color: variables.$textColorLightGray;

	&.tip {
		padding-left: 33px;

		&:before {
			content: '';
			display: inline-block;
			position: absolute;
			left: 0;
			top: 0;
			width: 28px;
			height: 18px;
			background: {
				position: left top;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_tip', 'png', 28px, 18px);
		}
	}

	&.align_right {
		text-align: right;
	}

	.delivery_fee {
		color: variables.$textColorBlack;
		font-weight: variables.$fontWeightMedium;

		.unit {
			color: variables.$textColorBlack;
			font-weight: variables.$fontWeightRegular;
		}
	}

	.btn_info_popup {
		margin-top: 1px;
		margin-left: 3px;
	}

	.price_value {
		color: #000;
		@include mixins.font_size_xs;

		.price {
			display: inline-block;
			vertical-align: top;
			font-size: 0;

			.val, .unit {
				@include mixins.font_size_xs;
				vertical-align: top;
			}

			.val {
				font-weight: variables.$fontWeightBold;
			}
		}
	}
}

/* form - input */
[class^=form_] {
	&::-ms-clear,
	&::-ms-reveal {
		display: none;
	}

	&::-webkit-search-decoration,
	&::-webkit-search-cancel-button,
	&::-webkit-search-results-button,
	&::-webkit-search-results-decoration {
		display: none;
	}

	&[type=number] {
		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}

	&[type=date], &[type=month], &[type=time], &[type=week] {
		&::-webkit-calendar-picker-indicator {
			background: none;
		}
	}

	&[type=search] {
		&::-webkit-search-cancel-button,
		&::-webkit-search-results-button,
		&::-webkit-search-results-decoration {
			display: none;
			width: 0;
			height: 0;
		}
	}
}
.form_ip {
	display: inline-block;
	width: 100%;
	height: 44px;
	padding: 0 14px 2px;
	color: variables.$textColorBlack;
	font-size: variables.$fontSizeXs;
	line-height: 42px;
	letter-spacing: variables.$letterSpacing;
	font-family: variables.$fontKor;
	background-color: #fff;
	border: 1px solid variables.$colorGray03;
	border-radius: 6px;
	box-sizing: border-box;
	@include mixins.transition(border-color .2s ease-out);
	outline: none;

	&:active, &:focus {
		border-color: #000;
	}

	&[disabled], &[aria-disabled], &[readonly] {
		color: variables.$textColorLightGray;
		background-color: variables.$colorGray06;
		pointer-events: none;
		cursor: default;
	}

	@include mixins.set_placeholder_color(variables.$textColorLightGray);

	// 여백
	& ~ & {
		margin-top: 8px;
	}

	& ~ .form_box, & ~ .form_item {
		margin-top: 20px;
	}

	&.align_right {
		text-align: right;
	}
}

/* input password 숨김/보임 */
.form_ip_pw {
	position: relative;

	.form_ip {
		padding: 0 48px 0 14px;
	}

	.btn_toggle_pw {
		display: inline-block;
		position: absolute;
		right: 14px;
		top: 11px;
		width: 22px;
		height: 20px;
		@include mixins.ratio_images('../images/ico_eye', 'png', 22px, 20px);
		@include mixins.transition(background 0.2s ease-out);

		&.active {
			@include mixins.ratio_images('../images/ico_eye_active', 'png', 22px, 20px);
		}
	}

	& ~ & {
		margin-top: 8px;
	}
}

/* input - 검색 */
.form_ip_search {
	position: relative;

	.btn_ip_clear {
		display: none;
		position: absolute;
		right: 14px;
		top: 50%;
		width: 18px;
		height: 18px;
		@include mixins.transform(translateY(-50%));
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/btn_ip_reset', 'png', 18px, 18px);
	}

	.form_ip {
		padding-right: 45px;
	}

	&.value {
		.form_ip {
			padding-right: 85px;
		}

		.btn_ip_clear {
			display: block;
		}
	}
}
.form_search_box {
	@include mixins.justify_width(center);
	position: relative;
	width: 100%;

	.form_ip_search {
		flex: 1;

		.btn_ip_clear {
			right: 50px;
		}
	}

	.btn_ip_search {
		display: block;
		flex: 0 auto;
		position: absolute;
		right: 14px;
		top: 50%;
		width: 24px;
		height: 24px;
		transform: translateY(-50%);
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/btn_ip_search', 'png', 24px, 24px);
	}

	+ .form_search_chk_box{
		margin-top: 10px;
		text-align: right;
	}

	&.type_sm {
		.form_ip_search {
			.form_ip {
				height: 38px;
				line-height: 36px;
				font-size: variables.$fontSizeXxs;
			}

			.btn_ip_clear {
				right: 40px;
				width: 14px;
				height: 14px;
				@include mixins.ratio_images('../images/btn_ip_reset_sm', 'png', 14px, 14px);
			}

			&.value {
				.form_ip {
					padding-right: 62px;
				}
			}
		}

		.btn_ip_search {
			width: 18px;
			height: 18px;
			@include mixins.ratio_images('../images/btn_ip_search_sm', 'png', 18px, 18px);
		}
	}

	&.has_sel {
		border: 1px solid variables.$colorGray03;
		border-radius: 6px;
		box-sizing: border-box;

		.form_sel .ui-selectmenu-button, .form_ip_search .form_ip {
			border: none;
		}
		.form_sel {
			width: auto;
		}
		.form_ip_search {
			&:before {
				content: '';
				position: absolute;
				display: inline-block;
				width: 1px;
				height: 12px;
				background: variables.$colorGray04;
				left: 0;
				top: 16px;
			}
		}
		.btn_ip_search {

		}
	}

	+ .list_result_wrap {
		margin-top: 20px;
	}
}

.form_prefix_box {
	position: relative;

	.prefix_unit {
		position: absolute;
		left: 14px;
		top: 50%;
		padding-top: 1px;
		color: variables.$textColorBlack;
		@include mixins.font_size_xs;
		@include mixins.transform(translateY(-50%));
	}

	.form_ip {
		padding: 0 14px 2px 28px;
	}
}

/* select */
// select button 영역
.form_sel {
	display: inline-block;
	width: 450px;

	.ui-selectmenu-button {
		display: inline-block;
		position: relative;
		width: 100%;
		height: 44px;
		margin: 0;
		padding: 0 14px;
		background-color: #fff;
		border: 1px solid variables.$colorGray03;
		border-radius: 6px;
		box-sizing: border-box;
		@include mixins.transition(border-color .2s ease-out, border-radius .2s ease-out);
		outline: none;

		&:focus {
			@include mixins.focus_outline;
		}

		&:active, &:focus {
			border-color: #000;
		}

		.ui-selectmenu-text {
			font-size: variables.$fontSizeXs;
			line-height: 42px;
			letter-spacing: variables.$letterSpacing;
			color: variables.$textColorBlack;
			font-family: variables.$fontKor;
		}

		.ui-selectmenu-icon {
			position: absolute;
			right: 14px;
			top: 17px;
			width: 9px;
			height: 8px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/arw_select', 'png', 9px, 8px);
			@include mixins.transform(rotate(0));
			@include mixins.transition(transform 0.2s ease-out);
		}

		&.ui-selectmenu-button-open {
			border-color: #000;
			border-radius: 6px 6px 0 0;

			.ui-selectmenu-icon {
				@include mixins.transform(rotate(180deg));
			}
		}

		&.ui-selectmenu-disabled {
			background-color: variables.$colorGray06;

			.ui-selectmenu-text {
				color: variables.$textColorLightGray;
			}
		}
	}

	// select - sm type button
	&.type_sm {
		width: 120px;

		.ui-selectmenu-button {
			width: 100%;
			height: 38px;

			.ui-selectmenu-text {
				font-size: variables.$fontSizeXxs;
				line-height: 36px;
			}

			.ui-selectmenu-icon {
				top: 14px;
			}
		}
	}

	// select - arw type button
	&.type_arw {
		width: auto;

		.ui-selectmenu-button {
			width: auto;
			height: 19px;
			padding: 0;
			border: none;
			border-radius: 0;

			.ui-selectmenu-text {
				display: inline-block;
				margin-right: 23px;
				@include mixins.font_size_xxs;
			}

			.ui-selectmenu-icon {
				top: 0;
				right: 0;
				width: 19px;
				height: 19px;
				border: 1px solid variables.$colorGray03;
				border-radius: 50%;
				box-sizing: border-box;
				background: {
					position: center 7px;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/arw_select_sm', 'png', 7px, 4px);
				@include mixins.transition(transform 0.2s ease-out, border-color 0.2s ease-out);
			}

			&.ui-selectmenu-button-open {
				.ui-selectmenu-icon {
					border-color: #000;
				}
			}

			// TODO : 디자인팀 문의 후 수정 필요
			&.ui-selectmenu-disabled {
				background-color: variables.$colorGray06;

				.ui-selectmenu-text {
					color: variables.$textColorLightGray;
				}
			}
		}

		& ~ &.type_arw {
			margin-left: 12px;
		}
	}

	// select - width full type
	&.w_full {
		width: 100%;

		.ui-selectmenu-button {
			width: 100%;
		}
	}

	+ .form_chk, + .form_rdo {
		margin-top: 10px;
	}

	+ .byte_check_wrap {
		margin-top: 8px;
	}
}

.sel_text_box {
	@include mixins.justify_width(center);

	.form_sel {
		width: 120px;

		& + .text {
			margin-left: 12px;
		}
	}
}

/* radio/check */
.form_rdo, .form_chk {
	display: inline-block;
	position: relative;
	min-height: 22px;
	vertical-align: top;

	@include mixins.focus_within;

	input {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;

		+ label {
			display: inline-block;
			position: relative;
			padding: 2px 0 0 27px;
			font-size: variables.$fontSizeXs;
			letter-spacing: variables.$letterSpacing;
			line-height: 20px;
			color: variables.$textColorBlack;
			cursor: pointer;
			vertical-align: top;
			@include mixins.transition(color .2s ease-out);
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;

			&:before,
			&:after {
				content: '';
				display: inline-block;
				position: absolute;
				left: 0;
				top: 0;
				width: 22px;
				height: 22px;
				text-align: center;
				border-radius: 50%;
				box-sizing: border-box;
			}

			.desc_gray {
				color: variables.$textColorLightGray;
			}
		}

		&[disabled], &[aria-disabled], &[readonly] {
			& + label {
				color: variables.$textColorLightGray;
				pointer-events: none;

				&:before {
					background-color: variables.$colorGray06;
					border-color: variables.$colorGray04;
				}
			}
		}

		&:checked {
			& + label {
				&:before {
					background-color: variables.$kyoboPurple;
					border-color: variables.$kyoboPurple;
				}
			}

			&[disabled], &[aria-disabled], &[readonly] {
				& + label {
					&:before {
						background-color: #b8badf;
						border-color: #b8badf;
					}
				}
			}
		}
	}

	&.no_label {
		width: 22px;
		height: 22px;
		min-height: auto;

		input {
			+ label {
				position: static;
				padding-left: 0;
				font-size: 0;
			}
		}
	}

	&.type_lg{
		input{
			+ label{
				padding-top: 0;
				@include mixins.font_size_def;
				font-weight: variables.$fontWeightBold;
			}
		}
	}

	& + .info_text_box {
		margin-top: 20px;
	}
}

.form_chk {
	input {
		+ label {
			&:before {
				background-color: #fff;
				border: 1px solid variables.$colorGray02;
				@include mixins.transition(border-color 0.2s ease-out, background-color 0.2s ease-out);
			}

			&:after {
				background: {
					position: center 8px;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_checkbox', 'png', 10px, 7px);
			}

			.spot {
				font-weight: variables.$fontWeightMedium;
				text-decoration: underline;
			}
		}

		&:checked {
			& + label {
				&:after {
					@include mixins.ratio_images('../images/ico_checkbox_active', 'png', 10px, 7px);
				}
			}
		}

		&[disabled], &[aria-disabled], &[readonly] {
			& + label {
				&:after {
					@include mixins.ratio_images('../images/ico_checkbox_disabled', 'png', 10px, 7px);
				}
			}
		}
	}
}

.form_rdo {
	input {
		+ label {
			&:before {
				background-color: #fff;
				border: 1px solid variables.$colorGray02;
				@include mixins.transition(border-color 0.2s ease-out, background-color 0.2s ease-out);
			}

			&:after {
				content: '';
				position: absolute;
				left: 6px;
				top: 6px;
				width: 10px;
				height: 10px;
				background-color: variables.$colorGray02;
				@include mixins.transition(background 0.2s ease-out);
			}
		}

		&:checked {
			& + label {
				&:after {
					background-color: #fff;
				}
			}
		}

		&[disabled], &[aria-disabled], &[readonly] {
			& + label {
				&:after {
					background-color: variables.$colorGray04;
				}
			}
		}
	}
}

.form_filter_box {
	@include mixins.justify_width(center, center, row, true);

	.form_filter {
		flex: 1 auto;

		input {
			position: absolute;
			width: 1px;
			height: 1px;
			padding: 0;
			margin: -1px;
			overflow: hidden;
			clip: rect(0, 0, 0, 0);
			border: 0;

			+ label {
				@include mixins.justify_width(center, center, row, true);
				position: relative;
				z-index: 1;
				min-width: 81px;
				width: 100%;
				height: 44px;
				padding: 0 16px;
				color: variables.$textColorLightGray;
				@include mixins.font_size_xs;
				font-family: variables.$fontKor;
				background-color: #fff;
				border: 1px solid variables.$colorGray03;
				box-sizing: border-box;
				text-align: center;
				cursor: pointer;
				vertical-align: top;
				@include mixins.transition(border-color 0.2s ease-out);
				-webkit-user-select: none;
				-moz-user-select: none;
				-ms-user-select: none;

				.text {
					position: relative;
					padding-left: 14px;
					text-align: left;

					&:before {
						content: '';
						position: absolute;
						left: 0;
						top: 50%;
						width: 10px;
						height: 7px;
						margin-top: -3px;
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/ico_checkbox', 'png', 10px, 7px);
					}
				}
			}

			&:checked {
				& + label {
					z-index: 2;
					border-color: variables.$kyoboPurple;
					color: variables.$textColorPurple;

					.text {
						&:before {
							@include mixins.ratio_images('../images/ico_checkbox_active_02', 'png', 10px, 7px);
						}
					}
				}
			}

			&[disabled], &[aria-disabled], &[readonly] {
				& + label {
					background-color: variables.$colorGray06;
					border-color: variables.$colorGray03;
					color: variables.$textColorLightGray;
					pointer-events: none;

					.text {
						&:before {
							@include mixins.ratio_images('../images/ico_checkbox_disabled', 'png', 10px, 7px);
						}
					}
				}

				&:checked {
					& + label {
						background-color: #ededf7;
						border-color: #a6a9d7;
						color: rgba(variables.$textColorPurple, .5);;

						.text {
							&:before {
								@include mixins.ratio_images('../images/ico_checkbox_active_disabled', 'png', 10px, 7px);
							}
						}
					}
				}
			}
		}

		&:first-child {
			input + label {
				border-radius: 6px 0 0 6px;
			}
		}

		&:last-child {
			input + label {
				border-radius: 0 6px 6px 0;
			}
		}

		&:only-child {
			input + label {
				border-radius: 6px;
			}
		}

		& ~ .form_filter {
			margin-left: -1px;
		}
	}



	&.type_sm {
		.form_filter input + label {
			min-width: auto;
			height: 38px;
			padding: 0 14px;
			@include mixins.font_size_xxs;

			.text {
				padding-left: 0;

				&:before {
					content: none;
				}
			}
		}
	}
}

/* checkbox & radio row list */
.chk_row_list {
	margin-top: -16px;
	font-size: 0;

	.chk_row_item {
		display: inline-block;
		margin-top: 16px;
		margin-right: 24px;

		&:last-child {
			margin-right: 0;
		}

		> .chk_row_list {
			margin: 0 0 0 27px;

			.chk_row_item {
				margin-top: 12px;
			}
		}

		> .chk_col_list {
			margin: 12px 0 0 27px;
		}
	}

	// 2단 (균등)
	&.col2 {
		.chk_row_item {
			margin-right: 0;
			width: calc((100% - 24px) / 2);

			& ~ .chk_row_item {
				margin-left: 24px;
			}

			&:nth-child(2n+1){
				margin-left: 0;
			}
		}
	}

	// 4단 (균등)
	&.col4 {
		.chk_row_item {
			margin-right: 0;
			width: calc((100% - 24px * 3) / 4);

			& ~ .chk_row_item {
				margin-left: 24px;
			}

			&:nth-child(4n+1){
				margin-left: 0;
			}
		}
	}
}

/* checkbox & radio col list */
.chk_col_list {
	.chk_col_item {
		& ~ .chk_col_item {
			margin-top: 16px;
		}

		> .chk_row_list {
			margin: 0 0 0 27px;

			.chk_row_item {
				margin-top: 12px;
			}
		}

		> .chk_col_list {
			margin: 12px 0 0 27px;
		}
	}
}

/* checkbox & radio - 약관 동의 폼 */
.agree_chk_wrap {
	.agree_contents_area {
		& + .agree_form_area {
			margin-top: 20px;
		}
	}
	.agree_form_area {
		display: block;
		text-align: center;

		.agree_info_text {
			display: block;
			@include mixins.font_size_xs;
			color: variables.$textColorBlack;
			vertical-align: top;

			& + .agree_info_text {
				margin-top: 10px;
			}

			&.size_md {
				@include mixins.font_size_sm;
				font-weight: variables.$fontWeightBold;
			}

			& ~ .chk_row_list {
				padding-top: 20px;
