/* textarea */
.form_textarea {
	width: 100%;
	height: 150px;
	padding: 12px 14px;
	color: variables.$textColorBlack;
	border: 1px solid variables.$colorGray03;
	border-radius: 6px;
	@include mixins.font_size_xs;
	font-family: variables.$fontKor;
	background-color: #fff;
	box-sizing: border-box;
	@include mixins.transition(background 0.2s ease-out, border-color .2s ease-out);
	outline: 0;
	overflow-y: auto;
	resize: none;

	@include mixins.set_placeholder_color(variables.$textColorLightGray);

	&:active, &:focus {
		border-color: #000;
	}

	&[disabled], &[aria-disabled], &[readonly] {
		color: variables.$textColorLightGray;
		background-color: variables.$colorGray06;
		pointer-events: none;
		cursor: default;
	}
}
/* byte_check */
.byte_check_wrap {
	display: flex;
	flex-direction: column;
	position: relative;
	overflow: hidden;
	border: 1px solid variables.$colorGray03;
	border-radius: 6px;
	font-size: 0;
	background: #fff;

	.form_textarea {
		border: none;
		padding: 11px 14px 0;

		&[disabled], &[aria-disabled], &[readonly] {
			& + .byte_check_footer {
				color: variables.$textColorLightGray;
				background-color: variables.$colorGray06;
			}
		}
	}

	.byte_check {
		display: inline-block;
		line-height: 1;
		font-size: 0;

		.count, .total {
			display: inline-block;
			@include mixins.font_size_min;
		}

		.count {
			display: inline-block;
			color: variables.$textColorDarkGray;
		}

		.total {
			color: variables.$textColorLightGray;

			&:before {
				content: '/';
			}
		}
	}

	.byte_check_footer {
		@include mixins.justify_width(center, end);
		padding: 10px 10px 10px 14px;
		text-align: right;
		@include mixins.transition(background 0.2s ease-out);

		.byte_check {
			margin-top: 2px;
		}

		.btn_wrap {
			display: inline-block;

			[class^=btn_] + [class^=btn_] {
				margin-left: 4px;
			}
		}

		.byte_check + .btn_wrap {
			margin-left: 10px;
		}
	}

	&.type_ip {
		border: none;

		.form_ip {
			padding-right: 50px;
		}

		.byte_check {
			position: absolute;
			right: 14px;
			top: 50%;
			@include mixins.transform(translateY(-50%));
		}
	}

	+ .bul_item_asterisk {
		margin-top: 10px;
	}

	+ .form_chk, + .form_rdo {
		margin-top: 10px;
	}
}

/* form - toggle */
.form_toggle {
	position: relative;
	display: inline-block;
	width: 40px;
	height: 22px;
	vertical-align: top;
	box-sizing: border-box;
	@include mixins.focus_within;

	input[type=checkbox] {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;

		& + label {
			display: inline-block;
			width: 100%;
			height: 100%;
			background: variables.$colorGray04;
			border-radius: 22px;
			font-size: 0;
			@include mixins.transition(background 0.2s ease-out);

			&:before {
				content: '';
				position: absolute;
				width: 18px;
				height: 18px;
				background: #fff;
				border-radius: 50%;
				left: 2px;
				top: 2px;
				z-index: 2;
				@include mixins.transition(all 0.2s ease-out);
			}
		}

		&:checked {
			& + label {
				background: variables.$kyoboPurple;

				&:before {
					left: 100%;
					margin-left: -20px;
				}
			}
		}

		&[disabled], &[aria-disabled], &[readonly] {
			pointer-events: none;

			& + label {
				opacity: .5; // TODO 추후 디자인 정의되면 수정 필요
				cursor: default;
			}
		}
	}
}

.form_toggle_box {
	display: inline-block;
	font-size: 0;
	box-sizing: border-box;
	vertical-align: top;

	.toggle_text {
		display: inline-block;
		padding-top: 3px;
		font-size: variables.$fontSizeXs;
		line-height: 19px;
		letter-spacing: variables.$letterSpacing;
		color: variables.$textColorLightGray;
		vertical-align: top;

		.date {
			padding-right: 4px;
		}

		.val {
			font-weight: variables.$fontWeightBold;
			@include mixins.transition(color 0.2s ease-out);
		}

		& + .form_toggle {
			margin-left: 20px;
		}
	}

	&.active {
		.toggle_text {
			.date {
				color: variables.$textColorBlack;
			}

			.val {
				color: variables.$textColorPurple;
			}
		}
	}
}

/* form - 유효성 검사 */
.valid_check {
	.valid_desc {
		display: none;
		position: relative;
		margin-top: 4px;
		@include mixins.font_size_xxs;
		color: variables.$textColorLightGray;

		button {
			cursor: pointer;
			&:after {
				content: '';
				display: inline-block;
				width: 5px;
				height: 9px;
				margin-left: 4px;
				@include mixins.ratio_images('../images/arw_shop', 'png', 5px, 9px);
			}
		}
	}

	.form_sel ~ .valid_desc {
		display: block;
	}

	&.success {
		.form_ip, .ip_group_box, .byte_check_wrap, .form_textarea {
			border: 1px solid variables.$kyoboGreen;
		}

		.form_sel {
			.ui-selectmenu-button {
				border: 1px solid variables.$kyoboGreen;
			}
		}

		.valid_desc {
			display: block;
			color: variables.$textColorGreen;
		}

		.byte_check_wrap {
			.form_ip {
				border: none;
			}

			.form_textarea {
				border: none;
			}
		}
	}

	&.fail {
		.form_ip, .ip_group_box, .byte_check_wrap, .form_textarea {
			border: 1px solid variables.$textColorRed;
		}

		.form_sel {
			.ui-selectmenu-button {
				border: 1px solid variables.$textColorRed;
			}
		}

		.valid_desc {
			display: block;
			padding-left: 16px;
			color: variables.$textColorRed;

			&:before {
				content: '';
				display: inline-block;
				position: absolute;
				left: 0;
				top: 3px;
				width: 12px;
				height: 12px;
				background: {
					position: left top;
				}
				@include mixins.ratio_images('../images/ico_warning', 'png', 12px, 12px);
			}
		}

		.byte_check_wrap {
			.form_ip {
				border: none;
			}

			.form_textarea {
				border: none;
			}
		}
	}

	// 여백
	& ~ & {
		margin-top: 20px;
	}
}

