/* form - 첨부파일 */
.file_list {
	font-size: 0;

	.list_item {
		display: inline-block;
		margin-right: 10px;
		vertical-align: top;

		&:last-child {
			margin-right: 0;
		}
	}

	.file_item {
		position: relative;
		display: inline-block;

		.btn_box {
			position: relative;
			display: inline-block;
			width: 94px;
			height: 94px;
			border: 1px dashed #e5e5e5;
			cursor: pointer;
			border-radius: 6px;
			vertical-align: top;
			box-sizing: border-box;

			input[type=file] {
				/* 파일 필드 숨기기 */
				position: absolute;
				width: 1px;
				height: 1px;
				padding: 0;
				margin: -1px;
				overflow: hidden;
				clip: rect(0, 0, 0, 0);
				border: 0;
			}

			label {
				display: block;
				width: 100%;
				height: 100%;
				font-size: 0 !important;
				@include mixins.ratio_images('../images/btn_attached_add', 'png', 16px, 16px);
				background: {
					position: center;
					repeat: no-repeat;
				}
			}
		}

		&.attached {
			.btn_box {
				border: none;
			}

			label {
				display: none;
			}

			.attach_img_box {
				position: absolute;
				display: none;
				width: 100%;
				height: 100%;
				cursor: default;
				left: 0;
				top: 0;
				z-index: 1;

				.attach_img_view {
					position: absolute;
					display: inline-block;
					width: 100%;
					height: 100%;
					background: {
						position: center;
						size: cover;
					}
					font-size: 0 !important;
					left: 0;
					top: 0;
				}

				.btn_remove_img {
					position: absolute;
					width: 22px;
					height: 22px;
					@include mixins.ratio_images('../images/btn_attached_remove', 'png', 8px, 8px);
					background: {
						position: center;
						repeat: no-repeat;
					}
					background-color: rgba(variables.$textColorBlack, .6);
					border-radius: 0 6px;
					font-size: 0 !important;
					pointer-events: auto;
					right: 0;
					top: 0;
				}
			}

			&.attached {
				.attach_img_box {
					display: inline-block;
				}
			}
		}
	}

	~ .bul_item_asterisk {
		margin-top: 10px;
	}
}

