			border-color: #000;
		}

		&[disabled], &[aria-disabled], &[readonly] {
			color: variables.$textColorLightGray;
			background-color: variables.$colorGray06;
			pointer-events: none;
			cursor: default;
		}

		@include mixins.set_placeholder_color(variables.$textColorLightGray);
	}

	.ui-datepicker-trigger {
		position: absolute;
		right: 14px;
		top: 12px;
		cursor: pointer;
		width: 20px;
		height: 20px;
	}
}

.date_picker_box {
	@include mixins.justify_width;

	.form_sel {
		flex-shrink: 0;
		width: 126px;

		.ui-selectmenu-button {
			width: 100%;
		}

		+ .date_box {
			margin-left: 6px;
		}
	}

	.date_box {
		flex: 1;

		& + .btn_ip {
			margin-left: 6px;
		}
	}

	.gap {
		display: inline-block;
		padding: 0 4px;
		font-size: variables.$fontSizeSm;
		color: variables.$textColorBlack;
		line-height: 42px;
		vertical-align: top;
		text-align: center;
		padding-bottom: 2px;
	}
}

// form select + input
.form_sort_search {
	@include mixins.justify_width(center, center);

	.form_sel {
		flex: 0 1 130px;
	}
	.form_ip {
		flex: 1;
		margin-left: 6px;
	}
}