@charset "UTF-8";

/* 버튼 */

button[class^=btn_],
a[class^=btn_] {
  display: inline-block;
  box-sizing: border-box;
  vertical-align: top;
}

button[class^=btn_]:hover,
a[class^=btn_]:hover {
  cursor: pointer;
}

button[class^=btn_][disabled],
button[class^=btn_][aria-disabled],
button[class^=btn_].disabled,
a[class^=btn_][disabled],
a[class^=btn_][aria-disabled],
a[class^=btn_].disabled {
  pointer-events: none;
  cursor: default;
}

button[class^=btn_] [class^=ico_],
a[class^=btn_] [class^=ico_] {
  display: inline-block;
  position: relative;
  vertical-align: top;
}

button[class^=btn_] .text,
a[class^=btn_] .text {
  display: inline-block;
  position: relative;
  vertical-align: top;
}

