@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';

// 이벤트 상세
.view_detail_wrap {
	> .btn_wrap {
		margin-top: 70px;
	}

	.btn_wrap {
		&.page_bottom {
			margin-top: 70px;
		}
	}
}
.view_detail_header {
	margin-bottom: 36px;

	.category {
		margin-bottom: 5px;
		font-weight: variables.$fontWeightBold;
		color: variables.$textColorLightGray;
	}

	.title_wrap {
		@include mixins.justify_width(start, center, column);
		padding-right: 240px;
		padding-bottom: 0;

		.right_area {
			position: absolute;
			right: 0;
			top: 0;
		}

		// TODO : 추후 이벤트 상세 기간 영역 마크업 현행화 후 삭제 필요
		&.has_sub_title {
			padding-bottom: 0;
		}
	}

	.edit_info_case {
		margin-top: 10px;
		font-size: 0;

		.flag_state {
			margin-right: 8px;

			&:last-child {
				margin: 0;
			}

			& ~ .date,
			& ~ .publish {
				margin-top: 2px;
			}
			& ~ .gap {
				margin-top: 9px;
			}
		}

		.date {
			display: inline-block;
			@include mixins.font_size_xs;
			color: variables.$textColorLightGray;
			vertical-align: top;
		}

		.gap {
			display: inline-block;
			font-size: 0;
			width: 1px;
			height: 7px;
			background-color: variables.$colorGray03;
			margin: 7px 8px 0;
			box-sizing: border-box;
			vertical-align: top;
		}

		.publish {
			display: inline-block;
			padding-top: 1px;
			@include mixins.font_size_xxs;
			color: variables.$textColorGray;
			vertical-align: top;
		}
	}
}
.view_detail_body {
	&.has_line {
		padding: 30px 10px 40px;
		border: {
			style: solid;
			color: variables.$colorGray04;
			width: 1px 0;
		}
	}

	.title_wrap.title_size_md {
		margin-top: 70px; // 댓글 타이틀 상단 여백
	}

	.no_data {
		padding: 40px 0 89px;
		border-bottom: 1px solid variables.$colorGray04;
	}

	> .btn_wrap {
		margin-top: 20px;
	}

	.detail_img_box {
		display: block;
		margin: 30px 0;

		img {
			max-width: 100%;
		}
	}

	&.event_end {
		.inner {
			.img_box {
				position: relative;

				&:before {
					content: '';
					position: absolute;
					left: 0;
					right: 0;
					bottom: 0;
					top: 0;
					background-color: rgba(variables.$textColorBlack, .5);
				}
			}
		}
	}

	+ .contents_inner {
		.btn_wrap {
			margin-top: 20px;
		}
	}
}
.view_detail_append {
	@include mixins.justify_width(center);
	margin-top: 20px;

	.btn_xxs {
		margin-left: 6px;
	}
}

// BO 등록 컨텐츠
.bo_contents {
	margin-top: 60px;

	&:first-child {
		margin-top: 0;
	}

	.imgs {
		overflow: hidden;
		text-align: center;
	}
	.inner {
		display: inline-block;
		position: relative;
		min-width: 100%;
		left: 50%;
		@include mixins.transform(translateX(-50%));
	}
	.img_box {
		overflow: hidden;
		position: relative;
		display: inline-block;
		border-radius: 20px;

		&.no_radius {
			border-radius: 0;
		}
	}
	.btn_wrap {
		margin-top: 60px;
	}

	.event_end_text {
		position: absolute;
		width: 1200px;
		display: inline-block;
		left: 50%;
		top: 0;
		transform: translateX(-50%);

		.event_end_text_inner {
			position: absolute;
			right: 34px;
			top: 30px;
			@include mixins.font_size_sm;
			color: #fff;

			[class^=ico_] {
				margin-right: 8px;
			}
		}
	}
}

// 에디터 상세
.edit_write_case {
	margin: 30px 0 0;
	@include mixins.font_size_xs();

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}

	& + .btn_wrap {
		text-align: left;
	}

	& + .book_survey_area {
		margin-top: 70px;
	}

	& + .info_text_box {
		margin-top: 30px;
	}
}

.prize_list_box {
	width: 700px;
	margin: 30px 0 10px;

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}

	.edit_write_case {
		margin-bottom: 14px;
	}

	ul {
		@include mixins.justify_width();
		flex-wrap: wrap;
		border-top: 1px solid #000;
		border-bottom: 1px solid variables.$colorGray04;

		li {
			@include mixins.justify_width();
			box-sizing: border-box;
			width: 50%;
			padding: 20px 12px;
			@include mixins.font_size_xs();

			.user_name {
				width: 100px;
				font-weight: variables.$fontWeightMedium;
			}

			.user_id {
				color: variables.$textColorLightGray;
			}
		}
	}

	&:first-child {
		margin: 0;
	}
}

/* 우리가 만드는 책 */
// 설문 진행 상태
.survey_status_box {
	@include mixins.justify_width(center);
	padding: 18px;
	background-color: variables.$colorGray06;
	border-radius: 10px;
	box-sizing: border-box;

	.survey_status {
		display: inline-block;
		vertical-align: top;
		font-size: 0;

		.ico_survey_status {
			display: inline-block;
			width: 25px;
			height: 24px;
			margin-right: 4px;
			vertical-align: top;
			background: {
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_survey_ing', 'png', 25px, 24px);
		}

		.status {
			display: inline-block;
			padding-top: 2px;
			@include mixins.font_size_def;
			color: variables.$textColorBlack;
			font-weight: variables.$fontWeightBold;
			vertical-align: top;
		}
	}

	.right_area {
		flex-shrink: 0;
		margin-left: auto;

		.join_num_box {
			font-size: 0;

			.join_num {
				display: inline-block;
				@include mixins.font_size_xs;
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;
				background: {
					repeat: no-repeat;
					position: right 8px;
				}
				vertical-align: top;

				&.up {
					padding-right: 14px;
					@include mixins.ratio_images('../images/ico_num_up', 'png', 8px, 5px);

					.val {
						color: variables.$textColorRed;
					}
				}

				&.down {
					padding-right: 14px;
					@include mixins.ratio_images('../images/ico_num_down', 'png', 8px, 5px);


					.val {
						color: variables.$textColorBlue;
					}
				}

				& ~ .join_subject {
					margin-left: 12px;
					padding-top: 1px;
				}
			}

			.join_subject {
				display: inline-block;
				@include mixins.font_size_xxs;
				color: variables.$textColorLightGray;
				vertical-align: top;
			}
		}
	}

	&.end {
		.survey_status {
			.ico_survey_status {
				@include mixins.ratio_images('../images/ico_survey_end', 'png', 25px, 24px);
			}
		}
	}
	&.closed {
		background-color: variables.$textColorLightGray;

		.survey_status {
			.status {
				color: #fff;
			}

			.ico_survey_status {
				@include mixins.ratio_images('../images/ico_survey_closed', 'png', 25px, 24px);
			}
		}

		.join_num_box {
			.join_num, .join_subject {
				color: #fff;
			}
		}
	}

	& + .edit_write_case,
	& + .book_survey_area {
		margin-top: 20px;
	}
}

// 설문 컨텐츠 그리드
.book_survey_area {
	display: block;
	box-sizing: border-box;

	.title_wrap {
		justify-content: center;
		text-align: center;
	}

	& ~ .book_survey_area {
		margin-top: 70px;
	}

	& ~ .edit_write_case {
		margin-top: 70px;
	}

	& + .info_text_box {
		margin-top: 30px;
	}
}

// 설문 컨텐츠 영역
.book_survey_box {
	display: block;
	padding: 49px;
	border: 1px solid variables.$colorGray03;
	border-radius: 20px;
	box-sizing: border-box;

	.btn_check_wrap {
		width: 500px;
		margin: 0 auto;
	}

	.byte_check_wrap {
		width: 500px;
		margin: 0 auto;
	}

	.prod_list {
		&.col5 {
			width: 994px;
			margin: 0 auto;
		}
	}

	.btn_wrap {
		margin-top: 40px;
	}
}

// 결과 순위
.result_rank_list {
	@include mixins.justify_width(end, start);
	flex-wrap: wrap;
	width: 924px;
	margin: 0 auto;
	box-sizing: border-box;

	.result_item {
		display: inline-block;
		position: relative;
		margin-left: 60px;
		vertical-align: top;
		box-sizing: border-box;
		text-align: center;

		&:nth-child(4n+1) {
			width: 234px;
			margin-left: 0;

			.prod_area {
				.prod_thumb_box {
					.img_box {
						img {
							max-height: calc(234px * 1.618 - 10px);
						}
					}
				}
			}
		}
		&:nth-child(n+5) {
			margin-top: 50px;
		}

		.result_rank {
			display: block;
			position: relative;
			margin-bottom: 14px;
			@include mixins.font_size_def;
			color: variables.$textColorBlack;
			text-align: center;
			box-sizing: border-box;

			.val {
				display: inline-block;
				font-size: variables.$fontSizeMd;
				font-weight: variables.$fontWeightBold;
				vertical-align: -1px;
			}
		}

		.prod_area {
			display: inline-block;
			vertical-align: top;
		}

		img	{
			width: 100%;
		}

		.result_info_box {
			margin-top: 20px;

			.result_title {
				@include mixins.font_size_sm;
				color: variables.$textColorBlack;
				font-weight: variables.$fontWeightMedium;

				& + .rate_bar {
					margin-top: 10px;
				}
			}
			.copy {
				margin-top: 6px;
				@include mixins.font_size_xs;
				color: variables.$textColorGray;

				& + .rate_bar {
					margin-top: 20px;
				}
			}

			.rate_bar {
				display: block;
				position: relative;
				width: 100%;
				height: 6px;
				overflow: hidden;
				border-radius: 6px;
				background-color: rgba(variables.$kyoboPurple, 0.06);

				.bar {
					display: inline-block;
					position: absolute;
					top: 0;
					left: 0;
					max-width: 100%;
					height: 100%;
					border-radius: 10px;
					background-color: variables.$kyoboPurple;
				}

				& + .rate_info {
					margin-top: 8px;
					margin-bottom: 0;
				}
			}

			.rate_info {
				@include mixins.justify_width(center);
				margin-bottom: 10px;

				.rate {
					display: inline-block;
					flex-shrink: 0;
					@include mixins.font_size_def;
					color: variables.$textColorBlack;
					font-weight: variables.$fontWeightBold;
					vertical-align: top;
				}

				.vote_num {
					display: inline-block;
					margin-left: auto;
					@include mixins.font_size_xxs;
					color: variables.$textColorLightGray;
					vertical-align: top;
				}
			}
		}

		.my_select {
			display: inline-block;
			position: absolute;
			right: -22px;
			bottom: 76px;
			z-index: 1;
			width: 62px;
			height: 62px;
			line-height: 62px;
			padding-top: 6px;
			color: #fff;
			font-size: variables.$fontSizeMin;
			font-weight: variables.$fontWeightBold;
			letter-spacing: variables.$letterSpacing;
			text-align: center;
			background-color: rgba(variables.$kyoboPurple, 0.9);
			border-radius: 50%;
			box-shadow: 0 5px 10px rgba(variables.$kyoboPurple, 0.4);
			box-sizing: border-box;

			&:before {
				content: '';
				display: inline-block;
				position: absolute;
				left: 50%;
				top: 16px;
				width: 10px;
				height: 7px;
				@include mixins.ratio_images('../images/ico_checkbox_active', 'png', 10px, 7px);
				@include mixins.transform(translateX(-50%));
			}
		}

		&.top {
			.result_rank {
				padding-top: 3px;
				height: 42px;
				margin-bottom: 9px;
				color: variables.$textColorPurple;
				font-weight: variables.$fontWeightBold;

				.val {
					font-size: variables.$fontSizeXxl;
					vertical-align: -2px;
				}

				&:before {
					content: '';
					position: absolute;
					left: 50%;
					top: 0;
					width: 73px;
					height: 42px;
					background: {
						repeat: no-repeat;
						position: center bottom;
					};
					@include mixins.ratio_images('../images/ico_champion', 'png', 73px, 42px);
					@include mixins.transform(translateX(-50%));
				}
			}

			.prod_area {
				width: 100%;

				.prod_thumb_box {
					width: 100%;
					box-shadow: 0 5px 10px rgba(#000, 0.1);
				}
			}

			.result_info_box {
				margin-top: 16px;
			}
		}
	}

	&.type_row {
		display: block;
		width: 590px;
		margin: 0 auto;
		
		.result_item {
			@include mixins.justify_width(center);
			width: 100%;
			padding-right: 42px;
			margin-left: 0;
			text-align: left;

			& ~ .result_item {
				margin-top: 20px;
			}

			.result_rank {
				flex-shrink: 0;
				width: 73px;
				margin: 0 17px 0 0;
			}
			.result_info_box {
				flex: 1;
				margin-top: 0;
				padding: 20px 30px;
				background-color: variables.$colorGray06;
				border-radius: 10px;

				.rate_bar {
					background-color: variables.$colorGray04;

					.bar {
						background-color: variables.$colorGray02;
					}
				}
				
				.rate_info {
					display: block;

					.rate {
						@include mixins.font_size_xxs;
					}
				}
			}

			.my_select {
				bottom: auto;
				top: 50%;
				right: 0;
				margin-top: -31px;
			}

			&.top {

				.result_info_box {
					background-color: rgba(variables.$kyoboPurple, 0.06);

					.rate_bar {
						background-color: rgba(variables.$kyoboPurple, 0.06);
					}

					.bar {
						background-color: variables.$kyoboPurple;
					}
				}
			}

			&:not(.top) {
				.result_rank {
					font-weight: variables.$fontWeightMedium;

					.val {
						font-size: variables.$fontSizeDef;
						font-weight: variables.$fontWeightBold;
						vertical-align: baseline;
					}
				}
			}
		}
	}
}

// 출간된 도서
.publish_book_box {
	.prod_area {
		width: 276px;
		margin: 0 auto;

		.prod_thumb_box {
			width: 100%;
			box-shadow: 10px 10px 30px rgba(#000, 0.08);

			.img_box {
				img {
					max-height: calc(276px * 1.618 - 10px);
				}
			}
		}

		.prod_info_box {
			text-align: center;

			& + .btn_wrap {
				margin-top: 20px;
			}
		}
	}
}

/* quiz 정답, 설문 등 선택 리스트 */
.btn_check_wrap {
	overflow: hidden;
	position: relative;
	border-radius: 10px;

	li {
		~ li {
			margin-top: 8px;
		}
	}

	.finish_box {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		@include mixins.justify_width(center, center, column);
		@include mixins.font_size_def;
		font-weight: variables.$fontWeightBold;
		text-align: center;
		color: #fff;
		background-color: rgba(variables.$textColorGray, .5);

		.desc {
			margin-top: 8px;
		}
	}
}

// 버튼형식 check
.form_chk, .form_rdo {
	&.type_btn {
		display: block;

		input:checked + label {
			font-weight: variables.$fontWeightBold;
			color: variables.$textColorPurple;
			border: 1px solid #5157ab;
			background-color: #f8f8fc;

			&:after {
				background-color: transparent;
				@include mixins.ratio_images('../images/ico_quiz_check_active', 'png', 16px, 12px);
			}
		}

		input[disabled] {
			& + label {
				.text, .copy {
					opacity: .3;
				}

				&:after {
					@include mixins.ratio_images('../images/ico_quiz_check', 'png', 16px, 12px);
				}
			}

			&[checked] {
				& + label {
					.text, .copy {
						opacity: 1;
					}

					&:after {
						@include mixins.ratio_images('../images/ico_quiz_check_active', 'png', 16px, 12px);
					}
				}
			}
		}

		label {
			display: block;
			padding: 21px 76px 19px 30px;
			border: 1px solid #fff;
			border-radius: 10px;
			@include mixins.font_size_sm;
			color: variables.$textColorBlack;
			font-weight: variables.$fontWeightMedium;
			background-color: #fff;
			text-align: left;

			.copy {
				display: block;
				margin-top: 12px;
				@include mixins.font_size_xs;
				color: variables.$textColorGray;
				font-weight: variables.$fontWeightRegular;
			}

			&:before {
				display: none;
			}

			&:after {
				width: 16px;
				height: 12px;
				left: auto;
				right: 30px;
				top: 50%;
				border-radius: 0;
				@include mixins.transform(translateY(-50%));
				@include mixins.ratio_images('../images/ico_quiz_check', 'png', 16px, 12px);
				background-position: center center;
				background-color: transparent;
			}
		}
	}

	&.type_gray {
		label {
			border: 1px solid variables.$colorGray06;
			background-color: variables.$colorGray06;
		}
	}
}